/* ===== Modern Green, Blue, Black Theme Dashboard Styles ===== */

/* Core dashboard layout */
.dashboard-container {
  display: flex;
  min-height: 100vh;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 25%, #0f4c75 50%, #3282b8 75%, #0e4b4e 100%);
  position: relative;
  margin: 0;
  padding: 0;
}

.dashboard-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 20% 20%, rgba(0, 255, 127, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 80% 80%, rgba(64, 224, 208, 0.1) 0%, transparent 50%);
  pointer-events: none;
}

/* Modern Sidebar */
.sidebar {
  width: 256px;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border-right: 1px solid rgba(64, 224, 208, 0.2);
  box-shadow: 4px 0 20px rgba(0, 0, 0, 0.3);
  position: relative;
  z-index: 10;
}

.sidebar.collapsed {
  width: 80px;
}

.sidebar-header {
  padding: 1.5rem;
  border-bottom: 1px solid rgba(64, 224, 208, 0.2);
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.sidebar-nav {
  padding: 1rem;
}

.nav-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem 1.2rem;
  margin-bottom: 0.5rem;
  border-radius: 12px;
  color: rgba(255, 255, 255, 0.7);
  text-decoration: none;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  font-weight: 500;
  position: relative;
}

.nav-item:hover {
  background: rgba(64, 224, 208, 0.1);
  color: #40e0d0;
  transform: translateX(5px);
}

.nav-item.active {
  background: linear-gradient(135deg, rgba(0, 255, 127, 0.15), rgba(64, 224, 208, 0.15));
  color: #00ff7f;
  font-weight: 600;
}

.nav-item.active:hover {
  background: linear-gradient(135deg, rgba(0, 255, 127, 0.2), rgba(64, 224, 208, 0.2));
  color: #00ff7f;
}

/* Main content area */
.main-content {
  flex: 1;
  padding: 0;
  position: relative;
  z-index: 1;
  margin: 0;
}

/* Modern Navbar */
.navbar {
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border-bottom: 1px solid rgba(64, 224, 208, 0.2);
  padding: 1rem 1.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
  position: relative;
  z-index: 10;
}

/* Cards and sections with modern glass effect */
.welcome-card {
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border-radius: 24px;
  padding: 2.5rem;
  margin-bottom: 2rem;
  box-shadow: 
    0 25px 50px rgba(0, 0, 0, 0.3),
    0 0 0 1px rgba(64, 224, 208, 0.2),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(64, 224, 208, 0.2);
  position: relative;
  overflow: hidden;
}

.welcome-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #00ff7f, #40e0d0, #1e90ff);
  border-radius: 24px 24px 0 0;
}

.welcome-title {
  font-size: 2.5rem;
  font-weight: 800;
  margin: 0 0 0.5rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0, #1e90ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.welcome-subtitle {
  color: #40e0d0;
  font-size: 1.1rem;
  font-weight: 500;
  margin: 0;
}

/* Quick actions with modern styling */
.quick-actions {
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border-radius: 20px;
  padding: 2rem;
  margin-bottom: 2rem;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(64, 224, 208, 0.2);
}

.actions-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
}

.action-button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.75rem;
  padding: 1rem 1.5rem;
  border: none;
  border-radius: 16px;
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
  background: rgba(64, 224, 208, 0.1);
  color: #40e0d0;
  border: 2px solid rgba(64, 224, 208, 0.2);
}

.action-button:hover {
  transform: translateY(-3px);
  box-shadow: 0 12px 30px rgba(64, 224, 208, 0.3);
}

.action-button.primary {
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: #000000;
  border: none;
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.3);
}

.action-button.primary:hover {
  box-shadow: 0 15px 40px rgba(0, 255, 127, 0.4);
  transform: translateY(-3px) scale(1.02);
}

/* Stats grid with modern cards */
.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.stat-card {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(20px);
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
}

.stat-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.15);
}

.stat-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.stat-title {
  font-size: 0.9rem;
  color: #636e72;
  font-weight: 600;
  margin-bottom: 0.5rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.stat-value {
  font-size: 2.5rem;
  font-weight: 800;
  color: #2d3436;
  line-height: 1;
}

.stat-icon {
  width: 60px;
  height: 60px;
  border-radius: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
}

.stat-icon::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 16px;
  opacity: 0.8;
}

.stat-icon.blue::before {
  background: linear-gradient(135deg, #74b9ff, #0984e3);
}

.stat-icon.green::before {
  background: linear-gradient(135deg, #00b894, #00a085);
}

.stat-icon.purple::before {
  background: linear-gradient(135deg, #a29bfe, #6c5ce7);
}

.stat-icon.pink::before {
  background: linear-gradient(135deg, #fd79a8, #e84393);
}

/* Recent activity with modern styling */
.recent-activity {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(20px);
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.activity-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem 0;
  border-bottom: 1px solid rgba(227, 227, 227, 0.5);
  transition: all 0.3s ease;
}

.activity-item:hover {
  background: rgba(116, 185, 255, 0.05);
  border-radius: 12px;
  padding-left: 1rem;
  padding-right: 1rem;
}

.activity-item:last-child {
  border-bottom: none;
}

.activity-icon {
  width: 40px;
  height: 40px;
  border-radius: 12px;
  background: linear-gradient(135deg, #ff6b6b, #ee5a24);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  flex-shrink: 0;
}

.activity-title {
  font-weight: 600;
  color: #2d3436;
  margin: 0;
}

.activity-meta {
  font-size: 0.85rem;
  color: #636e72;
  font-weight: 500;
}

/* Utility classes */
.text-muted {
  color: #636e72;
}

/* Responsive design */
@media (max-width: 768px) {
  .dashboard-container {
    flex-direction: column;
  }
  
  .sidebar {
    width: 100%;
    position: fixed;
    top: 0;
    left: -100%;
    height: 100vh;
    z-index: 1000;
    transition: left 0.3s ease;
  }
  
  .sidebar.open {
    left: 0;
  }
  
  .main-content {
    padding: 1rem;
  }
  
  .welcome-card {
    padding: 1.5rem;
  }
  
  .welcome-title {
    font-size: 2rem;
  }
  
  .stats-grid {
    grid-template-columns: 1fr;
  }
  
  .actions-grid {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 480px) {
  .welcome-title {
    font-size: 1.75rem;
  }
  
  .stat-value {
    font-size: 2rem;
  }
  
  .stat-card,
  .quick-actions,
  .recent-activity {
    padding: 1.5rem;
  }
}