/* ===== Enhanced Tree Visualization Component Styling ===== */
.tree-visualization-container {
  position: relative;
  width: 100%;
  height: 100%;
  min-height: 600px;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 16px;
  overflow: hidden;
  border: 1px solid rgba(64, 224, 208, 0.2);
}

.tree-visualization-container.enhanced {
  background: linear-gradient(135deg, rgba(26, 26, 46, 0.8), rgba(22, 33, 62, 0.8));
  border: 1px solid rgba(64, 224, 208, 0.3);
}

/* Enhanced Controls Section */
.tree-controls-enhanced {
  position: absolute;
  top: 1rem;
  left: 1rem;
  right: 1rem;
  z-index: 10;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 1rem;
}

/* Search Section */
.tree-search-section {
  position: relative;
  flex: 1;
  max-width: 400px;
}

.search-container {
  position: relative;
  display: flex;
  align-items: center;
  background: rgba(0, 0, 0, 0.9);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(64, 224, 208, 0.3);
  border-radius: 25px;
  padding: 0.75rem 1rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.5);
}

.search-icon {
  color: rgba(64, 224, 208, 0.7);
  margin-right: 0.75rem;
  flex-shrink: 0;
}

.search-input {
  flex: 1;
  background: transparent;
  border: none;
  outline: none;
  color: rgba(255, 255, 255, 0.9);
  font-size: 0.95rem;
  padding: 0;
}

.search-input::placeholder {
  color: rgba(255, 255, 255, 0.4);
}

.search-clear {
  background: none;
  border: none;
  color: rgba(255, 255, 255, 0.6);
  cursor: pointer;
  padding: 0.25rem;
  border-radius: 50%;
  margin-left: 0.5rem;
  transition: all 0.2s ease;
}

.search-clear:hover {
  color: rgba(255, 255, 255, 0.9);
  background: rgba(255, 255, 255, 0.1);
}

/* Search Results Dropdown */
.search-results {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.95);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(64, 224, 208, 0.3);
  border-radius: 16px;
  margin-top: 0.5rem;
  max-height: 300px;
  overflow-y: auto;
  z-index: 100;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.7);
}

.search-result-item {
  display: flex;
  align-items: center;
  padding: 0.75rem;
  cursor: pointer;
  transition: all 0.2s ease;
  border-bottom: 1px solid rgba(64, 224, 208, 0.1);
}

.search-result-item:hover {
  background: rgba(64, 224, 208, 0.1);
}

.search-result-item:last-child {
  border-bottom: none;
}

.search-result-photo {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 0.75rem;
  flex-shrink: 0;
}

.search-result-photo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.search-result-info {
  flex: 1;
}

.search-result-name {
  color: rgba(255, 255, 255, 0.9);
  font-weight: 600;
  margin-bottom: 0.25rem;
}

.search-result-details {
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.85rem;
  display: flex;
  gap: 0.5rem;
}

/* Enhanced View Controls */
.tree-view-controls {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.zoom-controls {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  background: rgba(0, 0, 0, 0.9);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(64, 224, 208, 0.3);
  border-radius: 25px;
  padding: 0.75rem 1rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.5);
}

.control-btn.enhanced {
  width: 40px;
  height: 40px;
  background: rgba(64, 224, 208, 0.1);
  border: 1px solid rgba(64, 224, 208, 0.3);
  border-radius: 50%;
  color: rgba(64, 224, 208, 0.8);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.2);
}

.control-btn.enhanced:hover {
  background: rgba(64, 224, 208, 0.2);
  border-color: #40e0d0;
  color: #40e0d0;
  transform: scale(1.05);
  box-shadow: 0 8px 24px rgba(64, 224, 208, 0.3);
}

.zoom-level {
  color: rgba(255, 255, 255, 0.9);
  font-size: 0.9rem;
  font-weight: 600;
  min-width: 50px;
  text-align: center;
}

/* Modern Tree Node Styling */
.modern-tree-node {
  position: relative;
  overflow: hidden;
}

.modern-tree-node:hover {
  transform: scale(1.05);
  box-shadow: 
    0 12px 48px rgba(30, 144, 255, 0.4),
    0 0 0 2px rgba(30, 144, 255, 0.3),
    inset 0 2px 0 rgba(255, 255, 255, 0.2) !important;
}

.modern-tree-node.female:hover {
  box-shadow: 
    0 12px 48px rgba(255, 105, 180, 0.4),
    0 0 0 2px rgba(255, 105, 180, 0.3),
    inset 0 2px 0 rgba(255, 255, 255, 0.2) !important;
}

.modern-tree-node.hovered {
  transform: scale(1.08);
  z-index: 10;
}

/* Enhanced Action Buttons */
.action-btn-enhanced {
  position: relative;
  overflow: hidden;
}

.action-btn-enhanced::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s;
}

.action-btn-enhanced:hover::before {
  left: 100%;
}

.action-btn-enhanced:hover {
  transform: scale(1.1) translateY(-2px);
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.4) !important;
}

/* Slide Up Fade Animation */
@keyframes slideUpFade {
  from {
    opacity: 0;
    transform: translateY(20px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

/* Enhanced Detail Panel */
.detail-panel-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(10px);
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  animation: fadeIn 0.3s ease;
}

.detail-panel {
  background: linear-gradient(135deg, rgba(0, 0, 0, 0.95), rgba(26, 26, 46, 0.95));
  backdrop-filter: blur(30px);
  border: 1px solid rgba(64, 224, 208, 0.3);
  border-radius: 24px;
  box-shadow: 0 32px 64px rgba(0, 0, 0, 0.8);
  width: 100%;
  max-width: 500px;
  max-height: 80vh;
  overflow: hidden;
  animation: slideInScale 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}

@keyframes slideInScale {
  from {
    opacity: 0;
    transform: scale(0.9) translateY(20px);
  }
  to {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

.detail-panel-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2rem;
  border-bottom: 1px solid rgba(64, 224, 208, 0.2);
  background: linear-gradient(90deg, rgba(64, 224, 208, 0.05), transparent);
}

.detail-header-content {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  flex: 1;
}

.detail-member-photo {
  position: relative;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  overflow: hidden;
  border: 3px solid rgba(64, 224, 208, 0.5);
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.4);
}

.detail-member-photo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.status-indicator {
  position: absolute;
  bottom: 5px;
  right: 5px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  border: 2px solid white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
}

.status-indicator.alive {
  background: #34c759;
}

.status-indicator.deceased {
  background: #ff453a;
}

.detail-member-info {
  flex: 1;
}

.detail-member-name {
  color: rgba(255, 255, 255, 0.95);
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
  line-height: 1.2;
}

.detail-member-subtitle {
  color: rgba(64, 224, 208, 0.8);
  font-size: 1rem;
  font-weight: 500;
}

.detail-close-btn {
  width: 44px;
  height: 44px;
  border-radius: 50%;
  border: none;
  background: rgba(255, 69, 58, 0.15);
  color: #ff453a;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  flex-shrink: 0;
}

.detail-close-btn:hover {
  background: rgba(255, 69, 58, 0.25);
  transform: scale(1.05);
}

.detail-panel-content {
  padding: 2rem;
  max-height: 60vh;
  overflow-y: auto;
}

.detail-section {
  margin-bottom: 2rem;
}

.detail-section:last-child {
  margin-bottom: 0;
}

.detail-section-title {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  color: rgba(255, 255, 255, 0.9);
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
  padding-bottom: 0.75rem;
  border-bottom: 1px solid rgba(64, 224, 208, 0.2);
}

.detail-section-title svg {
  color: #40e0d0;
}

.detail-info-grid {
  display: grid;
  gap: 1rem;
}

.detail-info-item {
  display: flex;
  align-items: flex-start;
  gap: 0.75rem;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(64, 224, 208, 0.1);
  border-radius: 12px;
  transition: all 0.3s ease;
}

.detail-info-item:hover {
  background: rgba(255, 255, 255, 0.05);
  border-color: rgba(64, 224, 208, 0.2);
}

.detail-info-item svg {
  color: #40e0d0;
  margin-top: 0.125rem;
  flex-shrink: 0;
}

.detail-info-item div {
  flex: 1;
}

.detail-info-item label {
  display: block;
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.85rem;
  font-weight: 500;
  margin-bottom: 0.25rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.detail-info-item span {
  color: rgba(255, 255, 255, 0.9);
  font-size: 1rem;
  font-weight: 500;
}

.detail-relationships {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.relationship-item {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.75rem;
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(64, 224, 208, 0.1);
  border-radius: 8px;
}

.relationship-label {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.9rem;
  font-weight: 500;
  min-width: 80px;
}

.relationship-name {
  color: rgba(255, 255, 255, 0.9);
  font-weight: 600;
}

.children-list {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.child-name {
  background: rgba(64, 224, 208, 0.1);
  color: rgba(64, 224, 208, 0.9);
  padding: 0.25rem 0.75rem;
  border-radius: 12px;
  font-size: 0.85rem;
  font-weight: 500;
}

.detail-actions {
  display: flex;
  gap: 1rem;
  padding-top: 1.5rem;
  border-top: 1px solid rgba(64, 224, 208, 0.2);
  margin-top: 1.5rem;
}

.detail-action-btn {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  padding: 0.875rem 1.5rem;
  border: none;
  border-radius: 12px;
  font-size: 0.95rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.detail-action-btn.edit {
  background: linear-gradient(135deg, #1e90ff, #40e0d0);
  color: white;
  box-shadow: 0 8px 24px rgba(30, 144, 255, 0.3);
}

.detail-action-btn.edit:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 32px rgba(30, 144, 255, 0.4);
}

.detail-action-btn.add {
  background: linear-gradient(135deg, #34c759, #00ff7f);
  color: white;
  box-shadow: 0 8px 24px rgba(52, 199, 89, 0.3);
}

.detail-action-btn.add:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 32px rgba(52, 199, 89, 0.4);
}
.tree-node-card {
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
}

.tree-node-card:hover {
  transform: scale(1.05);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.5);
}

.tree-node-card.male {
  border-color: #1e90ff !important;
  box-shadow: 0 4px 15px rgba(30, 144, 255, 0.4), 0 0 0 1px rgba(30, 144, 255, 0.2) !important;
}

.tree-node-card.female {
  border-color: #ff69b4 !important;
  box-shadow: 0 4px 15px rgba(255, 105, 180, 0.4), 0 0 0 1px rgba(255, 105, 180, 0.2) !important;
}

.tree-node-card.male:hover {
  box-shadow: 0 8px 25px rgba(30, 144, 255, 0.6), 0 0 0 2px rgba(30, 144, 255, 0.4) !important;
  border-color: #1e90ff !important;
}

.tree-node-card.female:hover {
  box-shadow: 0 8px 25px rgba(255, 105, 180, 0.6), 0 0 0 2px rgba(255, 105, 180, 0.4) !important;
  border-color: #ff69b4 !important;
}

/* Tree Links */
.tree-link {
  stroke: rgba(0, 255, 127, 0.7) !important;
  stroke-width: 2.5 !important;
  transition: all 0.3s ease;
  filter: drop-shadow(0 0 3px rgba(0, 255, 127, 0.3));
}

.tree-link:hover {
  stroke: rgba(0, 255, 127, 1) !important;
  stroke-width: 3.5 !important;
  filter: drop-shadow(0 0 8px rgba(0, 255, 127, 0.6));
}

/* Empty Tree State */
.empty-tree {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  color: rgba(255, 255, 255, 0.6);
  text-align: center;
}

.empty-tree p {
  margin-top: 1rem;
  font-size: 1.1rem;
}

/* Pulse animation for edit indicators */
@keyframes pulse {
  0%, 100% {
    opacity: 0.6;
    transform: scale(1);
  }
  50% {
    opacity: 1;
    transform: scale(1.1);
  }
}

/* Tree Controls */
.tree-controls {
  position: absolute;
  top: 1rem;
  left: 1rem;
  z-index: 10;
  display: flex;
  gap: 1rem;
  align-items: center;
}

.zoom-controls,
.view-controls {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(15px);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 12px;
  padding: 0.5rem;
}

.control-btn {
  width: 36px;
  height: 36px;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 8px;
  color: rgba(255, 255, 255, 0.8);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}

.control-btn:hover {
  background: rgba(64, 224, 208, 0.2);
  border-color: #40e0d0;
  color: #40e0d0;
  transform: scale(1.05);
}

.zoom-level {
  color: rgba(255, 255, 255, 0.9);
  font-size: 0.9rem;
  min-width: 50px;
  text-align: center;
}

/* Tree Canvas */
.tree-canvas {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  user-select: none;
}

.tree-svg {
  width: 100%;
  height: 100%;
  transition: transform 0.2s ease;
}

/* Connections */
.family-connection {
  transition: stroke 0.3s ease;
}

.family-connection:hover {
  stroke: rgba(0, 255, 127, 0.9);
  stroke-width: 3;
  filter: drop-shadow(0 0 5px rgba(0, 255, 127, 0.5));
}

.marriage-connection {
  transition: stroke 0.3s ease;
}

.marriage-connection:hover {
  stroke: rgba(255, 105, 180, 0.9);
  stroke-width: 4;
  filter: drop-shadow(0 0 5px rgba(255, 105, 180, 0.5));
}

/* Member Nodes */
.member-node {
  cursor: pointer;
  transition: all 0.3s ease;
}

.member-node:hover .member-background {
  fill: rgba(64, 224, 208, 0.15);
  stroke: rgba(64, 224, 208, 0.5);
  stroke-width: 2;
}

.member-node.selected .member-background {
  fill: rgba(0, 255, 127, 0.2);
  stroke: #00ff7f;
  stroke-width: 3;
  filter: drop-shadow(0 0 10px rgba(0, 255, 127, 0.5));
}

.member-node.editable:hover {
  transform: scale(1.05);
}

.member-background {
  fill: rgba(0, 0, 0, 0.8);
  stroke: rgba(64, 224, 208, 0.3);
  stroke-width: 1;
  transition: all 0.3s ease;
}

.member-photo {
  transition: all 0.3s ease;
}

.member-node:hover .member-photo {
  filter: brightness(1.1);
}

/* Gender Indicators */
.gender-indicator {
  stroke-width: 2;
  transition: all 0.3s ease;
}

.gender-indicator.male {
  fill: rgba(30, 144, 255, 0.4);
  stroke: #1e90ff;
}

.gender-indicator.female {
  fill: rgba(255, 105, 180, 0.4);
  stroke: #ff69b4;
}

/* Member Text */
.member-name {
  fill: rgba(255, 255, 255, 0.95);
  font-size: 12px;
  font-weight: 600;
}

.member-year {
  fill: rgba(255, 255, 255, 0.7);
  font-size: 10px;
}

/* Edit Indicator */
.edit-indicator {
  fill: rgba(0, 255, 127, 0.8);
  stroke: #00ff7f;
  stroke-width: 2;
  animation: pulse-edit 2s infinite;
}

@keyframes pulse-edit {
  0%, 100% { opacity: 0.6; }
  50% { opacity: 1; }
}

/* Member Details Panel */
.member-details-panel {
  position: absolute;
  top: 1rem;
  right: 1rem;
  width: 320px;
  background: rgba(0, 0, 0, 0.9);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(64, 224, 208, 0.3);
  border-radius: 16px;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.5);
  z-index: 20;
  animation: slideInRight 0.3s ease;
}

@keyframes slideInRight {
  from {
    transform: translateX(100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

.member-details-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 1.5rem 1rem 1.5rem;
  border-bottom: 1px solid rgba(64, 224, 208, 0.2);
}

.member-details-header h3 {
  color: rgba(255, 255, 255, 0.95);
  font-size: 1.1rem;
  margin: 0;
}

.close-details-btn {
  background: rgba(255, 69, 58, 0.2);
  border: 1px solid rgba(255, 69, 58, 0.3);
  color: #ff453a;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  font-weight: bold;
  transition: all 0.3s ease;
}

.close-details-btn:hover {
  background: rgba(255, 69, 58, 0.3);
  transform: scale(1.1);
}

.member-details-content {
  padding: 1.5rem;
}

.member-photo-large {
  text-align: center;
  margin-bottom: 1.5rem;
}

.member-photo-large img {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  border: 3px solid rgba(64, 224, 208, 0.3);
  object-fit: cover;
}

.member-info {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  margin-bottom: 1.5rem;
}

.info-item {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.9rem;
}

.info-item svg {
  color: #40e0d0;
  flex-shrink: 0;
}

.member-actions {
  border-top: 1px solid rgba(64, 224, 208, 0.2);
  padding-top: 1rem;
}

.edit-member-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  width: 100%;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
  border: none;
  padding: 0.75rem 1rem;
  border-radius: 12px;
  font-size: 0.9rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.edit-member-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.4);
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
  .tree-controls {
    position: static;
    margin-bottom: 1rem;
    justify-content: center;
  }
  
  .member-details-panel {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    width: auto;
    border-radius: 0;
    z-index: 100;
    animation: slideInUp 0.3s ease;
  }
  
  @keyframes slideInUp {
    from {
      transform: translateY(100%);
      opacity: 0;
    }
    to {
      transform: translateY(0);
      opacity: 1;
    }
  }
  
  .tree-canvas {
    height: calc(100% - 80px);
  }
  
  .control-btn {
    width: 32px;
    height: 32px;
  }
  
  .zoom-level {
    font-size: 0.8rem;
    min-width: 40px;
  }
}

@media (max-width: 480px) {
  .tree-visualization-container {
    min-height: 400px;
  }
  
  .tree-controls {
    gap: 0.5rem;
  }
  
  .zoom-controls,
  .view-controls {
    padding: 0.25rem;
  }
  
  .control-btn {
    width: 28px;
    height: 28px;
  }
  
  .member-details-content {
    padding: 1rem;
  }
  
  .member-photo-large img {
    width: 60px;
    height: 60px;
  }
}

/* RTL Support */
[dir="rtl"] .tree-controls {
  left: auto;
  right: 1rem;
}

[dir="rtl"] .member-details-panel {
  right: auto;
  left: 1rem;
  animation: slideInLeft 0.3s ease;
}

@keyframes slideInLeft {
  from {
    transform: translateX(-100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

[dir="rtl"] .info-item {
  flex-direction: row-reverse;
  text-align: right;
}

/* High DPI Support */
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .member-background {
    stroke-width: 0.5;
  }
  
  .gender-indicator {
    stroke-width: 1;
  }
}

/* Print Styles */
@media print {
  .tree-controls,
  .member-details-panel {
    display: none;
  }
  
  .tree-visualization-container {
    background: white;
    border: 1px solid #ccc;
  }
  
  .member-background {
    fill: white;
    stroke: #333;
  }
  
  .member-name,
  .member-year {
    fill: #333;
  }
}