/* ===== Member Management Component Styling ===== */
.member-management-container {
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(15px);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 20px;
  padding: 2rem;
}

.management-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}

.management-header h3 {
  color: rgba(255, 255, 255, 0.95);
  font-size: 1.5rem;
  margin: 0;
}

.add-member-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.add-member-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.4);
}

.search-members {
  position: relative;
  margin-bottom: 2rem;
}

.search-members svg {
  position: absolute;
  left: 1rem;
  top: 50%;
  transform: translateY(-50%);
  color: rgba(255, 255, 255, 0.5);
}

.search-members input {
  width: 100%;
  padding: 0.75rem 1rem 0.75rem 3rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 12px;
  color: rgba(255, 255, 255, 0.9);
  font-size: 1rem;
}

.search-members input:focus {
  outline: none;
  border-color: #40e0d0;
  box-shadow: 0 0 20px rgba(64, 224, 208, 0.2);
}

/* Member Form Overlay */
.member-form-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10001; /* Increased z-index to ensure it appears above all other elements */
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.member-form.enhanced {
  max-width: 800px;
  max-height: 90vh;
  overflow-y: auto;
  padding: 0;
}

/* Form Header */
.form-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2rem 2rem 1rem 2rem;
  border-bottom: 1px solid rgba(64, 224, 208, 0.2);
  background: linear-gradient(90deg, rgba(64, 224, 208, 0.05), transparent);
}

.header-content {
  display: flex;
  align-items: center;
  gap: 1rem;
  flex: 1;
}

.header-icon {
  color: #40e0d0;
}

.form-header h4 {
  color: rgba(255, 255, 255, 0.95);
  margin: 0;
  font-size: 1.4rem;
}

.close-btn {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  border: none;
  background: rgba(255, 69, 58, 0.15);
  color: #ff453a;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}

.close-btn:hover {
  background: rgba(255, 69, 58, 0.25);
  transform: scale(1.05);
}

/* Form Sections */
.form-section {
  padding: 1.5rem 2rem;
  border-bottom: 1px solid rgba(64, 224, 208, 0.1);
}

.form-section:last-child {
  border-bottom: none;
}

.section-title {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  color: rgba(255, 255, 255, 0.9);
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid rgba(64, 224, 208, 0.2);
}

.section-title svg {
  color: #40e0d0;
}

/* Form Groups */
.form-group {
  margin-bottom: 1.5rem;
}

.form-group.full-width {
  grid-column: 1 / -1;
}

.form-group label {
  display: block;
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.9rem;
  font-weight: 500;
  margin-bottom: 0.5rem;
}

/* Enhanced Form Inputs */
.form-input,
.form-select,
.form-textarea {
  width: 100%;
  padding: 0.875rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 12px;
  color: rgba(255, 255, 255, 0.9);
  font-size: 1rem;
  transition: all 0.3s ease;
}

.form-input:focus,
.form-select:focus,
.form-textarea:focus {
  outline: none;
  border-color: #40e0d0;
  box-shadow: 0 0 20px rgba(64, 224, 208, 0.2);
  background: rgba(255, 255, 255, 0.08);
}

.form-input.error,
.form-select.error {
  border-color: #ff453a;
  box-shadow: 0 0 15px rgba(255, 69, 58, 0.2);
}

.form-textarea {
  resize: vertical;
  min-height: 80px;
}

/* Checkbox Styling */
.checkbox-label {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 8px;
  transition: all 0.3s ease;
}

.checkbox-label:hover {
  background: rgba(64, 224, 208, 0.05);
}

.checkbox-input {
  width: 20px;
  height: 20px;
  accent-color: #40e0d0;
  cursor: pointer;
}

.checkbox-text {
  color: rgba(255, 255, 255, 0.9);
  font-weight: 500;
}

/* File Input Styling */
.file-input {
  width: 100%;
  padding: 0.875rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 12px;
  color: rgba(255, 255, 255, 0.9);
  cursor: pointer;
  transition: all 0.3s ease;
}

.file-input:hover {
  background: rgba(255, 255, 255, 0.08);
  border-color: #40e0d0;
}

/* Error Messages */
.error-message {
  color: #ff453a;
  font-size: 0.85rem;
  margin-top: 0.5rem;
  display: block;
}

.submit-error {
  background: rgba(255, 69, 58, 0.1);
  border: 1px solid rgba(255, 69, 58, 0.3);
  border-radius: 8px;
  padding: 1rem;
  margin: 1.5rem 2rem;
  font-weight: 500;
}

/* Spinner for loading states */
.spinner {
  width: 16px;
  height: 16px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top-color: white;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* Enhanced Form Actions */
.form-actions {
  display: flex;
  gap: 1rem;
  justify-content: flex-end;
  padding: 1.5rem 2rem;
  background: rgba(0, 0, 0, 0.3);
  border-top: 1px solid rgba(64, 224, 208, 0.2);
}

.cancel-btn,
.save-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.875rem 1.75rem;
  border-radius: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  min-width: 120px;
  justify-content: center;
}

.cancel-btn {
  background: rgba(255, 69, 58, 0.15);
  color: #ff453a;
  border: 1px solid rgba(255, 69, 58, 0.3);
}

.cancel-btn:hover {
  background: rgba(255, 69, 58, 0.25);
  transform: translateY(-1px);
}

.save-btn {
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
  border: none;
}

.save-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.4);
}

.save-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

@keyframes slideInUp {
  from {
    transform: translateY(50px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.member-form h4 {
  color: rgba(255, 255, 255, 0.95);
  margin-bottom: 1.5rem;
  font-size: 1.3rem;
}

.form-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1rem;
  margin-bottom: 2rem;
}

.form-grid input,
.form-grid select {
  padding: 0.75rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 12px;
  color: rgba(255, 255, 255, 0.9);
  font-size: 1rem;
}

.form-grid input:focus,
.form-grid select:focus {
  outline: none;
  border-color: #40e0d0;
  box-shadow: 0 0 15px rgba(64, 224, 208, 0.2);
}

.form-grid option {
  background: #1a1a2e;
  color: white;
}

.form-actions {
  display: flex;
  gap: 1rem;
  justify-content: flex-end;
}

.cancel-btn,
.save-btn {
  padding: 0.75rem 1.5rem;
  border-radius: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.cancel-btn {
  background: rgba(255, 69, 58, 0.15);
  color: #ff453a;
  border: 1px solid rgba(255, 69, 58, 0.3);
}

.cancel-btn:hover {
  background: rgba(255, 69, 58, 0.25);
}

.save-btn {
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
  border: none;
}

.save-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.4);
}

/* Members List */
.members-list {
  display: grid;
  gap: 1rem;
}

.member-card {
  display: flex;
  align-items: center;
  gap: 1rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 16px;
  padding: 1rem;
  transition: all 0.3s ease;
}

.member-card:hover {
  background: rgba(64, 224, 208, 0.1);
  border-color: rgba(64, 224, 208, 0.4);
}

.member-card img {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border: 2px solid rgba(64, 224, 208, 0.3);
  object-fit: cover;
}

.member-info {
  flex: 1;
}

.member-info h5 {
  color: rgba(255, 255, 255, 0.95);
  margin: 0 0 0.25rem 0;
  font-size: 1rem;
}

.member-info p {
  color: rgba(255, 255, 255, 0.7);
  margin: 0;
  font-size: 0.9rem;
}

.member-actions {
  display: flex;
  gap: 0.5rem;
}

.edit-btn,
.delete-btn {
  width: 36px;
  height: 36px;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}

.edit-btn {
  background: rgba(30, 144, 255, 0.15);
  color: #1e90ff;
}

.edit-btn:hover {
  background: rgba(30, 144, 255, 0.25);
  transform: scale(1.1);
}

.delete-btn {
  background: rgba(255, 69, 58, 0.15);
  color: #ff453a;
}

.delete-btn:hover {
  background: rgba(255, 69, 58, 0.25);
  transform: scale(1.1);
}

/* Responsive Design */
@media (max-width: 768px) {
  .management-header {
    flex-direction: column;
    gap: 1rem;
    align-items: stretch;
  }
  
  .member-form {
    padding: 1.5rem;
    margin: 1rem;
  }
  
  .form-grid {
    grid-template-columns: 1fr;
  }
  
  .form-actions {
    justify-content: stretch;
  }
  
  .cancel-btn,
  .save-btn {
    flex: 1;
  }
  
  .member-card {
    flex-direction: column;
    text-align: center;
  }
  
  .member-actions {
    width: 100%;
    justify-content: center;
  }
}

/* RTL Support */
[dir="rtl"] .search-members svg {
  left: auto;
  right: 1rem;
}

[dir="rtl"] .search-members input {
  padding: 0.75rem 3rem 0.75rem 1rem;
}

[dir="rtl"] .form-actions {
  justify-content: flex-start;
}

[dir="rtl"] .member-info {
  text-align: right;
}

/* Enhanced Members Grid Layout */
.members-grid-container {
  margin-top: 2rem;
}

.grid-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
  padding: 1rem 1.5rem;
  background: rgba(64, 224, 208, 0.05);
  border: 1px solid rgba(64, 224, 208, 0.15);
  border-radius: 12px;
  backdrop-filter: blur(10px);
}

.view-info {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: rgba(255, 255, 255, 0.8);
  font-weight: 500;
}

.view-info svg {
  color: #40e0d0;
}

.grid-actions {
  display: flex;
  gap: 0.5rem;
}

.grid-view-btn {
  padding: 0.5rem;
  background: rgba(64, 224, 208, 0.1);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 8px;
  color: rgba(255, 255, 255, 0.7);
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.grid-view-btn.active,
.grid-view-btn:hover {
  background: rgba(64, 224, 208, 0.2);
  color: #40e0d0;
  border-color: #40e0d0;
}

/* Members Grid */
.members-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1rem;
  animation: fadeInUp 0.6s ease-out;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Enhanced Member Cards */
.member-card-grid {
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border: 2px solid rgba(64, 224, 208, 0.2);
  border-radius: 16px;
  padding: 1rem;
  position: relative;
  overflow: hidden;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  animation: slideInCard 0.6s ease-out;
  min-height: 280px;
  max-height: 320px;
  display: flex;
  flex-direction: column;
}

@keyframes slideInCard {
  from {
    opacity: 0;
    transform: translateY(20px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.member-card-grid::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, #00ff7f, #40e0d0, #1e90ff);
  border-radius: 16px 16px 0 0;
}

.member-card-grid:hover {
  transform: translateY(-8px) scale(1.02);
  border-color: rgba(0, 255, 127, 0.4);
  box-shadow: 
    0 25px 50px rgba(0, 255, 127, 0.15),
    0 0 30px rgba(64, 224, 208, 0.1),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

/* Member Avatar Grid */
.member-avatar-grid {
  display: flex;
  justify-content: center;
  margin-bottom: 0.75rem;
  position: relative;
  flex-shrink: 0;
}

.avatar-image {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  object-fit: cover;
  border: 2px solid rgba(64, 224, 208, 0.4);
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
}

.member-card-grid:hover .avatar-image {
  border-color: #00ff7f;
  transform: scale(1.05);
  box-shadow: 0 6px 20px rgba(0, 255, 127, 0.3);
}

.status-indicator {
  position: absolute;
  bottom: 2px;
  right: calc(50% - 32px);
  width: 14px;
  height: 14px;
  border-radius: 50%;
  border: 2px solid white;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
}

.status-indicator.alive {
  background: linear-gradient(135deg, #34c759, #00ff7f);
}

.status-indicator.deceased {
  background: linear-gradient(135deg, #ff453a, #ff6b6b);
}

.gender-indicator {
  position: absolute;
  top: 2px;
  right: calc(50% - 32px);
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: 1px solid white;
}

.gender-indicator.male {
  background: linear-gradient(135deg, #1e90ff, #40e0d0);
}

.gender-indicator.female {
  background: linear-gradient(135deg, #ff69b4, #fd79a8);
}

/* Member Info Grid */
.member-info-grid {
  text-align: center;
  margin-bottom: 0.75rem;
  flex: 1;
  display: flex;
  flex-direction: column;
}

.member-name-grid {
  color: rgba(255, 255, 255, 0.95);
  font-size: 1rem;
  font-weight: 700;
  margin: 0 0 0.5rem 0;
  line-height: 1.3;
  text-shadow: 0 1px 3px rgba(0, 0, 0, 0.5);
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.member-details-grid {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  margin-bottom: 0.5rem;
  flex: 1;
}

.detail-item {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.375rem;
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.75rem;
  padding: 0.125rem 0.375rem;
  background: rgba(64, 224, 208, 0.05);
  border-radius: 4px;
  border: 1px solid rgba(64, 224, 208, 0.1);
}

.detail-item svg {
  color: #40e0d0;
  flex-shrink: 0;
}

.detail-item span {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 120px;
}

/* Contact Info Grid */
.contact-info-grid {
  display: flex;
  flex-direction: column;
  gap: 0.125rem;
  padding-top: 0.25rem;
  border-top: 1px solid rgba(64, 224, 208, 0.15);
  margin-top: auto;
}

.contact-item {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.25rem;
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.7rem;
}

.contact-item svg {
  color: #40e0d0;
  flex-shrink: 0;
}

/* Member Actions Grid */
.member-actions-grid {
  display: flex;
  gap: 0.5rem;
  justify-content: center;
  margin-bottom: 0.5rem;
  flex-shrink: 0;
}

.action-btn {
  display: flex;
  align-items: center;
  gap: 0.25rem;
  padding: 0.375rem 0.75rem;
  border: none;
  border-radius: 8px;
  font-size: 0.75rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  position: relative;
  overflow: hidden;
}

.action-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s;
}

.action-btn:hover::before {
  left: 100%;
}

.edit-btn-grid {
  background: rgba(30, 144, 255, 0.15);
  color: #1e90ff;
  border: 1px solid rgba(30, 144, 255, 0.3);
}

.edit-btn-grid:hover {
  background: rgba(30, 144, 255, 0.25);
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(30, 144, 255, 0.3);
}

.delete-btn-grid {
  background: rgba(255, 69, 58, 0.15);
  color: #ff453a;
  border: 1px solid rgba(255, 69, 58, 0.3);
}

.delete-btn-grid:hover {
  background: rgba(255, 69, 58, 0.25);
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(255, 69, 58, 0.3);
}

/* Relationship Badges */
.relationship-badges {
  display: flex;
  flex-wrap: wrap;
  gap: 0.25rem;
  justify-content: center;
  margin-top: auto;
}

.relationship-badge {
  display: flex;
  align-items: center;
  gap: 0.125rem;
  padding: 0.125rem 0.25rem;
  border-radius: 8px;
  font-size: 0.6rem;
  font-weight: 500;
  backdrop-filter: blur(5px);
}

.relationship-badge.parent {
  background: rgba(52, 199, 89, 0.15);
  color: #34c759;
  border: 1px solid rgba(52, 199, 89, 0.3);
}

.relationship-badge.children {
  background: rgba(0, 255, 127, 0.15);
  color: #00ff7f;
  border: 1px solid rgba(0, 255, 127, 0.3);
}

.relationship-badge.spouse {
  background: rgba(255, 105, 180, 0.15);
  color: #ff69b4;
  border: 1px solid rgba(255, 105, 180, 0.3);
}

.relationship-badge svg {
  flex-shrink: 0;
}

/* Empty State Grid */
.empty-members-grid {
  text-align: center;
  padding: 4rem 2rem;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(15px);
  border: 2px dashed rgba(64, 224, 208, 0.3);
  border-radius: 20px;
  animation: fadeIn 0.5s ease;
}

.empty-icon-container {
  margin-bottom: 2rem;
}

.empty-icon {
  color: rgba(64, 224, 208, 0.4);
  animation: pulse 2s ease-in-out infinite;
}

@keyframes pulse {
  0%, 100% {
    opacity: 0.4;
    transform: scale(1);
  }
  50% {
    opacity: 0.7;
    transform: scale(1.05);
  }
}

.empty-members-grid h3 {
  color: rgba(255, 255, 255, 0.8);
  font-size: 1.5rem;
  margin: 0 0 1rem 0;
}

.empty-members-grid p {
  color: rgba(255, 255, 255, 0.6);
  font-size: 1.1rem;
  margin: 0 0 2rem 0;
  line-height: 1.5;
}

.add-first-member-btn {
  display: inline-flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1rem 2rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.3);
}

.add-first-member-btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 12px 35px rgba(0, 255, 127, 0.4);
}

/* Grid Responsive Design */
@media (max-width: 768px) {
  .members-grid {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 0.75rem;
  }
  
  .member-card-grid {
    padding: 0.75rem;
    min-height: 260px;
    max-height: 280px;
  }
  
  .grid-controls {
    flex-direction: column;
    gap: 0.75rem;
    text-align: center;
    padding: 0.75rem 1rem;
  }
  
  .member-actions-grid {
    flex-direction: column;
    gap: 0.375rem;
  }
  
  .action-btn {
    justify-content: center;
    padding: 0.5rem 1rem;
  }
  
  .relationship-badges {
    justify-content: center;
  }
}

@media (max-width: 480px) {
  .members-grid {
    grid-template-columns: 1fr;
    gap: 0.75rem;
  }
  
  .member-card-grid {
    min-height: 240px;
    max-height: 260px;
  }
  
  .avatar-image {
    width: 50px;
    height: 50px;
  }
  
  .member-name-grid {
    font-size: 0.9rem;
  }
  
  .detail-item {
    font-size: 0.7rem;
  }
  
  .empty-members-grid {
    padding: 2rem 1rem;
  }
  
  .empty-members-grid h3 {
    font-size: 1.2rem;
  }
  
  .empty-members-grid p {
    font-size: 1rem;
  }
  
  .status-indicator {
    width: 12px;
    height: 12px;
    right: calc(50% - 27px);
  }
  
  .gender-indicator {
    width: 8px;
    height: 8px;
    right: calc(50% - 27px);
  }
}