/* ===== Join Request Form Styling ===== */
.join-request-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.join-request-modal {
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 20px;
  width: 100%;
  max-width: 700px;
  max-height: 90vh;
  overflow: hidden;
  animation: slideUp 0.3s ease;
  margin: 2rem;
}

@keyframes slideUp {
  from { 
    opacity: 0;
    transform: translateY(30px);
  }
  to { 
    opacity: 1;
    transform: translateY(0);
  }
}

/* ===== Modal Header ===== */
.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 2rem 1rem 2rem;
  border-bottom: 1px solid rgba(64, 224, 208, 0.2);
  background: linear-gradient(90deg, rgba(64, 224, 208, 0.05), transparent);
}

.modal-header h2 {
  color: rgba(255, 255, 255, 0.95);
  font-size: 1.5rem;
  margin: 0;
}

.close-btn {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: none;
  background: rgba(255, 69, 58, 0.15);
  color: #ff453a;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}

.close-btn:hover {
  background: rgba(255, 69, 58, 0.25);
  transform: scale(1.05);
}

/* ===== Modal Body ===== */
.modal-body {
  padding: 2rem;
  max-height: 70vh;
  overflow-y: auto;
}

.modal-body::-webkit-scrollbar {
  width: 6px;
}

.modal-body::-webkit-scrollbar-track {
  background: rgba(64, 224, 208, 0.1);
  border-radius: 3px;
}

.modal-body::-webkit-scrollbar-thumb {
  background: rgba(64, 224, 208, 0.5);
  border-radius: 3px;
}

.modal-body::-webkit-scrollbar-thumb:hover {
  background: rgba(64, 224, 208, 0.7);
}

/* ===== Tree Info Section ===== */
.tree-info {
  text-align: center;
  padding: 1.5rem;
  background: rgba(0, 255, 127, 0.05);
  border: 1px solid rgba(0, 255, 127, 0.2);
  border-radius: 12px;
  margin-bottom: 2rem;
}

.tree-info h3 {
  color: #00ff7f;
  font-size: 1.3rem;
  margin: 0 0 0.5rem 0;
}

.tree-info p {
  color: rgba(255, 255, 255, 0.8);
  margin: 0;
  font-size: 0.95rem;
}

/* ===== Form Sections ===== */
.form-section {
  margin-bottom: 2rem;
  padding: 1.5rem;
  background: rgba(64, 224, 208, 0.02);
  border: 1px solid rgba(64, 224, 208, 0.1);
  border-radius: 12px;
}

.form-section h4 {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  color: #40e0d0;
  font-size: 1.1rem;
  font-weight: 600;
  margin: 0 0 1.5rem 0;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid rgba(64, 224, 208, 0.2);
}

.form-section:last-of-type {
  margin-bottom: 0;
}

/* ===== Form Grid ===== */
.form-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1.5rem;
}

.form-group {
  display: flex;
  flex-direction: column;
}

.form-group.full-width {
  grid-column: 1 / -1;
}

.form-group label {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.9rem;
  font-weight: 500;
  margin-bottom: 0.5rem;
}

.form-group label svg {
  color: #40e0d0;
}

/* ===== Form Inputs ===== */
.form-input {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 8px;
  color: rgba(255, 255, 255, 0.9);
  font-size: 1rem;
  padding: 0.75rem;
  transition: all 0.3s ease;
}

.form-input:focus {
  outline: none;
  border-color: #40e0d0;
  box-shadow: 0 0 20px rgba(64, 224, 208, 0.2);
  background: rgba(255, 255, 255, 0.08);
}

.form-input::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.form-input.error {
  border-color: rgba(255, 69, 58, 0.6);
  background: rgba(255, 69, 58, 0.05);
}

textarea.form-input {
  resize: vertical;
  min-height: 100px;
  font-family: inherit;
}

/* ===== Error Text ===== */
.error-text {
  color: #ff453a;
  font-size: 0.8rem;
  margin-top: 0.5rem;
  display: flex;
  align-items: center;
  gap: 0.25rem;
}

/* ===== Form Actions ===== */
.form-actions {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
  padding: 2rem 0 0 0;
  border-top: 1px solid rgba(64, 224, 208, 0.2);
  margin-top: 2rem;
}

.cancel-btn,
.submit-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  padding: 0.875rem 2rem;
  border-radius: 12px;
  border: none;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  min-width: 140px;
}

.cancel-btn {
  background: rgba(64, 224, 208, 0.1);
  color: #40e0d0;
  border: 1px solid rgba(64, 224, 208, 0.3);
}

.cancel-btn:hover:not(:disabled) {
  background: rgba(64, 224, 208, 0.2);
  border-color: #40e0d0;
}

.submit-btn {
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
}

.submit-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.4);
}

.submit-btn:disabled,
.cancel-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

/* ===== Join Request Status Styles ===== */
.join-status {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  background: rgba(255, 193, 7, 0.1);
  color: #ffc107;
  border: 1px solid rgba(255, 193, 7, 0.3);
  border-radius: 8px;
  font-size: 0.9rem;
}

/* ===== Join Request Button ===== */
.join-btn {
  background: linear-gradient(135deg, #00ff7f, #40e0d0) !important;
  color: white !important;
  border: none !important;
}

.join-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.4);
}

/* ===== Responsive Design ===== */
@media (max-width: 768px) {
  .join-request-modal {
    margin: 1rem;
    max-height: 95vh;
  }
  
  .modal-header {
    padding: 1.5rem 1.5rem 1rem 1.5rem;
  }
  
  .modal-header h2 {
    font-size: 1.2rem;
  }
  
  .modal-body {
    padding: 1.5rem;
  }
  
  .form-grid {
    grid-template-columns: 1fr;
  }
  
  .form-actions {
    flex-direction: column;
  }
  
  .cancel-btn,
  .submit-btn {
    width: 100%;
  }
}

@media (max-width: 480px) {
  .join-request-modal {
    margin: 0.5rem;
    border-radius: 16px;
  }
  
  .modal-header {
    padding: 1rem;
  }
  
  .modal-body {
    padding: 1rem;
  }
  
  .form-section {
    padding: 1rem;
  }
}