/* ===== Modern About Section with Dark Green/Blue/Black Theme ===== */
.about-section {
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 25%, #0f4c75 50%, #3282b8 75%, #0e4b4e 100%);
  padding: 5rem 2rem;
  min-height: 100vh;
  position: relative;
}

.about-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 20% 20%, rgba(0, 255, 127, 0.05) 0%, transparent 50%),
    radial-gradient(circle at 80% 80%, rgba(30, 144, 255, 0.05) 0%, transparent 50%);
  pointer-events: none;
}

/* Container max width */
.about-container {
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
  z-index: 1;
}

/* Flex row for image and text */
.about-content {
  display: flex;
  flex-wrap: wrap;
  gap: 3rem;
  align-items: center;
  margin-bottom: 4rem;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border-radius: 24px;
  padding: 3rem;
  border: 1px solid rgba(64, 224, 208, 0.2);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.3);
  position: relative;
}

.about-content::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #00ff7f, #40e0d0, #1e90ff);
  border-radius: 24px 24px 0 0;
}

/* Text styling */
.about-text {
  flex: 1 1 400px;
}

.about-text h2 {
  font-size: 2.5rem;
  margin-bottom: 1.5rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-weight: 800;
}

.about-text h5 {
  font-size: 1.4rem;
  margin: 1.5rem 0;
  color: rgba(255, 255, 255, 0.9);
  font-weight: 600;
}

.about-text p {
  color: rgba(255, 255, 255, 0.8);
  line-height: 1.7;
  margin-bottom: 1rem;
  font-size: 1.1rem;
}

.about-text .lead {
  font-weight: 600;
  font-size: 1.2rem;
  color: rgba(255, 255, 255, 0.9);
}

/* Image styling */
.about-image {
  flex: 1 1 300px;
  text-align: center;
}

.about-image img {
  max-width: 100%;
  border-radius: 20px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.4);
  border: 2px solid rgba(64, 224, 208, 0.3);
  transition: all 0.3s ease;
}

.about-image img:hover {
  transform: scale(1.05);
  box-shadow: 0 25px 50px rgba(0, 255, 127, 0.15);
  border-color: rgba(0, 255, 127, 0.5);
}

/* Video section */
.about-video {
  text-align: center;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border-radius: 24px;
  padding: 3rem;
  border: 1px solid rgba(64, 224, 208, 0.2);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.3);
  position: relative;
}

.about-video::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #00ff7f, #40e0d0, #1e90ff);
  border-radius: 24px 24px 0 0;
}

.about-video h5 {
  font-size: 1.8rem;
  margin-bottom: 1rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-weight: 700;
}

.about-video p {
  margin-bottom: 2rem;
  color: rgba(255, 255, 255, 0.8);
  font-size: 1.1rem;
  line-height: 1.6;
}

.video-wrapper {
  position: relative;
  padding-bottom: 56.25%; /* 16:9 */
  height: 0;
  overflow: hidden;
  border-radius: 20px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.4);
  border: 2px solid rgba(64, 224, 208, 0.3);
}

.video-wrapper video,
.video-wrapper iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
  border-radius: 18px;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .about-section {
    padding: 3rem 1rem;
  }
  
  .about-content {
    flex-direction: column;
    padding: 2rem;
    gap: 2rem;
  }

  .about-text,
  .about-image {
    text-align: center;
  }
  
  .about-text h2 {
    font-size: 2rem;
  }
  
  .about-video {
    padding: 2rem;
  }
  
  .about-video h5 {
    font-size: 1.5rem;
  }
}

@media (max-width: 480px) {
  .about-content,
  .about-video {
    padding: 1.5rem;
  }
  
  .about-text h2 {
    font-size: 1.8rem;
  }
  
  .about-video h5 {
    font-size: 1.3rem;
  }
}

/* ===== About Us Section ===== */
.about-us-section {
  margin-top: 4rem;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border-radius: 24px;
  padding: 4rem 3rem;
  border: 1px solid rgba(64, 224, 208, 0.2);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.3);
  position: relative;
}

.about-us-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #00ff7f, #40e0d0, #1e90ff);
  border-radius: 24px 24px 0 0;
}

/* About Us Header */
.about-us-header {
  text-align: center;
  margin-bottom: 3rem;
}

.about-us-title {
  font-size: 2.8rem;
  margin-bottom: 1rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-weight: 800;
}

.about-us-subtitle {
  font-size: 1.6rem;
  margin-bottom: 1.5rem;
  color: rgba(255, 255, 255, 0.9);
  font-weight: 600;
}

.about-us-description {
  font-size: 1.2rem;
  line-height: 1.7;
  color: rgba(255, 255, 255, 0.8);
  max-width: 800px;
  margin: 0 auto;
}

/* Mission & Vision Cards */
.mission-vision-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 2rem;
  margin-bottom: 4rem;
}

.mission-card,
.vision-card {
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(15px);
  border-radius: 20px;
  padding: 2.5rem;
  border: 1px solid rgba(64, 224, 208, 0.2);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
}

.mission-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, #00ff7f, #40e0d0);
  border-radius: 20px 20px 0 0;
}

.vision-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, #40e0d0, #1e90ff);
  border-radius: 20px 20px 0 0;
}

.mission-card:hover,
.vision-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 20px 40px rgba(0, 255, 127, 0.15);
  border-color: rgba(0, 255, 127, 0.4);
}

.mission-card h4,
.vision-card h4 {
  font-size: 1.8rem;
  margin-bottom: 1.5rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-weight: 700;
}

.mission-card p,
.vision-card p {
  color: rgba(255, 255, 255, 0.8);
  line-height: 1.7;
  font-size: 1.1rem;
}

/* Features Section */
.features-section {
  text-align: center;
}

.features-title {
  font-size: 2.2rem;
  margin-bottom: 2.5rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-weight: 700;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 2rem;
}

.feature-card {
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(15px);
  border-radius: 20px;
  padding: 2.5rem 2rem;
  border: 1px solid rgba(64, 224, 208, 0.2);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  text-align: center;
  position: relative;
}

.feature-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, #00ff7f, #40e0d0, #1e90ff);
  border-radius: 20px 20px 0 0;
}

.feature-card:hover {
  transform: translateY(-8px) scale(1.02);
  box-shadow: 0 25px 50px rgba(0, 255, 127, 0.15);
  border-color: rgba(0, 255, 127, 0.4);
}

.feature-icon {
  font-size: 3rem;
  margin-bottom: 1.5rem;
  opacity: 0.9;
}

.feature-card h5 {
  font-size: 1.4rem;
  margin-bottom: 1rem;
  color: rgba(255, 255, 255, 0.95);
  font-weight: 600;
}

.feature-card p {
  color: rgba(255, 255, 255, 0.8);
  line-height: 1.6;
  font-size: 1rem;
}

/* Responsive Design for About Us Section */
@media (max-width: 768px) {
  .about-us-section {
    padding: 3rem 2rem;
    margin-top: 3rem;
  }
  
  .about-us-title {
    font-size: 2.2rem;
  }
  
  .about-us-subtitle {
    font-size: 1.4rem;
  }
  
  .about-us-description {
    font-size: 1.1rem;
  }
  
  .mission-vision-container {
    grid-template-columns: 1fr;
    gap: 1.5rem;
    margin-bottom: 3rem;
  }
  
  .mission-card,
  .vision-card {
    padding: 2rem;
  }
  
  .mission-card h4,
  .vision-card h4 {
    font-size: 1.5rem;
  }
  
  .features-title {
    font-size: 1.8rem;
  }
  
  .features-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .feature-card {
    padding: 2rem 1.5rem;
  }
}

@media (max-width: 480px) {
  .about-us-section {
    padding: 2rem 1.5rem;
  }
  
  .about-us-title {
    font-size: 1.8rem;
  }
  
  .about-us-subtitle {
    font-size: 1.2rem;
  }
  
  .about-us-description {
    font-size: 1rem;
  }
  
  .mission-card,
  .vision-card,
  .feature-card {
    padding: 1.5rem;
  }
  
  .mission-card h4,
  .vision-card h4 {
    font-size: 1.3rem;
  }
  
  .features-title {
    font-size: 1.6rem;
  }
  
  .feature-icon {
    font-size: 2.5rem;
  }
}
