/* ===== Modern Contact Page with Dark Green/Blue/Black Theme ===== */
body {
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 25%, #0f4c75 50%, #3282b8 75%, #0e4b4e 100%);
}

.contact-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 3rem;
  padding: 5rem 2rem;
  margin-top: 0 !important;
  min-height: 100vh;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 25%, #0f4c75 50%, #3282b8 75%, #0e4b4e 100%);
  position: relative;
}

.contact-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 20% 20%, rgba(0, 255, 127, 0.05) 0%, transparent 50%),
    radial-gradient(circle at 80% 80%, rgba(30, 144, 255, 0.05) 0%, transparent 50%);
  pointer-events: none;
}
  
  .contact-content {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  max-width: 1200px;
  width: 100%;
  padding: 3rem;
  border-radius: 24px;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(64, 224, 208, 0.2);
  position: relative;
  z-index: 1;
}

.contact-content::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #00ff7f, #40e0d0, #1e90ff);
  border-radius: 24px 24px 0 0;
}
  
  @media (min-width: 768px) {
    .contact-content {
      flex-direction: row; 
      align-items: center;
    }
  
 
    .image-container {
      flex: 2; 
    }
  
    .form-container {
      flex: 3;
    }
  }
  
  .image-container img {
  width: 100%;
  border-radius: 20px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.4);
  border: 2px solid rgba(64, 224, 208, 0.3);
  margin-top: 0;
  height: 400px;
  object-fit: cover;
  transition: all 0.3s ease;
}

.image-container img:hover {
  transform: scale(1.05);
  box-shadow: 0 25px 50px rgba(0, 255, 127, 0.15);
  border-color: rgba(0, 255, 127, 0.5);
}
  

  .form-container {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.form-container h2 {
  font-size: 2.5rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 1rem;
  text-align: center;
  font-weight: 800;
}
  

  .name-inputs {
    display: flex;
    gap: 10px;
  }
  
  .name-inputs input {
    flex: 1;
  }
  
  input,
textarea {
  width: 100%;
  padding: 1rem;
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 12px;
  font-size: 1rem;
  /* margin-bottom: 1.5rem ; */
  outline: none ;
  background: rgba(255, 255, 255, 0.05);
  color: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
}

input::placeholder,
textarea::placeholder {
  color: rgba(255, 255, 255, 0.6);
}

input:focus,
textarea:focus {
  border-color: #00ff7f;
  background: rgba(0, 255, 127, 0.1);
  box-shadow: 0 0 0 3px rgba(0, 255, 127, 0.1);
}
  
  textarea {
    resize: none;
  }
  
  .cbtn {
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
  padding: 1rem 2rem;
  border: none;
  border-radius: 12px;
  font-size: 1.1rem;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  width: 100%;
  letter-spacing: 2px;
  font-weight: 700;
  text-transform: uppercase;
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.3);
}

.cbtn:hover {
  transform: translateY(-3px);
  box-shadow: 0 12px 35px rgba(0, 255, 127, 0.4);
}
  

  .opening-hours {
  text-align: center;
  padding: 2rem;
  border-radius: 20px;
  max-width: 500px;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(64, 224, 208, 0.2);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
  position: relative;
}

.opening-hours::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, #00ff7f, #40e0d0, #1e90ff);
  border-radius: 20px 20px 0 0;
}

.opening-hours h3 {
  margin-bottom: 1rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-weight: 700;
  font-size: 1.5rem;
}

/* Responsive Design */
@media (max-width: 768px) {
  .contact-container {
    padding: 3rem 1rem;
  }
  
  .contact-content {
    padding: 2rem;
    gap: 1.5rem;
  }
  
  .form-container h2 {
    font-size: 2rem;
  }
  
  .image-container img {
    height: 300px;
  }
  
  .name-inputs {
    flex-direction: column;
    gap: 0;
  }
}

@media (max-width: 480px) {
  .contact-content {
    padding: 1.5rem;
  }
  
  .form-container h2 {
    font-size: 1.8rem;
  }
  
  .cbtn {
    letter-spacing: 1px;
  }
}
  