/* ===== Modern Footer with Dark Green/Blue/Black Theme ===== */
.footer {
  background: linear-gradient(135deg, rgba(0, 0, 0, 0.95) 0%, rgba(26, 26, 46, 0.95) 50%, rgba(15, 76, 117, 0.95) 100%);
  backdrop-filter: blur(20px);
  color: rgba(255, 255, 255, 0.9);
  padding: 4rem 0 0;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  position: relative;
  border-top: 1px solid rgba(64, 224, 208, 0.2);
}

.footer::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 20% 20%, rgba(0, 255, 127, 0.05) 0%, transparent 50%),
    radial-gradient(circle at 80% 80%, rgba(30, 144, 255, 0.05) 0%, transparent 50%);
  pointer-events: none;
}

.footer-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  position: relative;
  z-index: 1;
}

/* ===== Newsletter Section ===== */
.newsletter-section {
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border-radius: 24px;
  padding: 4rem 3rem;
  margin-bottom: 4rem;
  border: 1px solid rgba(64, 224, 208, 0.2);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.3);
  position: relative;
}

.newsletter-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #00ff7f, #40e0d0, #1e90ff);
  border-radius: 24px 24px 0 0;
}

.newsletter-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3rem;
  align-items: center;
}

.newsletter-text {
  text-align: left;
}

.newsletter-title {
  font-size: 2.5rem;
  font-weight: 800;
  margin-bottom: 0.5rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.newsletter-subtitle {
  font-size: 1.6rem;
  font-weight: 600;
  margin-bottom: 1rem;
  color: rgba(255, 255, 255, 0.9);
}

.newsletter-description {
  font-size: 1.1rem;
  line-height: 1.7;
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 0;
}

.newsletter-form-container {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.newsletter-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.input-group {
  display: flex;
  gap: 0.75rem;
  align-items: stretch;
}

.newsletter-input {
  flex: 1;
  padding: 1.25rem 1.5rem;
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 12px;
  background: rgba(255, 255, 255, 0.05);
  color: rgba(255, 255, 255, 0.9);
  font-size: 1rem;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
}

.newsletter-input:focus {
  outline: none;
  border-color: #40e0d0;
  background: rgba(64, 224, 208, 0.1);
  box-shadow: 0 0 0 3px rgba(64, 224, 208, 0.15);
  transform: translateY(-2px);
}

.newsletter-input::placeholder {
  color: rgba(255, 255, 255, 0.6);
}

.newsletter-button {
  padding: 1.25rem 2rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
  border: none;
  border-radius: 12px;
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0 4px 15px rgba(0, 255, 127, 0.3);
  min-width: 140px;
}

.newsletter-button:hover {
  background: linear-gradient(135deg, #00cc64, #1e90ff);
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.4);
}

.newsletter-success {
  padding: 1rem;
  background: rgba(0, 255, 127, 0.1);
  border: 1px solid rgba(0, 255, 127, 0.3);
  border-radius: 10px;
  color: #00ff7f;
  text-align: center;
  font-weight: 600;
  animation: fadeInUp 0.5s ease-out;
}

.newsletter-privacy {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.6);
  text-align: center;
  margin: 0;
}

/* Newsletter Responsive Design */
@media (max-width: 1024px) {
  .newsletter-content {
    grid-template-columns: 1fr;
    gap: 2.5rem;
    text-align: center;
  }
  
  .newsletter-text {
    text-align: center;
  }
}

@media (max-width: 768px) {
  .newsletter-section {
    padding: 3rem 2rem;
    margin-bottom: 3rem;
  }
  
  .newsletter-title {
    font-size: 2rem;
  }
  
  .newsletter-subtitle {
    font-size: 1.4rem;
  }
  
  .newsletter-description {
    font-size: 1rem;
  }
  
  .input-group {
    flex-direction: column;
    gap: 1rem;
  }
  
  .newsletter-button {
    width: 100%;
  }
}

@media (max-width: 480px) {
  .newsletter-section {
    padding: 2rem 1.5rem;
  }
  
  .newsletter-title {
    font-size: 1.8rem;
  }
  
  .newsletter-subtitle {
    font-size: 1.2rem;
  }
  
  .newsletter-input,
  .newsletter-button {
    padding: 1rem 1.25rem;
  }
}

.footer-top {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 3rem;
  margin-bottom: 3rem;
}

.footer-column {
  padding: 0;
}

.footer-heading {
  font-size: 1.3rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
  position: relative;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.footer-heading::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -8px;
  width: 60px;
  height: 3px;
  background: linear-gradient(90deg, #00ff7f, #40e0d0, #1e90ff);
  border-radius: 2px;
}

.footer-text {
  margin-bottom: 1.5rem;
  line-height: 1.7;
  color: rgba(255, 255, 255, 0.8);
  font-weight: 400;
}

.social-icons {
  display: flex;
  gap: 1rem;
  margin-top: 1.5rem;
}

.social-icons a {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 45px;
  height: 45px;
  background: rgba(64, 224, 208, 0.1);
  color: #40e0d0;
  font-size: 1.2rem;
  border-radius: 12px;
  border: 1px solid rgba(64, 224, 208, 0.2);
  backdrop-filter: blur(10px);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.social-icons a:hover {
  background: rgba(0, 255, 127, 0.2);
  color: #00ff7f;
  transform: translateY(-3px) scale(1.1);
  box-shadow: 0 10px 25px rgba(0, 255, 127, 0.3);
  border-color: #00ff7f;
}

.footer-links {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-links li {
  margin-bottom: 1rem;
}

.footer-links a {
  color: rgba(255, 255, 255, 0.8);
  text-decoration: none;
  font-weight: 500;
  transition: all 0.3s ease;
  display: inline-block;
  padding: 0.25rem 0;
  border-bottom: 1px solid transparent;
}

.footer-links a:hover {
  color: #40e0d0;
  border-bottom-color: rgba(64, 224, 208, 0.5);
  transform: translateX(5px);
}

.footer-contact {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-contact li {
  display: flex;
  align-items: flex-start;
  margin-bottom: 1.2rem;
  padding: 0.75rem;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
  border: 1px solid rgba(64, 224, 208, 0.1);
  transition: all 0.3s ease;
}

.footer-contact li:hover {
  background: rgba(64, 224, 208, 0.1);
  border-color: rgba(64, 224, 208, 0.3);
  transform: translateY(-2px);
}

.contact-icon {
  margin-right: 12px;
  color: #40e0d0;
  font-size: 1.3rem;
  margin-top: 2px;
  flex-shrink: 0;
}

/* Old newsletter form styles - kept for backwards compatibility if needed */
.footer .newsletter-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.footer .newsletter-form input {
  padding: 1rem;
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.05);
  color: rgba(255, 255, 255, 0.9);
  font-size: 0.95rem;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
}

.footer .newsletter-form input:focus {
  outline: none;
  border-color: #40e0d0;
  background: rgba(64, 224, 208, 0.1);
  box-shadow: 0 0 0 3px rgba(64, 224, 208, 0.1);
}

.footer .newsletter-form input::placeholder {
  color: rgba(255, 255, 255, 0.6);
}

.footer .newsletter-form button {
  padding: 1rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
  border: none;
  border-radius: 10px;
  font-weight: 600;
  font-size: 0.95rem;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0 4px 15px rgba(0, 255, 127, 0.3);
}

.footer .newsletter-form button:hover {
  background: linear-gradient(135deg, #00cc64, #1e90ff);
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.4);
}

.footer-bottom {
  border-top: 1px solid rgba(64, 224, 208, 0.2);
  padding: 2rem 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  background: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(10px);
  border-radius: 15px 15px 0 0;
  margin-top: 2rem;
}

.copyright {
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.9rem;
  font-weight: 500;
}

.footer-links-bottom {
  display: flex;
  gap: 1.5rem;
}

.footer-links-bottom a {
  color: rgba(255, 255, 255, 0.8);
  text-decoration: none;
  font-size: 0.9rem;
  font-weight: 500;
  padding: 0.5rem 1rem;
  border-radius: 8px;
  border: 1px solid transparent;
  transition: all 0.3s ease;
}

.footer-links-bottom a:hover {
  color: #40e0d0;
  background: rgba(64, 224, 208, 0.1);
  border-color: rgba(64, 224, 208, 0.2);
  transform: translateY(-2px);
}

/* Responsive Design */
@media (max-width: 768px) {
  .footer {
    padding: 3rem 0 0;
  }
  
  .footer-container {
    padding: 0 1.5rem;
  }
  
  .footer-top {
    grid-template-columns: 1fr;
    gap: 2.5rem;
  }
  
  .footer-column {
    margin-bottom: 2rem;
  }
  
  .footer-bottom {
    flex-direction: column;
    text-align: center;
    gap: 1.5rem;
    padding: 1.5rem;
  }
  
  .footer-links-bottom {
    justify-content: center;
    flex-wrap: wrap;
  }
  
  .social-icons {
    justify-content: center;
  }
}

@media (max-width: 480px) {
  .footer-top {
    gap: 2rem;
  }
  
  .footer-links-bottom {
    flex-direction: column;
    gap: 1rem;
  }
  
  .social-icons a {
    width: 40px;
    height: 40px;
    font-size: 1.1rem;
  }
  
  .newsletter-form {
    gap: 0.75rem;
  }
  
  .newsletter-form input,
  .newsletter-form button {
    padding: 0.875rem;
  }
}

/* Modern Animations and Effects */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideInLeft {
  from {
    opacity: 0;
    transform: translateX(-30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-5px);
  }
}

.footer-column {
  animation: fadeInUp 0.6s ease-out;
}

.footer-column:nth-child(1) { animation-delay: 0.1s; }
.footer-column:nth-child(2) { animation-delay: 0.2s; }
.footer-column:nth-child(3) { animation-delay: 0.3s; }
.footer-column:nth-child(4) { animation-delay: 0.4s; }

.social-icons a {
  animation: float 3s ease-in-out infinite;
}

.social-icons a:nth-child(1) { animation-delay: 0s; }
.social-icons a:nth-child(2) { animation-delay: 0.2s; }
.social-icons a:nth-child(3) { animation-delay: 0.4s; }
.social-icons a:nth-child(4) { animation-delay: 0.6s; }
.social-icons a:nth-child(5) { animation-delay: 0.8s; }

/* Glowing border effect */
.footer-heading::after {
  animation: glow 2s ease-in-out infinite alternate;
}

@keyframes glow {
  from {
    box-shadow: 0 0 5px rgba(0, 255, 127, 0.5);
  }
  to {
    box-shadow: 0 0 15px rgba(64, 224, 208, 0.8);
  }
}