/* ===== Modern Plans/Subscription Page with Dark Green/Blue/Black Theme ===== */
.plans-container {
  padding: 4rem 2rem;
  text-align: center;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 25%, #0f4c75 50%, #3282b8 75%, #0e4b4e 100%);
  min-height: 100vh;
  position: relative;
}

.plans-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 20% 20%, rgba(0, 255, 127, 0.05) 0%, transparent 50%),
    radial-gradient(circle at 80% 80%, rgba(30, 144, 255, 0.05) 0%, transparent 50%);
  pointer-events: none;
}

.section-heading {
  font-size: 3.5rem;
  font-weight: 800;
  margin-bottom: 1rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  position: relative;
  z-index: 1;
}

.section-subtitle {
  font-size: 1.3rem;
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 2rem;
  font-weight: 500;
  position: relative;
  z-index: 1;
}

/* Billing Toggle Section */
.billing-toggle-section {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 3rem;
  position: relative;
  z-index: 1;
}

.billing-toggle {
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(15px);
  padding: 0.5rem;
  border-radius: 16px;
  border: 1px solid rgba(64, 224, 208, 0.2);
  display: flex;
  align-items: center;
  gap: 1rem;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
}

.billing-option {
  padding: 0.75rem 1.5rem;
  border-radius: 12px;
  background: transparent;
  color: rgba(255, 255, 255, 0.7);
  border: none;
  cursor: pointer;
  font-weight: 600;
  font-size: 0.95rem;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
}

.billing-option.active {
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
  box-shadow: 0 4px 15px rgba(0, 255, 127, 0.3);
  transform: scale(1.05);
}

.billing-option:not(.active):hover {
  color: rgba(255, 255, 255, 0.9);
  background: rgba(64, 224, 208, 0.1);
}

.yearly-savings {
  background: linear-gradient(135deg, #ff6b6b, #ff8e8e);
  color: white;
  padding: 0.25rem 0.75rem;
  border-radius: 8px;
  font-size: 0.8rem;
  font-weight: 600;
  margin-left: 0.5rem;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.05); }
}

.plans-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 2rem;
  max-width: 1400px;
  margin: 0 auto;
  position: relative;
  z-index: 1;
}

/* Modern Plan Cards with Dark Glass Effect */
.plan-card {
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border-radius: 24px;
  padding: 2.5rem 2rem;
  box-shadow: 
    0 25px 50px rgba(0, 0, 0, 0.3),
    0 0 0 1px rgba(64, 224, 208, 0.2);
  border: 1px solid rgba(64, 224, 208, 0.2);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 600px;
}

.plan-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #00ff7f, #40e0d0, #1e90ff);
  border-radius: 24px 24px 0 0;
}

.plan-card:hover {
  transform: translateY(-10px) scale(1.02);
  box-shadow: 
    0 35px 70px rgba(0, 255, 127, 0.15),
    0 0 0 1px rgba(0, 255, 127, 0.3);
  border-color: rgba(0, 255, 127, 0.4);
}

.plan-card.popular {
  transform: scale(1.05);
  box-shadow: 
    0 30px 60px rgba(0, 255, 127, 0.2),
    0 0 0 2px rgba(0, 255, 127, 0.3);
  border-color: rgba(0, 255, 127, 0.4);
}

.plan-card.popular::before {
  height: 6px;
  background: linear-gradient(90deg, #00ff7f, #40e0d0);
}

.plan-card.popular:hover {
  transform: translateY(-15px) scale(1.08);
  box-shadow: 
    0 40px 80px rgba(0, 255, 127, 0.25),
    0 0 0 2px rgba(0, 255, 127, 0.4);
}

.plan-name {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
  color: rgba(255, 255, 255, 0.95);
  position: relative;
}

.plan-name.popular {
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* Price Styling */
.plan-price {
  margin: 1.5rem 0;
  text-align: center;
}

.price {
  font-size: 3rem;
  font-weight: 800;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 0.5rem;
  display: flex;
  align-items: baseline;
  justify-content: center;
  gap: 0.5rem;
}

.original-price {
  font-size: 1.2rem;
  color: rgba(255, 255, 255, 0.5);
  text-decoration: line-through;
  margin-right: 0.5rem;
}

.savings-badge {
  background: linear-gradient(135deg, #ff6b6b, #ff8e8e);
  color: white;
  padding: 0.25rem 0.75rem;
  border-radius: 12px;
  font-size: 0.8rem;
  font-weight: 600;
  margin-top: 0.5rem;
  display: inline-block;
}

.price .currency {
  font-size: 1.5rem;
  font-weight: 600;
}

.price .amount {
  font-size: 3.5rem;
}

.price .period {
  font-size: 1.2rem;
  color: rgba(255, 255, 255, 0.7);
  font-weight: 500;
}

.duration {
  font-size: 1.1rem;
  color: #1e90ff;
  margin-bottom: 2rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

/* Feature List Styling */
.feature-list {
  list-style: none;
  padding: 0;
  margin: 2rem 0;
  text-align: left;
}

.feature-list li {
  margin: 1rem 0;
  font-size: 1rem;
  color: rgba(255, 255, 255, 0.9);
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.75rem 0;
  border-bottom: 1px solid rgba(64, 224, 208, 0.2);
  transition: all 0.3s ease;
}

.feature-list li:hover {
  background: rgba(64, 224, 208, 0.05);
  padding-left: 1rem;
  border-radius: 8px;
}

.feature-list li:last-child {
  border-bottom: none;
}

.feature-list li::before {
  content: '';
  color: #00ff7f;
  font-weight: bold;
  font-size: 1.2rem;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: rgba(0, 255, 127, 0.1);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

/* Subscribe Button Styling */
.subscribe-btn {
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
  border: none;
  padding: 1.2rem 2.5rem;
  font-size: 1.1rem;
  font-weight: 600;
  border-radius: 16px;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  width: 100%;
  position: relative;
  overflow: hidden;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.3);
}

.subscribe-btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 15px 40px rgba(0, 255, 127, 0.4);
}

.subscribe-btn.popular {
  background: linear-gradient(135deg, #1e90ff, #40e0d0);
  font-size: 1.2rem;
  padding: 1.5rem 2.5rem;
}

.subscribe-btn.popular:hover {
  box-shadow: 0 15px 40px rgba(30, 144, 255, 0.4);
}

.subscribe-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.15), transparent);
  transition: left 0.6s;
}

.subscribe-btn:hover::before {
  left: 100%;
}

/* Popular Badge */
.popular-badge {
  position: absolute;
  top: -1px;
  right: 20px;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
  padding: 0.5rem 1.5rem;
  font-size: 0.9rem;
  font-weight: 600;
  border-radius: 0 0 12px 12px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

/* Additional Features Section */
.additional-features {
  margin-top: 3rem;
  padding-top: 2rem;
  border-top: 2px solid rgba(64, 224, 208, 0.2);
}

.additional-features h4 {
  font-size: 1.2rem;
  font-weight: 700;
  color: rgba(255, 255, 255, 0.9);
  margin-bottom: 1rem;
}

/* Responsive Design */
@media (max-width: 768px) {
  .plans-container {
    padding: 2rem 1rem;
  }
  
  .section-heading {
    font-size: 2.5rem;
  }
  
  .section-subtitle {
    font-size: 1.1rem;
  }
  
  .plans-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .plan-card {
    padding: 2rem 1.5rem;
  }
  
  .plan-card.popular {
    transform: none;
  }
  
  .billing-toggle {
    flex-direction: column;
    gap: 0.5rem;
    padding: 1rem;
  }
  
  .billing-option {
    width: 100%;
    text-align: center;
  }
  
  .price {
    font-size: 2.5rem;
  }
  
  .price .amount {
    font-size: 3rem;
  }
}

@media (max-width: 480px) {
  .section-heading {
    font-size: 2rem;
  }
  
  .price {
    font-size: 2rem;
    flex-direction: column;
    gap: 0;
  }
  
  .price .amount {
    font-size: 2.5rem;
  }
  
  .plan-card {
    padding: 1.5rem;
  }
}
