/* ===== Modern Payment Page with Dark Green/Blue/Black Theme ===== */
.payment-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 25%, #0f4c75 50%, #3282b8 75%, #0e4b4e 100%);
  padding: 2rem 1rem;
  position: relative;
}

.payment-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 20% 20%, rgba(0, 255, 127, 0.05) 0%, transparent 50%),
    radial-gradient(circle at 80% 80%, rgba(30, 144, 255, 0.05) 0%, transparent 50%);
  pointer-events: none;
}

.payment-content {
  max-width: 1400px;
  margin: 0 auto;
  position: relative;
  z-index: 1;
}

/* Header Section */
.payment-header {
  text-align: center;
  margin-bottom: 3rem;
}

.back-btn {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  background: rgba(64, 224, 208, 0.1);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 10px;
  padding: 0.75rem 1.5rem;
  color: #40e0d0;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-bottom: 2rem;
  text-decoration: none;
  font-weight: 500;
}

.back-btn:hover {
  background: rgba(64, 224, 208, 0.2);
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(64, 224, 208, 0.3);
}

.payment-title {
  font-size: 3rem;
  font-weight: 800;
  margin-bottom: 1rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.payment-subtitle {
  font-size: 1.2rem;
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 0;
}

.payment-loading {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50vh;
  font-size: 1.2rem;
  color: #40e0d0;
}

/* Layout */
.payment-layout {
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 3rem;
  align-items: start;
}

/* Order Summary */
.order-summary {
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border-radius: 20px;
  padding: 2rem;
  border: 1px solid rgba(64, 224, 208, 0.2);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.3);
  position: sticky;
  top: 2rem;
}

.summary-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: rgba(255, 255, 255, 0.95);
  margin-bottom: 1.5rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.plan-summary {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
  background: rgba(64, 224, 208, 0.05);
  border-radius: 12px;
  border: 1px solid rgba(64, 224, 208, 0.1);
  margin-bottom: 1.5rem;
}

.plan-info .plan-name {
  font-size: 1.2rem;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.95);
  margin-bottom: 0.25rem;
}

.plan-info .plan-billing {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.7);
  margin: 0;
}

.plan-price {
  text-align: right;
}

.plan-price .price {
  font-size: 1.8rem;
  font-weight: 700;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.plan-price .period {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.7);
}

.savings-info {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: #00ff7f;
  font-weight: 600;
  font-size: 0.9rem;
  margin-bottom: 1.5rem;
  padding: 0.75rem;
  background: rgba(0, 255, 127, 0.1);
  border-radius: 8px;
  border: 1px solid rgba(0, 255, 127, 0.2);
}

.features-included {
  margin-bottom: 2rem;
}

.features-included h5 {
  font-size: 1rem;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.9);
  margin-bottom: 1rem;
}

.features-included ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.features-included li {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.9rem;
  margin-bottom: 0.75rem;
  padding: 0.5rem;
  border-radius: 6px;
  transition: all 0.3s ease;
}

.features-included li:hover {
  background: rgba(64, 224, 208, 0.05);
}

.features-included li svg {
  color: #00ff7f;
  flex-shrink: 0;
}

.total-section {
  border-top: 1px solid rgba(64, 224, 208, 0.2);
  padding-top: 1.5rem;
}

.total-row, .discount-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.75rem;
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.95rem;
}

.discount-row .discount {
  color: #00ff7f;
  font-weight: 600;
}

.total-final {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.2rem;
  font-weight: 700;
  color: rgba(255, 255, 255, 0.95);
  padding: 1rem 0;
  border-top: 1px solid rgba(64, 224, 208, 0.2);
  margin-top: 0.75rem;
}

/* Payment Form */
.payment-form-section {
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border-radius: 20px;
  padding: 2.5rem;
  border: 1px solid rgba(64, 224, 208, 0.2);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.3);
}

.form-section {
  margin-bottom: 2.5rem;
}

.section-title {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  font-size: 1.3rem;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.95);
  margin-bottom: 1.5rem;
  padding-bottom: 0.75rem;
  border-bottom: 1px solid rgba(64, 224, 208, 0.2);
}

.section-title svg {
  color: #40e0d0;
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1.5rem;
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.9);
  margin-bottom: 0.5rem;
  font-size: 0.95rem;
}

.form-group input {
  width: 100%;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 10px;
  color: rgba(255, 255, 255, 0.9);
  font-size: 0.95rem;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
}

.form-group input:focus {
  outline: none;
  border-color: #40e0d0;
  background: rgba(255, 255, 255, 0.08);
  box-shadow: 0 0 0 3px rgba(64, 224, 208, 0.1);
}

.form-group input.error {
  border-color: #ff4757;
  background: rgba(255, 71, 87, 0.1);
}

.form-group input::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.input-with-icon {
  position: relative;
}

.input-with-icon svg {
  position: absolute;
  left: 1rem;
  top: 50%;
  transform: translateY(-50%);
  color: rgba(255, 255, 255, 0.5);
  z-index: 1;
}

.input-with-icon input {
  padding-left: 3rem;
}

.error-message {
  color: #ff4757;
  font-size: 0.85rem;
  margin-top: 0.5rem;
  display: block;
  font-weight: 500;
}

/* Payment Methods */
.payment-methods {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1rem;
  margin-bottom: 2rem;
}

.payment-option {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
  padding: 1.25rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
  color: rgba(255, 255, 255, 0.8);
  position: relative;
  overflow: hidden;
}

.payment-option::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 100%;
  background: transparent;
  transition: all 0.3s ease;
}

.payment-option:hover {
  background: rgba(64, 224, 208, 0.1);
  border-color: rgba(64, 224, 208, 0.4);
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(64, 224, 208, 0.15);
}

.payment-option.active {
  background: rgba(64, 224, 208, 0.15);
  border-color: #40e0d0;
  color: #40e0d0;
}

.payment-option.active::before {
  background: linear-gradient(180deg, #00ff7f, #40e0d0);
}

.payment-option-content {
  flex: 1;
}

.payment-option-title {
  display: block;
  font-weight: 600;
  font-size: 1rem;
  margin-bottom: 0.25rem;
  color: inherit;
}

.payment-option-desc {
  display: block;
  font-size: 0.85rem;
  color: rgba(255, 255, 255, 0.6);
  line-height: 1.4;
}

.payment-option svg {
  color: inherit;
  flex-shrink: 0;
  margin-top: 0.25rem;
}

/* Payment Method Icons */
.paypal-icon {
  background: linear-gradient(135deg, #0070ba, #003087);
  color: white;
  padding: 0.5rem 0.75rem;
  border-radius: 8px;
  font-weight: 700;
  font-size: 0.9rem;
  letter-spacing: 0.5px;
  flex-shrink: 0;
}

.apple-icon {
  font-size: 1.5rem;
  flex-shrink: 0;
  margin-top: -0.25rem;
}

.google-icon {
  color: #4285f4;
}

/* Payment Forms */
.credit-card-form, 
.bank-transfer-form {
  background: rgba(255, 255, 255, 0.02);
  border-radius: 12px;
  padding: 1.5rem;
  border: 1px solid rgba(64, 224, 208, 0.1);
  margin-top: 1rem;
}

.bank-form-header {
  margin-bottom: 1.5rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid rgba(64, 224, 208, 0.2);
}

.bank-form-header h4 {
  color: rgba(255, 255, 255, 0.95);
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.bank-form-header p {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.9rem;
  margin: 0;
}

/* Payment Redirect Forms */
.paypal-form,
.googlepay-form,
.applepay-form {
  margin-top: 1rem;
}

.payment-redirect-info {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  padding: 2rem;
  background: rgba(255, 255, 255, 0.03);
  border-radius: 12px;
  border: 1px solid rgba(64, 224, 208, 0.1);
}

.redirect-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: rgba(64, 224, 208, 0.1);
  border: 2px solid rgba(64, 224, 208, 0.3);
  flex-shrink: 0;
}

.paypal-logo {
  background: linear-gradient(135deg, #0070ba, #003087);
  color: white;
  padding: 0.5rem 0.75rem;
  border-radius: 6px;
  font-weight: 700;
  font-size: 0.8rem;
  letter-spacing: 0.5px;
}

.apple-pay-logo {
  font-size: 1rem;
  font-weight: 600;
  color: #40e0d0;
}

.redirect-content h4 {
  color: rgba(255, 255, 255, 0.95);
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.redirect-content p {
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.95rem;
  margin-bottom: 0.5rem;
}

.redirect-content small {
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.85rem;
  font-style: italic;
}

/* Security & Submit */
.security-info {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  color: #00ff7f;
  font-size: 0.9rem;
  font-weight: 500;
  margin-bottom: 2rem;
  padding: 1rem;
  background: rgba(0, 255, 127, 0.05);
  border-radius: 8px;
  border: 1px solid rgba(0, 255, 127, 0.2);
}

.security-info svg {
  color: #00ff7f;
}

.submit-payment-btn {
  width: 100%;
  padding: 1.5rem 2rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  border: none;
  border-radius: 12px;
  color: white;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.75rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  box-shadow: 0 10px 30px rgba(0, 255, 127, 0.3);
}

.submit-payment-btn:hover:not(:disabled) {
  transform: translateY(-3px);
  box-shadow: 0 15px 40px rgba(0, 255, 127, 0.4);
}

.submit-payment-btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
  transform: none;
}

.processing {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.processing::after {
  content: '';
  width: 20px;
  height: 20px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-top: 2px solid white;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* Responsive Design */
@media (max-width: 1024px) {
  .payment-layout {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
  
  .order-summary {
    position: static;
    order: 2;
  }
  
  .payment-form-section {
    order: 1;
  }
}

@media (max-width: 768px) {
  .payment-container {
    padding: 1rem 0.5rem;
  }
  
  .payment-title {
    font-size: 2rem;
  }
  
  .payment-subtitle {
    font-size: 1rem;
  }
  
  .order-summary,
  .payment-form-section {
    padding: 1.5rem;
  }
  
  .form-row {
    grid-template-columns: 1fr;
    gap: 0;
  }
  
  .payment-methods {
    grid-template-columns: 1fr;
    gap: 0.75rem;
  }
  
  .payment-option {
    padding: 1rem;
  }
  
  .payment-redirect-info {
    flex-direction: column;
    text-align: center;
    gap: 1rem;
    padding: 1.5rem;
  }
  
  .submit-payment-btn {
    padding: 1.25rem 1.5rem;
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .payment-title {
    font-size: 1.5rem;
  }
  
  .order-summary,
  .payment-form-section {
    padding: 1rem;
    border-radius: 12px;
  }
  
  .plan-summary {
    flex-direction: column;
    text-align: center;
    gap: 1rem;
  }
  
  .plan-price {
    text-align: center;
  }
  
  .payment-methods {
    grid-template-columns: 1fr;
  }
  
  .payment-option {
    padding: 0.75rem;
    gap: 0.75rem;
  }
  
  .payment-option-title {
    font-size: 0.9rem;
  }
  
  .payment-option-desc {
    font-size: 0.8rem;
  }
  
  .payment-redirect-info {
    padding: 1rem;
    gap: 0.75rem;
  }
  
  .redirect-icon {
    width: 50px;
    height: 50px;
  }
}