/* ===== Modern Green, Blue, Black Theme - Global Styles ===== */
:root {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  overflow-x: hidden !important;
  
  /* Green, Blue, Black Color Palette */
  --primary: #00ff7f;
  --primary-dark: #00cc64;
  --secondary: #40e0d0;
  --secondary-dark: #1e90ff;
  --tertiary: #000000;
  --success: #32cd32;
  --success-dark: #228b22;
  --info: #1e90ff;
  --info-dark: #0f4c75;
  --dark: #1a1a2e;
  --darker: #16213e;
  
  /* Neutral Colors */
  --text-primary: #ffffff;
  --text-secondary: rgba(255, 255, 255, 0.8);
  --text-muted: rgba(255, 255, 255, 0.6);
  --background: linear-gradient(135deg, #1a1a2e 0%, #16213e 25%, #0f4c75 50%, #3282b8 75%, #0e4b4e 100%);
  --card-background: rgba(0, 0, 0, 0.7);
  --border-color: rgba(64, 224, 208, 0.2);
  
  /* Glass Effect Variables */
  --glass-bg: rgba(0, 0, 0, 0.7);
  --glass-border: 1px solid rgba(64, 224, 208, 0.2);
  --glass-shadow: 0 25px 50px rgba(0, 0, 0, 0.3);
  --glass-backdrop: blur(20px);
  
  /* Typography */
  --font-family: 'Inter', 'Segoe UI', -apple-system, BlinkMacSystemFont, sans-serif;
  --font-family-arabic: 'Tajawal', 'Noto Kufi Arabic', 'IBM Plex Sans Arabic', 'Arial', sans-serif;
  --font-weight-thin: 100;
  --font-weight-extralight: 200;
  --font-weight-light: 300;
  --font-weight-normal: 400;
  --font-weight-medium: 500;
  --font-weight-semibold: 600;
  --font-weight-bold: 700;
  --font-weight-extrabold: 800;
  --font-weight-black: 900;
  
  /* Spacing */
  --spacing-xs: 0.25rem;
  --spacing-sm: 0.5rem;
  --spacing-md: 1rem;
  --spacing-lg: 1.5rem;
  --spacing-xl: 2rem;
  --spacing-xxl: 3rem;
  
  /* Border Radius */
  --radius-sm: 8px;
  --radius-md: 12px;
  --radius-lg: 16px;
  --radius-xl: 20px;
  --radius-xxl: 24px;
  
  /* Transitions */
  --transition-fast: 0.15s ease;
  --transition-normal: 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  --transition-slow: 0.6s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Global Reset */
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* Body and HTML */
html {
  scroll-behavior: smooth;
  font-size: 16px;
}

body {
  font-family: var(--font-family);
  font-weight: var(--font-weight-normal);
  color: var(--text-primary);
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--background);
  overflow-x: hidden;
  position: relative;
  min-height: 100vh;
}

/* Arabic Font Support */
[dir="rtl"] body {
  font-family: var(--font-family-arabic);
}

[lang="ar"] {
  font-family: var(--font-family-arabic);
}

[lang="ar"] * {
  font-family: var(--font-family-arabic);
}

/* Typography */
h1, h2, h3, h4, h5, h6 {
  font-weight: var(--font-weight-bold);
  line-height: 1.2;
  margin-bottom: 0.5em;
  color: var(--text-primary);
}

h1 {
  font-size: 2.5rem;
  font-weight: var(--font-weight-extrabold);
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.5rem;
}

h4 {
  font-size: 1.25rem;
}

h5 {
  font-size: 1.125rem;
}

h6 {
  font-size: 1rem;
}

p {
  margin-bottom: 1em;
  color: var(--text-secondary);
}

/* Links */
a {
  text-decoration: none !important;
  color: var(--secondary);
  transition: var(--transition-normal);
}

a:hover {
  color: var(--primary);
}

/* Section Headings */
.section-heading {
  text-align: center;
  font-size: 2.5rem;
  font-weight: var(--font-weight-extrabold);
  background: linear-gradient(135deg, var(--primary), var(--secondary), var(--info));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 2rem;
  position: relative;
  display: inline-block;
  width: 100%;
}

.section-heading::after {
  content: '';
  display: block;
  width: 80px;
  height: 4px;
  background: linear-gradient(90deg, var(--primary), var(--secondary), var(--info));
  margin: 1rem auto 0;
  border-radius: 2px;
}

/* Utility Classes */
.text-primary {
  color: var(--text-primary);
}

.text-secondary {
  color: var(--text-secondary);
}

.text-muted {
  color: var(--text-muted);
}

.text-center {
  text-align: center;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

.font-medium {
  font-weight: var(--font-weight-medium);
}

.font-semibold {
  font-weight: var(--font-weight-semibold);
}

.font-bold {
  font-weight: var(--font-weight-bold);
}

/* ===== Comprehensive Saudi Font Weight Classes ===== */
.font-thin {
  font-weight: var(--font-weight-thin);
}

.font-extralight {
  font-weight: var(--font-weight-extralight);
}

.font-light {
  font-weight: var(--font-weight-light);
}

.font-normal {
  font-weight: var(--font-weight-normal);
}

.font-medium {
  font-weight: var(--font-weight-medium);
}

.font-semibold {
  font-weight: var(--font-weight-semibold);
}

.font-bold {
  font-weight: var(--font-weight-bold);
}

.font-extrabold {
  font-weight: var(--font-weight-extrabold);
}

.font-black {
  font-weight: var(--font-weight-black);
}

/* Arabic Typography Classes */
.arabic-text {
  font-family: var(--font-family-arabic);
  direction: rtl;
  text-align: right;
}

.arabic-light {
  font-family: var(--font-family-arabic);
  font-weight: var(--font-weight-light);
}

.arabic-medium {
  font-family: var(--font-family-arabic);
  font-weight: var(--font-weight-medium);
}

.arabic-bold {
  font-family: var(--font-family-arabic);
  font-weight: var(--font-weight-bold);
}

.arabic-extrabold {
  font-family: var(--font-family-arabic);
  font-weight: var(--font-weight-extrabold);
}

.arabic-black {
  font-family: var(--font-family-arabic);
  font-weight: var(--font-weight-black);
}

/* Enhanced Arabic Typography for Headers */
[dir="rtl"] h1, 
[dir="rtl"] h2, 
[dir="rtl"] h3, 
[dir="rtl"] h4, 
[dir="rtl"] h5, 
[dir="rtl"] h6 {
  font-family: var(--font-family-arabic);
  font-weight: var(--font-weight-bold);
}

[lang="ar"] h1, 
[lang="ar"] h2, 
[lang="ar"] h3, 
[lang="ar"] h4, 
[lang="ar"] h5, 
[lang="ar"] h6 {
  font-family: var(--font-family-arabic);
  font-weight: var(--font-weight-bold);
}

/* Enhanced Arabic Section Headings */
[dir="rtl"] .section-heading {
  font-family: var(--font-family-arabic);
  font-weight: var(--font-weight-extrabold);
}

[lang="ar"] .section-heading {
  font-family: var(--font-family-arabic);
  font-weight: var(--font-weight-extrabold);
}

/* ===== Leaf Background Integration ===== */
.app-content {
  position: relative;
  z-index: 1;
  min-height: 100vh;
}

/* Enhanced content readability with leaf background */
.glass-container {
  background: var(--glass-bg);
  backdrop-filter: var(--glass-backdrop);
  border: var(--glass-border);
  box-shadow: var(--glass-shadow);
  border-radius: var(--radius-lg);
  position: relative;
  z-index: 2;
}

/* Ensure text remains readable over leaf background */
.content-section {
  position: relative;
  z-index: 2;
  background: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(10px);
  border-radius: var(--radius-md);
  padding: var(--spacing-lg);
  margin: var(--spacing-md) 0;
}

/* Card overlays for better readability */
.card-overlay {
  position: relative;
  z-index: 3;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(15px);
  border: 1px solid rgba(64, 224, 208, 0.3);
  border-radius: var(--radius-lg);
  padding: var(--spacing-lg);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.4);
}

/* Navigation and header enhancement */
.nav-enhanced {
  background: rgba(0, 0, 0, 0.9);
  backdrop-filter: blur(25px);
  border-bottom: 1px solid rgba(64, 224, 208, 0.3);
  z-index: 1000;
}

/* Footer enhancement */
.footer-enhanced {
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(20px);
  border-top: 1px solid rgba(64, 224, 208, 0.2);
  z-index: 3;
}

/* Glass Effect Utility */
.glass {
  background: var(--glass-bg);
  backdrop-filter: var(--glass-backdrop);
  border: var(--glass-border);
  box-shadow: var(--glass-shadow);
}

/* Spacing Utilities */
.m-0 { margin: 0; }
.m-1 { margin: var(--spacing-xs); }
.m-2 { margin: var(--spacing-sm); }
.m-3 { margin: var(--spacing-md); }
.m-4 { margin: var(--spacing-lg); }
.m-5 { margin: var(--spacing-xl); }

.p-0 { padding: 0; }
.p-1 { padding: var(--spacing-xs); }
.p-2 { padding: var(--spacing-sm); }
.p-3 { padding: var(--spacing-md); }
.p-4 { padding: var(--spacing-lg); }
.p-5 { padding: var(--spacing-xl); }

/* Flexbox Utilities */
.d-flex {
  display: flex;
}

.flex-column {
  flex-direction: column;
}

.flex-row {
  flex-direction: row;
}

.justify-center {
  justify-content: center;
}

.justify-between {
  justify-content: space-between;
}

.justify-end {
  justify-content: flex-end;
}

.align-center {
  align-items: center;
}

.align-start {
  align-items: flex-start;
}

.align-end {
  align-items: flex-end;
}

/* Grid Utilities */
.grid {
  display: grid;
}

.grid-cols-1 {
  grid-template-columns: repeat(1, minmax(0, 1fr));
}

.grid-cols-2 {
  grid-template-columns: repeat(2, minmax(0, 1fr));
}

.grid-cols-3 {
  grid-template-columns: repeat(3, minmax(0, 1fr));
}

.grid-cols-4 {
  grid-template-columns: repeat(4, minmax(0, 1fr));
}

.gap-1 { gap: var(--spacing-xs); }
.gap-2 { gap: var(--spacing-sm); }
.gap-3 { gap: var(--spacing-md); }
.gap-4 { gap: var(--spacing-lg); }
.gap-5 { gap: var(--spacing-xl); }

/* Border Radius Utilities */
.rounded-sm { border-radius: var(--radius-sm); }
.rounded-md { border-radius: var(--radius-md); }
.rounded-lg { border-radius: var(--radius-lg); }
.rounded-xl { border-radius: var(--radius-xl); }
.rounded-xxl { border-radius: var(--radius-xxl); }
.rounded-full { border-radius: 9999px; }

/* Shadow Utilities */
.shadow-sm {
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
}

.shadow {
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}

.shadow-md {
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

.shadow-lg {
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}

.shadow-xl {
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
}

/* Animation Utilities */
.transition {
  transition: var(--transition-normal);
}

.transition-fast {
  transition: var(--transition-fast);
}

.transition-slow {
  transition: var(--transition-slow);
}

/* Hover Effects */
.hover-lift:hover {
  transform: translateY(-2px);
}

.hover-scale:hover {
  transform: scale(1.05);
}

.hover-glow:hover {
  box-shadow: 0 0 20px rgba(255, 107, 107, 0.3);
}

/* Focus Styles */
.focus-ring:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(255, 107, 107, 0.1);
}

/* Scrollbar Styling */
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: rgba(255, 107, 107, 0.3);
  border-radius: 3px;
}

::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 107, 107, 0.5);
}

/* Responsive Design */
@media (max-width: 768px) {
  .section-heading {
    font-size: 2rem;
  }
  
  h1 {
    font-size: 2rem;
  }
  
  h2 {
    font-size: 1.75rem;
  }
  
  h3 {
    font-size: 1.5rem;
  }
}

@media (max-width: 480px) {
  .section-heading {
    font-size: 1.75rem;
  }
  
  h1 {
    font-size: 1.75rem;
  }
  
  h2 {
    font-size: 1.5rem;
  }
}

/* Print Styles */
@media print {
  * {
    background: transparent !important;
    color: black !important;
    box-shadow: none !important;
  }
}

/* Reduced Motion */
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}

