/* ===== Modern Public Navbar with Dark Green/Blue/Black Theme ===== */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Body padding for fixed navbar */
body {
  /* padding-top: 80px; */
}

@media (max-width: 768px) {
  body {
    /* padding-top: 70px; */
  }
}

@media (max-width: 480px) {
  body {
    /* padding-top: 65px; */
  }
}

.navbar {
  width: 100%;
  padding: 1rem 2rem;
  background: rgba(0, 0, 0, 0.85);
  backdrop-filter: blur(20px);
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  border-bottom: 1px solid rgba(64, 224, 208, 0.2);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.navbar.scrolled {
  background: rgba(0, 0, 0, 0.95);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
  border-bottom-color: rgba(0, 255, 127, 0.3);
}

/* Logo Section */
.navbar-brand {
  flex: 1;
}

.logo-link {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  text-decoration: none;
  transition: all 0.3s ease;
}

.logo-link:hover {
  transform: translateX(3px);
}

.logo-icon {
  color: #00ff7f;
  transition: all 0.3s ease;
}

.logo-link:hover .logo-icon {
  transform: rotate(10deg) scale(1.1);
  color: #40e0d0;
}

.logo-text {
  font-weight: 800;
  font-size: 1.5rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* Desktop Navigation */
.nav-center {
  flex: 2;
  display: flex;
  justify-content: center;
}

.nav-links {
  list-style: none;
  display: flex;
  gap: 2rem;
  align-items: center;
}

.nav-link {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  text-decoration: none;
  color: rgba(255, 255, 255, 0.8);
  font-weight: 500;
  padding: 0.75rem 1rem;
  border-radius: 12px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
}

.nav-link::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0;
  height: 2px;
  background: linear-gradient(90deg, #00ff7f, #40e0d0);
  transition: all 0.3s ease;
  transform: translateX(-50%);
}

.nav-link:hover {
  color: #40e0d0;
  background: rgba(64, 224, 208, 0.1);
  transform: translateY(-2px);
}

.nav-link:hover::before {
  width: 80%;
}

/* Right Actions */
.nav-actions {
  flex: 1;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 1rem;
}

/* Language Toggle Button */
.language-toggle-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: rgba(30, 144, 255, 0.1);
  border: 1px solid rgba(30, 144, 255, 0.2);
  border-radius: 10px;
  padding: 0.5rem 0.75rem;
  color: rgba(255, 255, 255, 0.9);
  font-weight: 600;
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.language-toggle-btn:hover {
  background: rgba(30, 144, 255, 0.15);
  border-color: rgba(30, 144, 255, 0.3);
  transform: scale(1.05);
}

.language-toggle-btn svg {
  color: #1e90ff;
  transition: all 0.3s ease;
}

.language-toggle-btn:hover svg {
  transform: rotate(180deg);
  color: #40e0d0;
}

/* User Dropdown */
.user-dropdown {
  position: relative;
  display: inline-block;
}

.user-dropdown-menu {
  position: absolute;
  top: 100%;
  right: 0;
  background: rgba(0, 0, 0, 0.95);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 12px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
  min-width: 200px;
  margin-top: 0.5rem;
  z-index: 1001;
  overflow: hidden;
}

.user-dropdown-item {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1rem 1.25rem;
  text-decoration: none;
  color: rgba(255, 255, 255, 0.8);
  font-weight: 500;
  transition: all 0.3s ease;
  width: 100%;
  border: none;
  background: transparent;
  cursor: pointer;
  text-align: left;
}

.user-dropdown-item:hover {
  background: rgba(64, 224, 208, 0.1);
  color: #40e0d0;
  transform: translateX(3px);
}

.user-dropdown-item.logout-item:hover {
  background: rgba(255, 77, 87, 0.1);
  color: #ff4d57;
}

.user-dropdown-item svg {
  min-width: 16px;
}

/* Profile Button */
.profile-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 42px;
  height: 42px;
  background: rgba(64, 224, 208, 0.1);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 50%;
  color: #40e0d0;
  cursor: pointer;
  transition: all 0.3s ease;
}

.profile-btn:hover {
  background: rgba(64, 224, 208, 0.2);
  border-color: #40e0d0;
  transform: scale(1.1);
}

/* Mobile Menu Button */
.mobile-menu-btn {
  display: none;
  align-items: center;
  justify-content: center;
  width: 42px;
  height: 42px;
  background: rgba(64, 224, 208, 0.1);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 10px;
  color: #40e0d0;
  cursor: pointer;
  transition: all 0.3s ease;
}

.mobile-menu-btn:hover {
  background: rgba(64, 224, 208, 0.2);
  border-color: #40e0d0;
  transform: scale(1.05);
}

/* ===== Mobile Navigation ===== */
.mobile-nav-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(4px);
  z-index: 999;
}

.mobile-nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 320px;
  height: 100%;
  background: rgba(0, 0, 0, 0.95);
  backdrop-filter: blur(30px);
  border-right: 1px solid rgba(64, 224, 208, 0.2);
  z-index: 1000;
  display: flex;
  flex-direction: column;
  box-shadow: 4px 0 25px rgba(0, 0, 0, 0.5);
}

.mobile-nav-header {
  padding: 1.5rem;
  border-bottom: 1px solid rgba(64, 224, 208, 0.2);
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(135deg, rgba(0, 255, 127, 0.05), rgba(30, 144, 255, 0.05));
}

.mobile-logo {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  color: #00ff7f;
  font-weight: 700;
  font-size: 1.2rem;
}

.mobile-close-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 36px;
  height: 36px;
  background: rgba(255, 77, 87, 0.1);
  border: 1px solid rgba(255, 77, 87, 0.2);
  border-radius: 8px;
  color: #ff4d57;
  cursor: pointer;
  transition: all 0.3s ease;
}

.mobile-close-btn:hover {
  background: rgba(255, 77, 87, 0.2);
  transform: scale(1.1);
}

.mobile-nav-content {
  flex: 1;
  padding: 1.5rem 0;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.mobile-nav-links {
  list-style: none;
  padding: 0;
  margin: 0;
}

.mobile-nav-link {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem 1.5rem;
  text-decoration: none;
  color: rgba(255, 255, 255, 0.8);
  font-weight: 500;
  font-size: 1.1rem;
  transition: all 0.3s ease;
  border-left: 3px solid transparent;
}

.mobile-nav-link:hover {
  color: #40e0d0;
  background: rgba(64, 224, 208, 0.1);
  border-left-color: #40e0d0;
  transform: translateX(5px);
}

.mobile-nav-actions {
  padding: 0 1.5rem;
  border-top: 1px solid rgba(64, 224, 208, 0.2);
  padding-top: 1.5rem;
}

.mobile-language-toggle {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  background: rgba(30, 144, 255, 0.1);
  border: 1px solid rgba(30, 144, 255, 0.2);
  border-radius: 12px;
  padding: 1rem;
  margin-bottom: 1rem;
  color: rgba(255, 255, 255, 0.9);
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  width: 100%;
  justify-content: center;
}

.mobile-language-toggle:hover {
  background: rgba(30, 144, 255, 0.15);
  border-color: rgba(30, 144, 255, 0.3);
  transform: scale(1.02);
}

.mobile-language-toggle svg {
  color: #1e90ff;
  transition: all 0.3s ease;
}

.mobile-language-toggle:hover svg {
  transform: rotate(180deg);
  color: #40e0d0;
}

.mobile-auth-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.75rem;
  padding: 1rem;
  border: none;
  border-radius: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 1rem;
  width: 100%;
}

.mobile-auth-btn.login-btn {
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
}

.mobile-auth-btn.logout-btn {
  background: rgba(255, 77, 87, 0.1);
  color: #ff4d57;
  border: 1px solid rgba(255, 77, 87, 0.2);
}

/* ===== Responsive Design ===== */

/* Hide mobile elements on desktop */
@media (min-width: 769px) {
  .mobile-menu-btn {
    display: none;
  }
  
  .mobile-nav,
  .mobile-nav-backdrop {
    display: none !important;
  }
}

/* Tablet and small desktop adjustments */
@media (max-width: 1024px) {
  .nav-links {
    gap: 1.5rem;
  }
  
  .nav-link {
    padding: 0.5rem 0.75rem;
    font-size: 0.9rem;
  }
  
  .navbar {
    padding: 1rem 1.5rem;
  }
}

/* Mobile and tablet */
@media (max-width: 768px) {
  .nav-center {
    display: none;
  }
  
  .mobile-menu-btn {
    display: flex;
  }
  
  .nav-actions {
    gap: 0.75rem;
  }
  
  /* Auth functionality handled by profile-btn */
  
  .language-toggle-btn {
    padding: 0.5rem;
    min-width: auto;
  }
  
  .language-toggle-btn span {
    display: none;
  }
  
  .logo-text {
    font-size: 1.3rem;
  }
  
  .mobile-nav {
    width: 280px;
  }
}

/* Small mobile devices */
@media (max-width: 480px) {
  .navbar {
    padding: 0.75rem 1rem;
  }
  
  .nav-actions {
    gap: 0.5rem;
  }
  
  .logo-text {
    font-size: 1.1rem;
  }
  
  .logo-icon {
    width: 24px;
    height: 24px;
  }
  
  .auth-btn,
  .profile-btn,
  .mobile-menu-btn {
    width: 36px;
    height: 36px;
    padding: 0.5rem;
  }
  
  .language-switcher {
    padding: 0.375rem 0.5rem;
  }
  
  .mobile-nav {
    width: 260px;
  }
  
  .mobile-nav-header {
    padding: 1rem;
  }
  
  .mobile-nav-content {
    padding: 1rem 0;
  }
  
  .mobile-nav-link {
    padding: 0.875rem 1rem;
    font-size: 1rem;
  }
  
  .mobile-nav-actions {
    padding: 0 1rem;
  }
}

/* Extra small devices */
@media (max-width: 360px) {
  .navbar {
    padding: 0.75rem 0.75rem;
  }
  
  .logo-text {
    font-size: 1rem;
  }
  
  .mobile-nav {
    width: 240px;
  }
}

/* High DPI displays */
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .navbar {
    border-bottom-width: 0.5px;
  }
}

/* Landscape mobile devices */
@media (max-width: 768px) and (orientation: landscape) {
  .navbar {
    padding: 0.5rem 1rem;
  }
  
  .mobile-nav {
    width: 320px;
  }
  
  .mobile-nav-header {
    padding: 1rem;
  }
}

/* Dark mode preference support */
@media (prefers-color-scheme: dark) {
  .navbar {
    background: rgba(0, 0, 0, 0.9);
  }
  
  .navbar.scrolled {
    background: rgba(0, 0, 0, 0.98);
  }
}

/* Reduced motion preference */
@media (prefers-reduced-motion: reduce) {
  .nav-link,
  .auth-btn,
  .profile-btn,
  .mobile-menu-btn,
  .mobile-nav-link,
  .logo-link {
    transition: none;
  }
  
  .logo-link:hover .logo-icon {
    transform: none;
  }
}

/* Print styles */
@media print {
  .navbar {
    display: none;
  }
}