/* ===== Modern Dashboard with Green, Blue, Black Theme ===== */
#dashboard-root {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 2rem;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 25%, #0f4c75 50%, #3282b8 75%, #0e4b4e 100%);
  min-height: 100vh;
  position: relative;
}

#dashboard-root::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 20% 20%, rgba(0, 255, 127, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 80% 80%, rgba(64, 224, 208, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 50% 50%, rgba(30, 144, 255, 0.05) 0%, transparent 50%);
  pointer-events: none;
}

/* ===== Welcome Card ===== */
.welcome-card {
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border-radius: 24px;
  padding: 2.5rem;
  box-shadow: 
    0 25px 50px rgba(0, 0, 0, 0.3),
    0 0 0 1px rgba(64, 224, 208, 0.2),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(64, 224, 208, 0.2);
  position: relative;
  z-index: 1;
  overflow: hidden;
}

.welcome-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #00ff7f, #40e0d0, #1e90ff);
  border-radius: 24px 24px 0 0;
}

.welcome-title {
  font-size: 2.5rem;
  font-weight: 800;
  margin-bottom: 0.5rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0, #1e90ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.welcome-subtitle {
  color: #40e0d0;
  font-size: 1.1rem;
  font-weight: 500;
  margin: 0;
}

/* ===== Quick Actions ===== */
.quick-actions {
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(64, 224, 208, 0.2);
  position: relative;
  z-index: 1;
}

.actions-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
}

.action-button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.75rem;
  padding: 1rem 1.5rem;
  border: none;
  border-radius: 16px;
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
  background: rgba(64, 224, 208, 0.1);
  color: #40e0d0;
  border: 2px solid rgba(64, 224, 208, 0.2);
}

.action-button.primary {
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: #000000;
  border: none;
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.3);
}

.action-button:hover {
  transform: translateY(-3px);
  box-shadow: 0 12px 30px rgba(64, 224, 208, 0.2);
}

.action-button.primary:hover {
  box-shadow: 0 15px 40px rgba(0, 255, 127, 0.4);
  transform: translateY(-3px) scale(1.02);
}

.action-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.6s;
}

.action-button:hover::before {
  left: 100%;
}

/* ===== Stats Grid ===== */
.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1.5rem;
}

.stat-card {
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(64, 224, 208, 0.2);
  position: relative;
  z-index: 1;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  overflow: hidden;
}

.stat-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 35px 70px rgba(0, 0, 0, 0.4);
  border-color: #00ff7f;
}

.stat-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.stat-title {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.7);
  font-weight: 600;
  margin-bottom: 0.5rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.stat-value {
  font-size: 2.5rem;
  font-weight: 800;
  color: #ffffff;
  line-height: 1;
}

.stat-icon {
  width: 60px;
  height: 60px;
  border-radius: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
}

.stat-icon::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 16px;
  opacity: 0.8;
}

.stat-icon.blue::before {
  background: linear-gradient(135deg, #1e90ff, #0f4c75);
}

.stat-icon.green::before {
  background: linear-gradient(135deg, #00ff7f, #32cd32);
}

.stat-icon.purple::before {
  background: linear-gradient(135deg, #40e0d0, #1e90ff);
}

.stat-icon.pink::before {
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
}

/* ===== Recent Activity ===== */
.recent-activity {
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(64, 224, 208, 0.2);
  position: relative;
  z-index: 1;
}

.activity-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem 0;
  border-bottom: 1px solid rgba(64, 224, 208, 0.2);
  transition: all 0.3s ease;
}

.activity-item:hover {
  background: rgba(0, 255, 127, 0.05);
  border-radius: 12px;
  padding-left: 1rem;
  padding-right: 1rem;
}

.activity-item:last-child {
  border-bottom: none;
}

.activity-icon {
  width: 40px;
  height: 40px;
  border-radius: 12px;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  display: flex;
  align-items: center;
  justify-content: center;
  color: #000000;
  flex-shrink: 0;
}

.activity-title {
  font-weight: 600;
  color: #ffffff;
  margin: 0;
}

.activity-meta {
  font-size: 0.85rem;
  color: rgba(255, 255, 255, 0.7);
  font-weight: 500;
}

/* ===== Dashboard Container ===== */
.dashboard-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 25%, #0f4c75 50%, #3282b8 75%, #0e4b4e 100%);
}

/* ===== Responsive Design ===== */
@media (max-width: 768px) {
  #dashboard-root {
    padding: 1rem;
    gap: 1.5rem;
  }
  
  .welcome-card {
    padding: 1.5rem;
  }
  
  .welcome-title {
    font-size: 2rem;
  }
  
  .stats-grid {
    grid-template-columns: 1fr;
  }
  
  .actions-grid {
    grid-template-columns: 1fr;
  }
  
  .sidebar {
    position: fixed;
    top: 0;
    left: -256px;
    width: 256px;
    transition: left 0.3s;
    z-index: 100;
    box-shadow: 2px 0 8px rgba(0,0,0,0.1);
  }
  
  .sidebar.open {
    left: 0;
  }
}

@media (max-width: 480px) {
  .welcome-title {
    font-size: 1.75rem;
  }
  
  .stat-value {
    font-size: 2rem;
  }
  
  .stat-card,
  .quick-actions,
  .recent-activity {
    padding: 1.5rem;
  }
}

/* ===== Utility Classes ===== */
.text-muted {
  color: #636e72;
}

.search {
  margin-bottom: 0 !important;
}