/* ===== Member Form Component Styling (AddMemberForm and PersonEditForm) ===== */
.member-form-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10001; /* Increased z-index to ensure it appears above all other elements */
  padding: 1rem;
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.member-form-modal {
  background: rgba(0, 0, 0, 0.9);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(64, 224, 208, 0.3);
  border-radius: 24px;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.7);
  width: 100%;
  max-width: 600px;
  max-height: 90vh;
  overflow-y: auto;
  animation: slideUp 0.3s ease;
}

@keyframes slideUp {
  from {
    transform: translateY(30px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

/* Header */
.member-form-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2rem 2rem 1rem 2rem;
  border-bottom: 1px solid rgba(64, 224, 208, 0.2);
}

.header-content {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.header-content h2 {
  color: rgba(255, 255, 255, 0.95);
  font-size: 1.5rem;
  margin: 0;
}

.close-btn {
  background: rgba(255, 69, 58, 0.2);
  border: 1px solid rgba(255, 69, 58, 0.3);
  color: #ff453a;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}

.close-btn:hover:not(:disabled) {
  background: rgba(255, 69, 58, 0.3);
  transform: scale(1.05);
}

.close-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Form */
.member-form {
  padding: 2rem;
}

.form-section {
  margin-bottom: 2rem;
  background: rgba(255, 255, 255, 0.02);
  border: 1px solid rgba(64, 224, 208, 0.1);
  border-radius: 16px;
  padding: 1.5rem;
}

.form-section h3 {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  color: rgba(255, 255, 255, 0.9);
  font-size: 1.1rem;
  margin-bottom: 1.5rem;
  padding-bottom: 0.75rem;
  border-bottom: 1px solid rgba(64, 224, 208, 0.2);
}

.form-section h3 svg {
  color: #40e0d0;
}

.optional-text {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.9rem;
  font-weight: normal;
  margin-left: 0.5rem;
}

/* Form Groups */
.form-group {
  margin-bottom: 1.5rem;
}

.form-group:last-child {
  margin-bottom: 0;
}

.form-group label {
  display: block;
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.95rem;
  font-weight: 500;
  margin-bottom: 0.5rem;
}

.form-input,
.form-textarea,
.form-select {
  width: 100%;
  padding: 0.75rem 1rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 12px;
  color: rgba(255, 255, 255, 0.9);
  font-size: 1rem;
  transition: all 0.3s ease;
  box-sizing: border-box;
}

.form-input:focus,
.form-textarea:focus,
.form-select:focus {
  outline: none;
  border-color: #40e0d0;
  box-shadow: 0 0 15px rgba(64, 224, 208, 0.2);
}

.form-input::placeholder,
.form-textarea::placeholder {
  color: rgba(255, 255, 255, 0.4);
}

.form-textarea {
  resize: vertical;
  min-height: 80px;
}

.form-input.error,
.form-textarea.error,
.form-select.error {
  border-color: #ff453a;
  box-shadow: 0 0 15px rgba(255, 69, 58, 0.2);
}

.form-input:disabled,
.form-textarea:disabled,
.form-select:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* Form Row */
.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
}

.form-row .form-group {
  margin-bottom: 0;
}

/* Checkbox Group */
.checkbox-group {
  display: flex;
  align-items: center;
  height: 100%;
}

.checkbox-label {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.95rem;
  cursor: pointer;
}

/* Toggle Label */
.toggle-label {
  display: flex;
  align-items: center;
  gap: 1rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 12px;
  padding: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.toggle-label:hover {
  background: rgba(64, 224, 208, 0.05);
  border-color: #40e0d0;
}

.toggle-slider {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 24px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 24px;
  transition: all 0.3s ease;
}

.toggle-label input:checked + .toggle-slider {
  background: #40e0d0;
}

.toggle-slider::before {
  content: '';
  position: absolute;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: white;
  top: 2px;
  left: 2px;
  transition: all 0.3s ease;
}

.toggle-label input:checked + .toggle-slider::before {
  transform: translateX(26px);
}

.toggle-content {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  flex: 1;
}

.toggle-icon {
  color: #40e0d0;
}

.toggle-text .toggle-title {
  display: block;
  color: rgba(255, 255, 255, 0.9);
  font-weight: 500;
  font-size: 0.95rem;
}

.toggle-text .toggle-description {
  display: block;
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.85rem;
  margin-top: 0.25rem;
}

/* Image Upload */
.image-upload-container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.image-preview {
  width: 100%;
  height: 200px;
  border-radius: 12px;
  overflow: hidden;
  background: rgba(255, 255, 255, 0.05);
  border: 1px dashed rgba(64, 224, 208, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
}

.image-preview img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.image-placeholder {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  color: rgba(255, 255, 255, 0.5);
}

.image-placeholder svg {
  opacity: 0.5;
}

.image-upload-controls {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

.upload-button {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: rgba(64, 224, 208, 0.15);
  color: #40e0d0;
  border: 1px solid rgba(64, 224, 208, 0.3);
  padding: 0.75rem 1.25rem;
  border-radius: 12px;
  font-size: 0.9rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
}

.upload-button:hover {
  background: rgba(64, 224, 208, 0.25);
  transform: translateY(-2px);
}

.remove-image-btn,
.save-image-btn {
  background: rgba(255, 69, 58, 0.15);
  color: #ff453a;
  border: 1px solid rgba(255, 69, 58, 0.3);
  padding: 0.75rem 1.25rem;
  border-radius: 12px;
  font-size: 0.9rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
}

.remove-image-btn:hover,
.save-image-btn:hover {
  background: rgba(255, 69, 58, 0.25);
  transform: translateY(-2px);
}

.save-image-btn {
  background: rgba(0, 255, 127, 0.15);
  color: #00ff7f;
  border: 1px solid rgba(0, 255, 127, 0.3);
}

.save-image-btn:hover {
  background: rgba(0, 255, 127, 0.25);
}

/* Error Messages */
.error-message {
  display: block;
  color: #ff453a;
  font-size: 0.85rem;
  margin-top: 0.5rem;
  font-weight: 500;
}

.submit-error {
  background: rgba(255, 69, 58, 0.1);
  border: 1px solid rgba(255, 69, 58, 0.2);
  border-radius: 8px;
  padding: 1rem;
  margin-bottom: 1rem;
}

/* Form Actions */
.form-actions {
  display: flex;
  gap: 1rem;
  justify-content: flex-end;
  margin-top: 2rem;
  padding-top: 1.5rem;
  border-top: 1px solid rgba(64, 224, 208, 0.1);
}

.cancel-btn {
  background: rgba(255, 255, 255, 0.05);
  color: rgba(255, 255, 255, 0.8);
  border: 1px solid rgba(255, 255, 255, 0.2);
  padding: 0.75rem 1.5rem;
  border-radius: 12px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
}

.cancel-btn:hover {
  background: rgba(255, 255, 255, 0.1);
  transform: translateY(-2px);
}

.create-btn,
.update-btn,
.save-btn,
.delete-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  border-radius: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  border: none;
}

.create-btn,
.update-btn,
.save-btn {
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
}

.create-btn:hover:not(:disabled),
.update-btn:hover:not(:disabled),
.save-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.4);
}

.delete-btn {
  background: rgba(255, 69, 58, 0.2);
  color: #ff453a;
  border: 1px solid rgba(255, 69, 58, 0.3) !important;
}

.delete-btn:hover {
  background: rgba(255, 69, 58, 0.3);
  transform: translateY(-2px);
}

.create-btn:disabled,
.update-btn:disabled,
.save-btn:disabled,
.delete-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

/* Spinner for loading states */
.spinner {
  width: 16px;
  height: 16px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top-color: white;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .member-form-modal {
    margin: 1rem;
    max-width: calc(100% - 2rem);
  }
  
  .member-form-header {
    padding: 1.5rem 1.5rem 1rem 1.5rem;
  }
  
  .member-form {
    padding: 1.5rem;
  }
  
  .form-section {
    padding: 1.25rem;
  }
  
  .form-row {
    grid-template-columns: 1fr;
    gap: 0;
  }
  
  .image-upload-controls {
    flex-direction: column;
  }
  
  .form-actions {
    flex-direction: column;
  }
  
  .cancel-btn,
  .create-btn,
  .update-btn,
  .save-btn,
  .delete-btn {
    width: 100%;
    justify-content: center;
  }
}

@media (max-width: 480px) {
  .member-form-modal {
    margin: 0.5rem;
    max-width: calc(100% - 1rem);
  }
  
  .member-form-header,
  .member-form {
    padding: 1rem;
  }
  
  .form-section {
    padding: 1rem;
  }
}