/* ===== User Settings Page Styling ===== */
.user-settings-page {
  min-height: 100vh;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 25%, #0f4c75 50%, #3282b8 75%, #0e4b4e 100%);
  padding: 2rem 0;
}

.settings-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
}

/* ===== Header ===== */
.settings-header {
  text-align: center;
  margin-bottom: 3rem;
}

.settings-header h1 {
  font-size: 2.5rem;
  font-weight: 800;
  margin-bottom: 1rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.settings-header p {
  font-size: 1.1rem;
  color: rgba(255, 255, 255, 0.8);
  max-width: 600px;
  margin: 0 auto;
}

/* ===== Content Layout ===== */
.settings-content {
  display: grid;
  grid-template-columns: 280px 1fr;
  gap: 2rem;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 20px;
  overflow: hidden;
}

/* ===== Sidebar Navigation ===== */
.settings-sidebar {
  background: rgba(0, 0, 0, 0.8);
  border-right: 1px solid rgba(64, 224, 208, 0.2);
  padding: 2rem 0;
}

.settings-nav {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.nav-tab {
  display: flex;
  align-items: center;
  gap: 1rem;
  width: 100%;
  padding: 1rem 2rem;
  background: transparent;
  border: none;
  color: rgba(255, 255, 255, 0.7);
  font-size: 1rem;
  text-align: left;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
}

.nav-tab::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 4px;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  transform: scaleY(0);
  transition: transform 0.3s ease;
}

.nav-tab:hover,
.nav-tab.active {
  color: rgba(255, 255, 255, 0.95);
  background: rgba(64, 224, 208, 0.1);
}

.nav-tab.active::before {
  transform: scaleY(1);
}

.nav-tab svg {
  transition: all 0.3s ease;
}

.nav-tab:hover svg,
.nav-tab.active svg {
  color: #40e0d0;
}

/* ===== Main Content ===== */
.settings-main {
  padding: 2rem;
  overflow-y: auto;
  max-height: 80vh;
}

/* ===== Messages ===== */
.message {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem 1.5rem;
  border-radius: 12px;
  margin-bottom: 2rem;
  position: relative;
}

.message button {
  background: none;
  border: none;
  color: inherit;
  cursor: pointer;
  margin-left: auto;
  opacity: 0.7;
  transition: opacity 0.3s ease;
}

.message button:hover {
  opacity: 1;
}

.success-message {
  background: rgba(0, 255, 127, 0.1);
  border: 1px solid rgba(0, 255, 127, 0.3);
  color: #00ff7f;
}

.error-message {
  background: rgba(255, 69, 58, 0.1);
  border: 1px solid rgba(255, 69, 58, 0.3);
  color: #ff453a;
}

/* ===== Section Styling ===== */
.settings-section {
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

.section-header {
  margin-bottom: 2rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid rgba(64, 224, 208, 0.2);
}

.section-header h2 {
  font-size: 1.8rem;
  font-weight: 700;
  color: rgba(255, 255, 255, 0.95);
  margin-bottom: 0.5rem;
}

.section-header p {
  color: rgba(255, 255, 255, 0.7);
  font-size: 1rem;
}

/* ===== Profile Section ===== */
.profile-picture-section {
  margin-bottom: 2rem;
  text-align: center;
}

.picture-container {
  display: inline-block;
}

.profile-avatar {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  overflow: hidden;
  border: 3px solid rgba(64, 224, 208, 0.3);
  margin-bottom: 0.75rem;
  position: relative;
}

.profile-avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.avatar-placeholder {
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

.avatar-placeholder svg {
  width: 28px;
  height: 28px;
}

.picture-actions {
  display: flex;
  gap: 0.75rem;
  justify-content: center;
}

.upload-btn,
.remove-btn {
  display: flex;
  align-items: center;
  gap: 0.4rem;
  padding: 0.4rem 0.75rem;
  border-radius: 8px;
  font-size: 0.8rem;
  cursor: pointer;
  transition: all 0.3s ease;
  border: none;
}

.upload-btn svg,
.remove-btn svg {
  width: 14px;
  height: 14px;
}

.upload-btn {
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
}

.upload-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.3);
}

.remove-btn {
  background: rgba(255, 69, 58, 0.1);
  color: #ff453a;
  border: 1px solid rgba(255, 69, 58, 0.3);
}

.remove-btn:hover {
  background: rgba(255, 69, 58, 0.2);
}

/* ===== Form Styling ===== */
.form-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.form-group {
  display: flex;
  flex-direction: column;
}

.form-group.full-width {
  grid-column: 1 / -1;
}

.form-group label {
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.9rem;
  font-weight: 500;
  margin-bottom: 0.5rem;
}

.form-group input,
.form-group textarea,
.form-group select {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 8px;
  color: rgba(255, 255, 255, 0.9);
  font-size: 1rem;
  padding: 0.75rem;
  transition: all 0.3s ease;
}

.form-group input:focus,
.form-group textarea:focus,
.form-group select:focus {
  outline: none;
  border-color: #40e0d0;
  box-shadow: 0 0 20px rgba(64, 224, 208, 0.2);
}

.form-actions {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
  padding-top: 2rem;
  border-top: 1px solid rgba(64, 224, 208, 0.2);
}

.save-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
  border: none;
  padding: 0.875rem 2rem;
  border-radius: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.save-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.4);
}

.save-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* ===== Plan Section ===== */
.plan-overview {
  max-width: 600px;
}

.plan-card {
  background: rgba(0, 0, 0, 0.6);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 16px;
  padding: 2rem;
  position: relative;
  overflow: hidden;
}

.plan-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #ffd700, #ffed4e);
}

.plan-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 2rem;
}

.plan-info h3 {
  font-size: 1.5rem;
  font-weight: 700;
  color: rgba(255, 255, 255, 0.95);
  margin-bottom: 0.5rem;
}

.plan-price {
  font-size: 1.2rem;
  font-weight: 600;
  color: #00ff7f;
}

.plan-status {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-size: 0.9rem;
  font-weight: 600;
}

.plan-status.active {
  background: rgba(0, 255, 127, 0.1);
  color: #00ff7f;
  border: 1px solid rgba(0, 255, 127, 0.3);
}

.plan-features {
  margin-bottom: 2rem;
}

.plan-features h4 {
  color: rgba(255, 255, 255, 0.9);
  margin-bottom: 1rem;
  font-size: 1.1rem;
}

.plan-features ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.plan-features li {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 0.75rem;
}

.plan-features li svg {
  color: #00ff7f;
}

.plan-usage {
  margin-bottom: 2rem;
}

.plan-usage h4 {
  color: rgba(255, 255, 255, 0.9);
  margin-bottom: 1rem;
  font-size: 1.1rem;
}

.usage-item {
  margin-bottom: 1rem;
}

.usage-info {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5rem;
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.9rem;
}

.usage-bar {
  height: 8px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 4px;
  overflow: hidden;
}

.usage-fill {
  height: 100%;
  background: linear-gradient(90deg, #00ff7f, #40e0d0);
  transition: width 0.3s ease;
}

.plan-actions {
  display: flex;
  gap: 1rem;
}

.upgrade-btn,
.manage-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  border-radius: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  border: none;
}

.upgrade-btn {
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
  flex: 1;
}

.upgrade-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.4);
}

.manage-btn {
  background: rgba(64, 224, 208, 0.1);
  color: #40e0d0;
  border: 1px solid rgba(64, 224, 208, 0.3);
}

.manage-btn:hover {
  background: rgba(64, 224, 208, 0.2);
}

/* ===== Security Section ===== */
.security-card {
  background: rgba(0, 0, 0, 0.6);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 16px;
  padding: 1.5rem;
  max-width: 600px;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid rgba(64, 224, 208, 0.2);
}

.card-header h3 {
  color: rgba(255, 255, 255, 0.9);
  font-size: 1.2rem;
}

.toggle-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.toggle-btn:hover {
  transform: translateY(-2px);
}

.error-text {
  color: #ff453a;
  font-size: 0.9rem;
  margin-bottom: 1rem;
}

/* ===== Notifications Section ===== */
.notifications-content,
.preferences-content {
  max-width: 600px;
}

.notification-item,
.preference-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
  background: rgba(0, 0, 0, 0.6);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 12px;
  margin-bottom: 1rem;
}

.notification-info h4,
.preference-info h4 {
  color: rgba(255, 255, 255, 0.9);
  font-size: 1.1rem;
  margin-bottom: 0.5rem;
}

.notification-info p,
.preference-info p {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.9rem;
}

/* ===== Toggle Switch ===== */
.toggle-switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.toggle-switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.toggle-slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.2);
  transition: 0.4s;
  border-radius: 34px;
}

.toggle-slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: 0.4s;
  border-radius: 50%;
}

input:checked + .toggle-slider {
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
}

input:checked + .toggle-slider:before {
  transform: translateX(26px);
}

/* ===== Language Button ===== */
.language-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.language-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.3);
}

/* ===== Responsive Design ===== */
@media (max-width: 768px) {
  .settings-content {
    grid-template-columns: 1fr;
    gap: 0;
  }
  
  .settings-sidebar {
    border-right: none;
    border-bottom: 1px solid rgba(64, 224, 208, 0.2);
    padding: 1rem 0;
  }
  
  .settings-nav {
    flex-direction: row;
    overflow-x: auto;
    gap: 0;
  }
  
  .nav-tab {
    flex-shrink: 0;
    padding: 1rem;
    text-align: center;
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .nav-tab span {
    font-size: 0.8rem;
  }
  
  .form-grid {
    grid-template-columns: 1fr;
  }
  
  .plan-actions {
    flex-direction: column;
  }
  
  .notification-item,
  .preference-item {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
  }
  
  .plan-header {
    flex-direction: column;
    gap: 1rem;
  }
}

@media (max-width: 480px) {
  .settings-container {
    padding: 0 1rem;
  }
  
  .settings-header h1 {
    font-size: 2rem;
  }
  
  .settings-main {
    padding: 1rem;
  }
  
  .form-actions {
    flex-direction: column;
  }
  
  .save-btn {
    width: 100%;
    justify-content: center;
  }
}