/* ===== Owner Dashboard Styles ===== */
#owner-dashboard-root {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 2rem;
  background: linear-gradient(135deg, #0f0c29 0%, #302b63 50%, #24243e 100%);
  min-height: 100vh;
  position: relative;
  color: #ffffff;
}

#owner-dashboard-root::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 20% 20%, rgba(0, 255, 127, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 80% 80%, rgba(64, 224, 208, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 50% 50%, rgba(30, 144, 255, 0.05) 0%, transparent 50%);
  pointer-events: none;
}

/* ===== Welcome Card ===== */
.welcome-card {
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border-radius: 24px;
  padding: 2.5rem;
  box-shadow: 
    0 25px 50px rgba(0, 0, 0, 0.3),
    0 0 0 1px rgba(64, 224, 208, 0.2),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(64, 224, 208, 0.2);
  position: relative;
  z-index: 1;
  overflow: hidden;
}

.welcome-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #00ff7f, #40e0d0, #1e90ff);
  border-radius: 24px 24px 0 0;
}

.welcome-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.welcome-title {
  font-size: 2.5rem;
  font-weight: 800;
  margin-bottom: 0.5rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0, #1e90ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.welcome-subtitle {
  color: #40e0d0;
  font-size: 1.1rem;
  font-weight: 500;
  margin: 0;
}

.welcome-icon {
  opacity: 0.8;
  color: #40e0d0;
}

/* ===== Stats Grid ===== */
.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
}

.stat-card {
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(64, 224, 208, 0.2);
  position: relative;
  z-index: 1;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  overflow: hidden;
}

.stat-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 35px 70px rgba(0, 0, 0, 0.4);
  border-color: #00ff7f;
}

.stat-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.stat-title {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.7);
  font-weight: 600;
  margin-bottom: 0.5rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.stat-value {
  font-size: 2.5rem;
  font-weight: 800;
  color: #ffffff;
  line-height: 1;
}

.stat-icon {
  width: 60px;
  height: 60px;
  border-radius: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
}

.stat-icon::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 16px;
  opacity: 0.8;
}

.stat-icon.blue::before {
  background: linear-gradient(135deg, #1e90ff, #0f4c75);
}

.stat-icon.green::before {
  background: linear-gradient(135deg, #00ff7f, #32cd32);
}

.stat-icon.purple::before {
  background: linear-gradient(135deg, #9370db, #4b0082);
}

.stat-icon.pink::before {
  background: linear-gradient(135deg, #ff69b4, #ff1493);
}

.stat-icon.yellow::before {
  background: linear-gradient(135deg, #ffd700, #ffa500);
}

.stat-icon.teal::before {
  background: linear-gradient(135deg, #00ced1, #20b2aa);
}

.stat-footer {
  display: flex;
  justify-content: space-between;
  margin-top: 1.5rem;
  padding-top: 1rem;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.change-indicator {
  font-weight: 600;
  font-size: 0.9rem;
}

.change-indicator.positive {
  color: #00ff7f;
}

.change-indicator.negative {
  color: #ff4757;
}

.time-period {
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.6);
}

/* ===== Dashboard Row Layout ===== */
.dashboard-row {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
  gap: 2rem;
}

/* ===== Section Titles ===== */
.section-title {
  font-size: 1.5rem;
  font-weight: 700;
  margin: 0 0 1.5rem 0;
  display: flex;
  align-items: center;
  gap: 0.75rem;
  color: #ffffff;
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
}

/* ===== Quick Actions ===== */
.quick-actions-section {
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(64, 224, 208, 0.2);
  position: relative;
  z-index: 1;
}

.actions-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1.5rem;
}

.action-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  padding: 1.5rem;
  border-radius: 16px;
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
  background: rgba(64, 224, 208, 0.1);
  color: #40e0d0;
  border: 2px solid rgba(64, 224, 208, 0.2);
}

.action-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 30px rgba(64, 224, 208, 0.2);
  border-color: #00ff7f;
}

.action-card.blue {
  background: rgba(30, 144, 255, 0.1);
  border-color: rgba(30, 144, 255, 0.2);
  color: #1e90ff;
}

.action-card.blue:hover {
  border-color: #1e90ff;
}

.action-card.green {
  background: rgba(0, 255, 127, 0.1);
  border-color: rgba(0, 255, 127, 0.2);
  color: #00ff7f;
}

.action-card.green:hover {
  border-color: #00ff7f;
}

.action-card.pink {
  background: rgba(255, 105, 180, 0.1);
  border-color: rgba(255, 105, 180, 0.2);
  color: #ff69b4;
}

.action-card.pink:hover {
  border-color: #ff69b4;
}

.action-card.purple {
  background: rgba(147, 112, 219, 0.1);
  border-color: rgba(147, 112, 219, 0.2);
  color: #9370db;
}

.action-card.purple:hover {
  border-color: #9370db;
}

/* ===== Recent Activity ===== */
.recent-activity-section {
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(64, 224, 208, 0.2);
  position: relative;
  z-index: 1;
}

.activity-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.activity-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  border-radius: 12px;
  transition: all 0.3s ease;
  background: rgba(255, 255, 255, 0.05);
}

.activity-item:hover {
  background: rgba(0, 255, 127, 0.1);
}

.activity-icon {
  width: 40px;
  height: 40px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.activity-icon.success {
  background: linear-gradient(135deg, #00ff7f, #32cd32);
  color: #000000;
}

.activity-icon.info {
  background: linear-gradient(135deg, #1e90ff, #0f4c75);
  color: #ffffff;
}

.activity-icon.warning {
  background: linear-gradient(135deg, #ffd700, #ffa500);
  color: #000000;
}

.activity-content {
  flex: 1;
}

.activity-title {
  font-weight: 600;
  color: #ffffff;
  margin: 0 0 0.25rem 0;
}

.activity-meta {
  font-size: 0.85rem;
  color: rgba(255, 255, 255, 0.7);
  font-weight: 500;
}

.activity-meta span:first-child {
  font-weight: 600;
}

/* ===== Subscription Stats ===== */
.subscription-stats {
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(64, 224, 208, 0.2);
  position: relative;
  z-index: 1;
}

.subscription-charts {
  height: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 16px;
  margin-bottom: 1.5rem;
}

.chart-placeholder {
  text-align: center;
  color: rgba(255, 255, 255, 0.5);
}

.chart-placeholder p {
  margin-top: 0.5rem;
  font-size: 0.9rem;
}

.subscription-details {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.detail-item {
  display: flex;
  justify-content: space-between;
  padding: 0.75rem 1rem;
  border-radius: 12px;
  background: rgba(255, 255, 255, 0.05);
}

.detail-label {
  font-weight: 500;
  color: rgba(255, 255, 255, 0.8);
}

.detail-value {
  font-weight: 700;
  color: #00ff7f;
}

/* ===== System Health ===== */
.system-health {
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(64, 224, 208, 0.2);
  position: relative;
  z-index: 1;
}

.health-indicators {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.health-item {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.health-icon {
  width: 40px;
  height: 40px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.health-icon.success {
  background: linear-gradient(135deg, #00ff7f, #32cd32);
  color: #000000;
}

.health-icon.warning {
  background: linear-gradient(135deg, #ffd700, #ffa500);
  color: #000000;
}

.health-content {
  flex: 1;
}

.health-label {
  font-weight: 600;
  color: #ffffff;
  margin-bottom: 0.25rem;
}

.health-status {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.7);
}

/* ===== Loading and Error States ===== */
.owner-dashboard-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  color: #ffffff;
}

.spinner {
  width: 50px;
  height: 50px;
  border: 5px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top-color: #00ff7f;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.owner-dashboard-error {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  color: #ffffff;
  text-align: center;
  padding: 2rem;
}

.owner-dashboard-error h3 {
  margin: 1rem 0;
  font-size: 1.5rem;
}

.owner-dashboard-error p {
  color: #ff6b6b;
  margin-bottom: 1.5rem;
}

/* ===== Responsive Design ===== */
@media (max-width: 1200px) {
  .dashboard-row {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 768px) {
  #owner-dashboard-root {
    padding: 1rem;
    gap: 1.5rem;
  }
  
  .welcome-header {
    flex-direction: column;
    text-align: center;
    gap: 1.5rem;
  }
  
  .welcome-title {
    font-size: 2rem;
  }
  
  .stats-grid {
    grid-template-columns: 1fr;
  }
  
  .actions-grid {
    grid-template-columns: 1fr 1fr;
  }
  
  .detail-item {
    flex-direction: column;
    gap: 0.5rem;
  }
}

@media (max-width: 480px) {
  .welcome-title {
    font-size: 1.75rem;
  }
  
  .stat-value {
    font-size: 2rem;
  }
  
  .stat-card,
  .quick-actions-section,
  .recent-activity-section,
  .subscription-stats,
  .system-health {
    padding: 1.5rem;
  }
  
  .actions-grid {
    grid-template-columns: 1fr;
  }
  
  .section-title {
    font-size: 1.25rem;
  }
}