/* ===== Family Trees Page with Dark Green/Blue/Black Theme ===== */
.family-trees-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 25%, #0f4c75 50%, #3282b8 75%, #0e4b4e 100%);
  padding: 2rem 1rem;
  position: relative;
}

.family-trees-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 20% 20%, rgba(0, 255, 127, 0.05) 0%, transparent 50%),
    radial-gradient(circle at 80% 80%, rgba(30, 144, 255, 0.05) 0%, transparent 50%);
  pointer-events: none;
}

.family-trees-content {
  max-width: 1400px;
  margin: 0 auto;
  position: relative;
  z-index: 1;
}

/* Loading State */
.family-trees-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 60vh;
  color: rgba(255, 255, 255, 0.8);
}

.loading-icon {
  color: #40e0d0;
  animation: pulse 2s infinite;
  margin-bottom: 1rem;
}

@keyframes pulse {
  0%, 100% { opacity: 0.5; transform: scale(1); }
  50% { opacity: 1; transform: scale(1.1); }
}

/* Header Section */
.family-trees-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  padding: 2rem;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border-radius: 20px;
  border: 1px solid rgba(64, 224, 208, 0.2);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.3);
}

.header-main h1.page-title {
  font-size: 2.5rem;
  font-weight: 800;
  margin-bottom: 0.5rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.header-main p.page-subtitle {
  font-size: 1.1rem;
  color: rgba(255, 255, 255, 0.7);
  margin: 0;
}

.create-tree-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
  border: none;
  padding: 1rem 2rem;
  border-radius: 16px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.3);
}

.create-tree-btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 15px 40px rgba(0, 255, 127, 0.4);
}

/* Controls Section */
.trees-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  margin-bottom: 2rem;
  padding: 1.5rem;
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(15px);
  border-radius: 16px;
  border: 1px solid rgba(64, 224, 208, 0.2);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
}

.search-section {
  flex: 1;
  max-width: 400px;
}

.search-box {
  position: relative;
  display: flex;
  align-items: center;
}

.search-box svg {
  position: absolute;
  left: 1rem;
  color: rgba(255, 255, 255, 0.5);
  pointer-events: none;
  z-index: 2;
}

.search-input {
  width: 100%;
  padding: 0.75rem 1rem 0.75rem 3rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 12px;
  color: rgba(255, 255, 255, 0.9);
  font-size: 1rem;
  transition: all 0.3s ease;
}

.search-input:focus {
  outline: none;
  border-color: #40e0d0;
  background: rgba(255, 255, 255, 0.08);
  box-shadow: 0 0 20px rgba(64, 224, 208, 0.2);
}

.search-input::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.filters-section {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.filter-group {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.filter-group svg {
  color: rgba(255, 255, 255, 0.6);
}

.filter-select {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 10px;
  color: rgba(255, 255, 255, 0.9);
  padding: 0.5rem 1rem;
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.filter-select:focus {
  outline: none;
  border-color: #40e0d0;
  box-shadow: 0 0 15px rgba(64, 224, 208, 0.2);
}

.filter-select option {
  background: #1a1a2e;
  color: white;
}

.sort-order-btn {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 8px;
  color: rgba(255, 255, 255, 0.8);
  padding: 0.5rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.sort-order-btn:hover {
  background: rgba(64, 224, 208, 0.1);
  border-color: #40e0d0;
}

.view-mode-toggle {
  display: flex;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
  padding: 0.25rem;
  border: 1px solid rgba(64, 224, 208, 0.2);
}

.view-mode-btn {
  background: transparent;
  border: none;
  color: rgba(255, 255, 255, 0.6);
  padding: 0.5rem;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.view-mode-btn.active {
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
  box-shadow: 0 4px 15px rgba(0, 255, 127, 0.3);
}

/* Trees Container */
.trees-container {
  display: grid;
  gap: 1.5rem;
  margin-top: 2rem;
}

/* Error Message */
.error-message {
  text-align: center;
  padding: 2rem;
  background: rgba(255, 69, 58, 0.1);
  border: 1px solid rgba(255, 69, 58, 0.3);
  border-radius: 16px;
  margin: 2rem 0;
}

.error-message p {
  color: #ff453a;
  margin-bottom: 1rem;
  font-size: 1rem;
}

.retry-btn {
  background: rgba(255, 69, 58, 0.2);
  color: #ff453a;
  border: 1px solid rgba(255, 69, 58, 0.3);
  padding: 0.5rem 1rem;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 0.9rem;
}

.retry-btn:hover:not(:disabled) {
  background: rgba(255, 69, 58, 0.3);
  transform: translateY(-1px);
}

.retry-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Trees Container */
.trees-container {
  display: grid;
  gap: 2rem;
  margin-bottom: 2rem;
}

.trees-container.grid {
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
}

.trees-container.list {
  grid-template-columns: 1fr;
}

/* Tree Cards */
.tree-card {
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border-radius: 20px;
  border: 1px solid rgba(64, 224, 208, 0.2);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.3);
  overflow: hidden;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
}

.tree-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 35px 70px rgba(0, 255, 127, 0.15);
  border-color: rgba(0, 255, 127, 0.3);
}

.tree-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #00ff7f, #40e0d0, #1e90ff);
  border-radius: 20px 20px 0 0;
}

/* Tree Image */
.tree-image {
  position: relative;
  height: 200px;
  overflow: hidden;
}

.tree-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.4s ease;
}

.tree-card:hover .tree-image img {
  transform: scale(1.05);
}

.tree-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 0.3) 50%,
    rgba(0, 0, 0, 0.8) 100%
  );
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.tree-card:hover .tree-overlay {
  opacity: 1;
}

.tree-actions {
  display: flex;
  gap: 1rem;
}

.action-btn {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.view-btn {
  background: rgba(0, 255, 127, 0.2);
  color: #00ff7f;
  border: 1px solid rgba(0, 255, 127, 0.3);
}

.view-btn:hover {
  background: rgba(0, 255, 127, 0.3);
  transform: scale(1.1);
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.4);
}

.edit-btn {
  background: rgba(30, 144, 255, 0.2);
  color: #1e90ff;
  border: 1px solid rgba(30, 144, 255, 0.3);
}

.edit-btn:hover {
  background: rgba(30, 144, 255, 0.3);
  transform: scale(1.1);
  box-shadow: 0 8px 25px rgba(30, 144, 255, 0.4);
}

.delete-btn {
  background: rgba(255, 69, 58, 0.2);
  color: #ff453a;
  border: 1px solid rgba(255, 69, 58, 0.3);
}

.delete-btn:hover {
  background: rgba(255, 69, 58, 0.3);
  transform: scale(1.1);
  box-shadow: 0 8px 25px rgba(255, 69, 58, 0.4);
}

/* Status Badges */
.status-badge {
  position: absolute;
  top: 1rem;
  right: 1rem;
  padding: 0.25rem 0.75rem;
  border-radius: 15px;
  font-size: 0.8rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.status-badge.draft {
  background: rgba(255, 165, 0, 0.15);
  color: #ffa500;
  border: 1px solid rgba(255, 165, 0, 0.3);
}

.status-badge.public {
  background: rgba(0, 255, 127, 0.15);
  color: #00ff7f;
  border: 1px solid rgba(0, 255, 127, 0.3);
}

/* Tree Info */
.tree-info {
  padding: 1.5rem;
}

.tree-name {
  font-size: 1.3rem;
  font-weight: 700;
  color: rgba(255, 255, 255, 0.95);
  margin-bottom: 0.5rem;
}

.tree-description {
  font-size: 0.95rem;
  color: rgba(255, 255, 255, 0.7);
  margin-bottom: 1rem;
  line-height: 1.5;
}

.tree-stats {
  display: flex;
  gap: 1rem;
  margin-bottom: 1rem;
  flex-wrap: wrap;
}

.stat {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.9rem;
}

.stat svg {
  color: #40e0d0;
}

.tree-owner {
  padding-top: 1rem;
  border-top: 1px solid rgba(64, 224, 208, 0.2);
  font-size: 0.9rem;
}

.owner-label {
  color: rgba(255, 255, 255, 0.6);
}

.owner-name {
  color: #40e0d0;
  margin-left: 0.5rem;
}

/* Empty State */
.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4rem 2rem;
  text-align: center;
  grid-column: 1 / -1;
}

.empty-state svg {
  color: rgba(255, 255, 255, 0.3);
  margin-bottom: 1.5rem;
}

.empty-state h3 {
  font-size: 1.5rem;
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 1rem;
}

.empty-state p {
  color: rgba(255, 255, 255, 0.6);
  margin-bottom: 2rem;
  max-width: 400px;
}

.create-first-tree-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
  border: none;
  padding: 1rem 2rem;
  border-radius: 16px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.3);
}

.create-first-tree-btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 15px 40px rgba(0, 255, 127, 0.4);
}

/* List View Specific Styles */
.trees-container.list .tree-card {
  display: flex;
  align-items: center;
  min-height: 120px;
}

.trees-container.list .tree-image {
  width: 200px;
  height: 120px;
  flex-shrink: 0;
}

.trees-container.list .tree-info {
  flex: 1;
  padding: 1rem 1.5rem;
}

.trees-container.list .tree-stats {
  margin-bottom: 0.5rem;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .trees-controls {
    flex-direction: column;
    gap: 1rem;
  }
  
  .search-section {
    max-width: none;
    width: 100%;
  }
  
  .filters-section {
    width: 100%;
    justify-content: space-between;
  }
  
  .trees-container.grid {
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  }
}

@media (max-width: 768px) {
  .family-trees-header {
    flex-direction: column;
    gap: 1rem;
    text-align: center;
    padding: 1.5rem;
  }
  
  .filters-section {
    flex-wrap: wrap;
    gap: 0.5rem;
  }
  
  .trees-container.grid {
    grid-template-columns: 1fr;
  }
  
  .trees-container.list .tree-card {
    flex-direction: column;
  }
  
  .trees-container.list .tree-image {
    width: 100%;
    height: 200px;
  }
  
  .tree-actions {
    gap: 0.5rem;
  }
  
  .action-btn {
    width: 40px;
    height: 40px;
  }
}

@media (max-width: 480px) {
  .family-trees-container {
    padding: 1rem 0.5rem;
  }
  
  .header-main h1.page-title {
    font-size: 2rem;
  }
  
  .create-tree-btn {
    width: 100%;
    justify-content: center;
  }
  
  .tree-info {
    padding: 1rem;
  }
  
  .tree-stats {
    flex-direction: column;
    gap: 0.5rem;
  }
}

/* RTL Support */
[dir="rtl"] .search-box svg {
  left: auto;
  right: 1rem;
}

[dir="rtl"] .search-input {
  padding: 0.75rem 3rem 0.75rem 1rem;
}

[dir="rtl"] .owner-name {
  margin-left: 0;
  margin-right: 0.5rem;
}

[dir="rtl"] .status-badge {
  right: auto;
  left: 1rem;
}