/* ===== Tree Settings Component Styling ===== */
.tree-settings-container {
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(15px);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 20px;
  padding: 2rem;
}

.settings-header {
  margin-bottom: 2rem;
  text-align: center;
}

.settings-header h3 {
  color: rgba(255, 255, 255, 0.95);
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}

.settings-header p {
  color: rgba(255, 255, 255, 0.7);
  font-size: 1rem;
  margin: 0;
}

.settings-sections {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.settings-section {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(64, 224, 208, 0.1);
  border-radius: 16px;
  padding: 1.5rem;
}

.settings-section.danger-zone {
  border-color: rgba(255, 69, 58, 0.3);
  background: rgba(255, 69, 58, 0.05);
}

.section-header {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  margin-bottom: 1.5rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid rgba(64, 224, 208, 0.2);
}

.danger-zone .section-header {
  border-bottom-color: rgba(255, 69, 58, 0.2);
}

.section-header svg {
  color: #40e0d0;
}

.danger-zone .section-header svg {
  color: #ff453a;
}

.section-header h4 {
  color: rgba(255, 255, 255, 0.95);
  font-size: 1.2rem;
  margin: 0;
}

.settings-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.form-group label {
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.9rem;
  font-weight: 500;
}

.form-group input,
.form-group textarea {
  padding: 0.75rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 12px;
  color: rgba(255, 255, 255, 0.9);
  font-size: 1rem;
  transition: all 0.3s ease;
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: #40e0d0;
  box-shadow: 0 0 15px rgba(64, 224, 208, 0.2);
}

.form-group textarea {
  resize: vertical;
  min-height: 80px;
}

/* Toggle Controls */
.toggle-group {
  display: flex;
  align-items: center;
}

.toggle-label {
  display: flex;
  align-items: center;
  gap: 1rem;
  cursor: pointer;
  width: 100%;
}

.toggle-label input[type="checkbox"] {
  display: none;
}

.toggle-slider {
  position: relative;
  width: 50px;
  height: 26px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  border: 1px solid rgba(64, 224, 208, 0.3);
  transition: all 0.3s ease;
  cursor: pointer;
}

.toggle-slider::before {
  content: '';
  position: absolute;
  top: 2px;
  left: 2px;
  width: 20px;
  height: 20px;
  background: rgba(255, 255, 255, 0.8);
  border-radius: 50%;
  transition: all 0.3s ease;
}

.toggle-label input[type="checkbox"]:checked + .toggle-slider {
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  border-color: #00ff7f;
}

.toggle-label input[type="checkbox"]:checked + .toggle-slider::before {
  transform: translateX(24px);
  background: white;
}

.toggle-text {
  color: rgba(255, 255, 255, 0.9);
  font-size: 0.95rem;
  flex: 1;
}

/* Danger Zone */
.danger-content {
  text-align: center;
}

.danger-content p {
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 1.5rem;
  font-size: 0.95rem;
}

.delete-tree-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  background: rgba(255, 69, 58, 0.15);
  color: #ff453a;
  border: 1px solid rgba(255, 69, 58, 0.3);
  padding: 0.75rem 1.5rem;
  border-radius: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin: 0 auto;
}

.delete-tree-btn:hover {
  background: rgba(255, 69, 58, 0.25);
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(255, 69, 58, 0.3);
}

/* Save Button */
.settings-footer {
  margin-top: 2rem;
  padding-top: 2rem;
  border-top: 1px solid rgba(64, 224, 208, 0.2);
  text-align: center;
}

.save-settings-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
  border: none;
  padding: 1rem 2rem;
  border-radius: 16px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin: 0 auto;
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.3);
}

.save-settings-btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 15px 40px rgba(0, 255, 127, 0.4);
}

/* Responsive Design */
@media (max-width: 768px) {
  .tree-settings-container {
    padding: 1.5rem;
  }
  
  .settings-section {
    padding: 1rem;
  }
  
  .section-header {
    flex-direction: column;
    text-align: center;
    gap: 0.5rem;
  }
  
  .toggle-label {
    flex-direction: column;
    gap: 0.75rem;
    text-align: center;
  }
  
  .toggle-slider {
    margin: 0 auto;
  }
  
  .settings-footer {
    margin-top: 1.5rem;
    padding-top: 1.5rem;
  }
  
  .save-settings-btn {
    width: 100%;
  }
}

@media (max-width: 480px) {
  .settings-header h3 {
    font-size: 1.3rem;
  }
  
  .section-header h4 {
    font-size: 1.1rem;
  }
  
  .form-group input,
  .form-group textarea {
    padding: 0.6rem;
    font-size: 0.9rem;
  }
  
  .delete-tree-btn,
  .save-settings-btn {
    padding: 0.75rem 1.25rem;
    font-size: 0.9rem;
  }
}

/* RTL Support */
[dir="rtl"] .section-header {
  flex-direction: row-reverse;
}

[dir="rtl"] .toggle-label {
  flex-direction: row-reverse;
}

[dir="rtl"] .toggle-slider::before {
  left: auto;
  right: 2px;
}

[dir="rtl"] .toggle-label input[type="checkbox"]:checked + .toggle-slider::before {
  transform: translateX(-24px);
}

[dir="rtl"] .form-group {
  text-align: right;
}