/* ===== Create Tree Form Component Styling ===== */
.create-tree-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 1rem;
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.create-tree-modal {
  background: rgba(0, 0, 0, 0.9);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(64, 224, 208, 0.3);
  border-radius: 24px;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.7);
  width: 100%;
  max-width: 600px;
  max-height: 90vh;
  overflow-y: auto;
  animation: slideUp 0.3s ease;
}

@keyframes slideUp {
  from {
    transform: translateY(30px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

/* Header */
.create-tree-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2rem 2rem 1rem 2rem;
  border-bottom: 1px solid rgba(64, 224, 208, 0.2);
}

.header-content {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.header-icon {
  color: #40e0d0;
}

.create-tree-header h2 {
  color: rgba(255, 255, 255, 0.95);
  font-size: 1.5rem;
  margin: 0;
}

.close-btn {
  background: rgba(255, 69, 58, 0.2);
  border: 1px solid rgba(255, 69, 58, 0.3);
  color: #ff453a;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}

.close-btn:hover:not(:disabled) {
  background: rgba(255, 69, 58, 0.3);
  transform: scale(1.05);
}

.close-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Form */
.create-tree-form {
  padding: 2rem;
}

.form-section {
  margin-bottom: 2rem;
  background: rgba(255, 255, 255, 0.02);
  border: 1px solid rgba(64, 224, 208, 0.1);
  border-radius: 16px;
  padding: 1.5rem;
}

.section-title {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  color: rgba(255, 255, 255, 0.9);
  font-size: 1.1rem;
  margin-bottom: 1.5rem;
  padding-bottom: 0.75rem;
  border-bottom: 1px solid rgba(64, 224, 208, 0.2);
}

.section-title svg {
  color: #40e0d0;
}

.optional-text {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.9rem;
  font-weight: normal;
  margin-left: 0.5rem;
}

/* Form Groups */
.form-group {
  margin-bottom: 1.5rem;
}

.form-group:last-child {
  margin-bottom: 0;
}

.form-group label {
  display: block;
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.95rem;
  font-weight: 500;
  margin-bottom: 0.5rem;
}

.form-input,
.form-textarea,
.form-select {
  width: 100%;
  padding: 0.75rem 1rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 12px;
  color: rgba(255, 255, 255, 0.9);
  font-size: 1rem;
  transition: all 0.3s ease;
  box-sizing: border-box;
}

.form-input:focus,
.form-textarea:focus,
.form-select:focus {
  outline: none;
  border-color: #40e0d0;
  box-shadow: 0 0 15px rgba(64, 224, 208, 0.2);
}

.form-input::placeholder,
.form-textarea::placeholder {
  color: rgba(255, 255, 255, 0.4);
}

.form-textarea {
  resize: vertical;
  min-height: 80px;
}

.form-input.error,
.form-textarea.error,
.form-select.error {
  border-color: #ff453a;
  box-shadow: 0 0 15px rgba(255, 69, 58, 0.2);
}

.form-input:disabled,
.form-textarea:disabled,
.form-select:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* Form Row */
.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
}

/* Toggle Label */
.toggle-label {
  display: flex;
  align-items: center;
  gap: 1rem;
  cursor: pointer;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(64, 224, 208, 0.1);
  border-radius: 12px;
  transition: all 0.3s ease;
}

.toggle-label:hover {
  background: rgba(255, 255, 255, 0.05);
  border-color: rgba(64, 224, 208, 0.2);
}

.toggle-label input[type="checkbox"] {
  display: none;
}

.toggle-slider {
  position: relative;
  width: 50px;
  height: 26px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  border: 1px solid rgba(64, 224, 208, 0.3);
  transition: all 0.3s ease;
  flex-shrink: 0;
}

.toggle-slider::before {
  content: '';
  position: absolute;
  top: 2px;
  left: 2px;
  width: 20px;
  height: 20px;
  background: rgba(255, 255, 255, 0.8);
  border-radius: 50%;
  transition: all 0.3s ease;
}

.toggle-label input[type="checkbox"]:checked + .toggle-slider {
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  border-color: #00ff7f;
}

.toggle-label input[type="checkbox"]:checked + .toggle-slider::before {
  transform: translateX(24px);
  background: white;
}

.toggle-content {
  flex: 1;
}

.toggle-icon {
  color: #40e0d0;
  margin-bottom: 0.25rem;
}

.toggle-title {
  display: block;
  color: rgba(255, 255, 255, 0.9);
  font-weight: 600;
  margin-bottom: 0.25rem;
}

.toggle-description {
  display: block;
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.85rem;
}

/* Error Messages */
.error-message {
  display: block;
  color: #ff453a;
  font-size: 0.85rem;
  margin-top: 0.5rem;
}

.submit-error {
  background: rgba(255, 69, 58, 0.1);
  border: 1px solid rgba(255, 69, 58, 0.3);
  border-radius: 8px;
  padding: 0.75rem;
  margin-bottom: 1.5rem;
  text-align: center;
}

/* Form Actions */
.form-actions {
  display: flex;
  gap: 1rem;
  margin-top: 2rem;
  padding-top: 2rem;
  border-top: 1px solid rgba(64, 224, 208, 0.2);
}

.cancel-btn,
.create-btn {
  flex: 1;
  padding: 0.875rem 1.5rem;
  border-radius: 12px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  border: none;
}

.cancel-btn {
  background: rgba(255, 255, 255, 0.08);
  color: rgba(255, 255, 255, 0.8);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.cancel-btn:hover:not(:disabled) {
  background: rgba(255, 255, 255, 0.12);
  color: rgba(255, 255, 255, 0.9);
}

.create-btn {
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.3);
}

.create-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 12px 35px rgba(0, 255, 127, 0.4);
}

.cancel-btn:disabled,
.create-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none;
}

/* Mobile Responsive */
@media (max-width: 768px) {
  .create-tree-overlay {
    padding: 0.5rem;
  }
  
  .create-tree-modal {
    max-height: 95vh;
    border-radius: 16px;
  }
  
  .create-tree-header {
    padding: 1.5rem 1.5rem 1rem 1.5rem;
  }
  
  .create-tree-header h2 {
    font-size: 1.3rem;
  }
  
  .create-tree-form {
    padding: 1.5rem;
  }
  
  .form-section {
    padding: 1rem;
  }
  
  .form-row {
    grid-template-columns: 1fr;
    gap: 0;
  }
  
  .form-actions {
    flex-direction: column;
  }
  
  .toggle-label {
    flex-direction: column;
    text-align: center;
    gap: 0.75rem;
  }
}

@media (max-width: 480px) {
  .create-tree-header {
    padding: 1rem;
    flex-direction: column;
    gap: 1rem;
    text-align: center;
  }
  
  .create-tree-header h2 {
    font-size: 1.2rem;
  }
  
  .create-tree-form {
    padding: 1rem;
  }
  
  .form-section {
    padding: 0.75rem;
  }
  
  .section-title {
    font-size: 1rem;
  }
  
  .form-input,
  .form-textarea,
  .form-select {
    padding: 0.6rem 0.8rem;
    font-size: 0.9rem;
  }
  
  .cancel-btn,
  .create-btn {
    padding: 0.75rem 1.25rem;
    font-size: 0.9rem;
  }
}

/* RTL Support */
[dir="rtl"] .header-content {
  flex-direction: row-reverse;
}

[dir="rtl"] .section-title {
  flex-direction: row-reverse;
}

[dir="rtl"] .toggle-label {
  flex-direction: row-reverse;
}

[dir="rtl"] .toggle-slider::before {
  left: auto;
  right: 2px;
}

[dir="rtl"] .toggle-label input[type="checkbox"]:checked + .toggle-slider::before {
  transform: translateX(-24px);
}

[dir="rtl"] .form-actions {
  flex-direction: row-reverse;
}

/* Loading state */
.create-btn:disabled {
  position: relative;
}

.create-btn:disabled::after {
  content: '';
  position: absolute;
  left: 50%;
  top: 50%;
  width: 16px;
  height: 16px;
  margin: -8px 0 0 -8px;
  border: 2px solid transparent;
  border-top: 2px solid white;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Image Upload Section */
.image-upload-container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.image-preview {
  width: 100%;
  height: 200px;
  border-radius: 12px;
  overflow: hidden;
  background: rgba(255, 255, 255, 0.03);
  border: 1px dashed rgba(64, 224, 208, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
}

.preview-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.image-placeholder {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  color: rgba(255, 255, 255, 0.5);
}

.image-placeholder svg {
  color: rgba(64, 224, 208, 0.5);
}

.image-upload-controls {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

.upload-button {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
  border-radius: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  border: none;
}

.upload-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.3);
}

.remove-image-btn {
  padding: 0.75rem 1.5rem;
  background: rgba(255, 69, 58, 0.2);
  color: #ff453a;
  border: 1px solid rgba(255, 69, 58, 0.3);
  border-radius: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.remove-image-btn:hover:not(:disabled) {
  background: rgba(255, 69, 58, 0.3);
  transform: translateY(-2px);
}

.save-image-btn {
  padding: 0.75rem 1.5rem;
  background: linear-gradient(135deg, #1e90ff, #40e0d0);
  color: white;
  border: none;
  border-radius: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.save-image-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(30, 144, 255, 0.3);
}

.upload-button:disabled,
.remove-image-btn:disabled,
.save-image-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none;
}

/* Action Buttons */
.action-buttons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}

.delete-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  background: rgba(255, 69, 58, 0.2);
  color: #ff453a;
  border: 1px solid rgba(255, 69, 58, 0.3);
  border-radius: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.delete-btn:hover:not(:disabled) {
  background: rgba(255, 69, 58, 0.3);
  transform: translateY(-2px);
}

.primary-actions {
  display: flex;
  gap: 1rem;
}

.submit-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.875rem 1.5rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
  border-radius: 12px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  border: none;
}

.submit-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 12px 35px rgba(0, 255, 127, 0.4);
}

.spinner {
  width: 20px;
  height: 20px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-top: 2px solid white;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Mobile Responsive for Image Upload */
@media (max-width: 768px) {
  .image-preview {
    height: 150px;
  }
  
  .image-upload-controls {
    flex-direction: column;
  }
  
  .action-buttons {
    flex-direction: column;
  }
  
  .primary-actions {
    width: 100%;
  }
  
  .submit-btn,
  .cancel-btn,
  .delete-btn {
    width: 100%;
    justify-content: center;
  }
}

@media (max-width: 480px) {
  .image-preview {
    height: 120px;
  }
}

/* Scrollbar styling for modal */
.create-tree-modal::-webkit-scrollbar {
  width: 6px;
}

.create-tree-modal::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 3px;
}

.create-tree-modal::-webkit-scrollbar-thumb {
  background: rgba(64, 224, 208, 0.5);
  border-radius: 3px;
}

.create-tree-modal::-webkit-scrollbar-thumb:hover {
  background: rgba(64, 224, 208, 0.7);
}