/* ===== Legal Pages Styling with Dark Green/Blue/Black Theme ===== */
.legal-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 25%, #0f4c75 50%, #3282b8 75%, #0e4b4e 100%);
  padding: 2rem 1rem;
  position: relative;
}

.legal-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 20% 20%, rgba(0, 255, 127, 0.05) 0%, transparent 50%),
    radial-gradient(circle at 80% 80%, rgba(30, 144, 255, 0.05) 0%, transparent 50%);
  pointer-events: none;
}

.legal-content {
  max-width: 1000px;
  margin: 0 auto;
  position: relative;
  z-index: 1;
}

/* Header Section */
.legal-header {
  text-align: center;
  margin-bottom: 3rem;
  padding: 2rem;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border-radius: 20px;
  border: 1px solid rgba(64, 224, 208, 0.2);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.3);
}

.legal-icon {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 80px;
  height: 80px;
  background: linear-gradient(135deg, rgba(0, 255, 127, 0.15), rgba(30, 144, 255, 0.15));
  border: 2px solid rgba(64, 224, 208, 0.3);
  border-radius: 50%;
  color: #40e0d0;
  margin-bottom: 1.5rem;
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.2);
}

.legal-title {
  font-size: 3rem;
  font-weight: 800;
  margin-bottom: 1rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.legal-subtitle {
  font-size: 1.1rem;
  color: rgba(255, 255, 255, 0.7);
  margin-bottom: 0;
  font-weight: 500;
}

/* Body Content */
.legal-body {
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border-radius: 20px;
  padding: 3rem;
  border: 1px solid rgba(64, 224, 208, 0.2);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.3);
}

.legal-intro {
  font-size: 1.2rem;
  line-height: 1.8;
  color: rgba(255, 255, 255, 0.9);
  text-align: center;
  margin-bottom: 3rem;
  padding: 2rem;
  background: rgba(64, 224, 208, 0.05);
  border-radius: 15px;
  border: 1px solid rgba(64, 224, 208, 0.1);
}

/* Sections */
.legal-section {
  margin-bottom: 3rem;
  padding-bottom: 2rem;
  border-bottom: 1px solid rgba(64, 224, 208, 0.1);
}

.legal-section:last-child {
  border-bottom: none;
  margin-bottom: 0;
  padding-bottom: 0;
}

.section-header {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1.5rem;
  padding-bottom: 1rem;
  border-bottom: 2px solid rgba(64, 224, 208, 0.2);
}

.section-header svg {
  color: #40e0d0;
  flex-shrink: 0;
}

.section-header h2 {
  font-size: 1.8rem;
  font-weight: 700;
  color: rgba(255, 255, 255, 0.95);
  margin: 0;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.section-content {
  line-height: 1.7;
  color: rgba(255, 255, 255, 0.8);
}

.section-content h3 {
  font-size: 1.3rem;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.95);
  margin: 2rem 0 1rem 0;
  padding-left: 1rem;
  border-left: 3px solid #40e0d0;
}

.section-content h4 {
  font-size: 1.1rem;
  font-weight: 600;
  color: #40e0d0;
  margin: 1.5rem 0 0.75rem 0;
}

.section-content p {
  margin-bottom: 1.5rem;
  font-size: 1rem;
}

.section-content ul {
  margin: 1rem 0 1.5rem 0;
  padding-left: 0;
  list-style: none;
}

.section-content li {
  margin-bottom: 0.75rem;
  padding: 0.75rem 1rem;
  background: rgba(255, 255, 255, 0.03);
  border-radius: 8px;
  border-left: 3px solid rgba(0, 255, 127, 0.3);
  transition: all 0.3s ease;
  position: relative;
}

.section-content li::before {
  content: '';
  color: #00ff7f;
  font-weight: bold;
  margin-right: 0.75rem;
  font-size: 1rem;
}

.section-content li:hover {
  background: rgba(64, 224, 208, 0.05);
  border-left-color: #40e0d0;
  transform: translateX(5px);
}

/* Contact Information */
.contact-info {
  background: rgba(64, 224, 208, 0.05);
  padding: 1.5rem;
  border-radius: 12px;
  border: 1px solid rgba(64, 224, 208, 0.2);
  margin-top: 1.5rem;
}

.contact-info p {
  margin-bottom: 0.75rem;
  font-size: 0.95rem;
}

.contact-info strong {
  color: #40e0d0;
  font-weight: 600;
}

/* Information Boxes */
.info-box {
  background: rgba(0, 255, 127, 0.05);
  border: 1px solid rgba(0, 255, 127, 0.2);
  border-radius: 12px;
  padding: 1.5rem;
  margin: 1.5rem 0;
}

.info-box h4 {
  color: #00ff7f;
  margin-bottom: 1rem;
  font-size: 1.1rem;
  font-weight: 600;
}

.warning-box {
  background: rgba(255, 165, 0, 0.05);
  border: 1px solid rgba(255, 165, 0, 0.2);
  border-radius: 12px;
  padding: 1.5rem;
  margin: 1.5rem 0;
}

.warning-box h4 {
  color: #ffa500;
  margin-bottom: 1rem;
  font-size: 1.1rem;
  font-weight: 600;
}

/* Cookie Policy Specific Styles */
.cookie-type {
  background: rgba(255, 255, 255, 0.02);
  border-radius: 12px;
  padding: 1.5rem;
  margin-bottom: 1.5rem;
  border: 1px solid rgba(64, 224, 208, 0.1);
  transition: all 0.3s ease;
}

.cookie-type:hover {
  background: rgba(64, 224, 208, 0.03);
  border-color: rgba(64, 224, 208, 0.2);
}

.cookie-type-header {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1rem;
  flex-wrap: wrap;
}

.cookie-type-header h3 {
  margin: 0;
  flex: 1;
  font-size: 1.2rem;
  color: rgba(255, 255, 255, 0.95);
}

.cookie-type-header svg {
  color: #40e0d0;
}

.cookie-status {
  padding: 0.25rem 0.75rem;
  border-radius: 15px;
  font-size: 0.8rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.cookie-status.required {
  background: rgba(255, 69, 58, 0.15);
  color: #ff453a;
  border: 1px solid rgba(255, 69, 58, 0.3);
}

.cookie-status.optional {
  background: rgba(0, 255, 127, 0.15);
  color: #00ff7f;
  border: 1px solid rgba(0, 255, 127, 0.3);
}

/* Third Party Services */
.third-party-services {
  display: grid;
  gap: 1rem;
  margin-top: 1.5rem;
}

.service-item {
  background: rgba(255, 255, 255, 0.03);
  border-radius: 10px;
  padding: 1.25rem;
  border: 1px solid rgba(64, 224, 208, 0.1);
  transition: all 0.3s ease;
}

.service-item:hover {
  background: rgba(64, 224, 208, 0.05);
  border-color: rgba(64, 224, 208, 0.2);
  transform: translateY(-2px);
}

.service-item h4 {
  color: #40e0d0;
  margin-bottom: 0.5rem;
  font-size: 1rem;
}

.service-item p {
  margin: 0;
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.7);
}

/* Browser Links */
.browser-links ul {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 1rem;
  margin-top: 1rem;
}

.browser-links li {
  background: rgba(30, 144, 255, 0.05);
  border: 1px solid rgba(30, 144, 255, 0.2);
  border-radius: 8px;
  padding: 0.75rem;
  text-align: center;
}

.browser-links li::before {
  display: none;
}

.browser-links a {
  color: #1e90ff;
  text-decoration: none;
  font-weight: 500;
  transition: all 0.3s ease;
}

.browser-links a:hover {
  color: #40e0d0;
}

/* Responsive Design */
@media (max-width: 768px) {
  .legal-container {
    padding: 1rem 0.5rem;
  }
  
  .legal-header {
    padding: 1.5rem;
    margin-bottom: 2rem;
  }
  
  .legal-title {
    font-size: 2rem;
  }
  
  .legal-body {
    padding: 2rem 1.5rem;
  }
  
  .section-header {
    flex-direction: column;
    text-align: center;
    gap: 0.5rem;
  }
  
  .section-header h2 {
    font-size: 1.5rem;
  }
  
  .cookie-type-header {
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 0.75rem;
  }
  
  .browser-links ul {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 480px) {
  .legal-title {
    font-size: 1.7rem;
  }
  
  .legal-body {
    padding: 1.5rem 1rem;
  }
  
  .legal-icon {
    width: 60px;
    height: 60px;
  }
  
  .legal-icon svg {
    width: 32px;
    height: 32px;
  }
  
  .section-content li {
    padding: 0.5rem 0.75rem;
    font-size: 0.9rem;
  }
}

/* RTL Support */
[dir="rtl"] .section-content li {
  border-left: none;
  border-right: 3px solid rgba(0, 255, 127, 0.3);
}

[dir="rtl"] .section-content li:hover {
  transform: translateX(-5px);
}

[dir="rtl"] .section-content h3 {
  padding-left: 0;
  padding-right: 1rem;
  border-left: none;
  border-right: 3px solid #40e0d0;
}

[dir="rtl"] .section-content li::before {
  margin-right: 0;
  margin-left: 0.75rem;
}