/* ===== Animated Tree Leaves Background ===== */
.leaf-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: -1;
  overflow: hidden;
}

.leaves-container {
  position: relative;
  width: 100%;
  height: 100%;
}

/* Individual Leaf Styling */
.leaf {
  position: absolute;
  top: -50px;
  color: rgba(0, 255, 127, 0.15);
  animation: 
    leafFall var(--duration, 10s) var(--delay, 0s) infinite linear,
    leafRotate var(--duration, 10s) var(--delay, 0s) infinite linear,
    leafSway 3s ease-in-out infinite;
  transform-origin: center;
  will-change: transform;
}

/* Different leaf colors and sizes */
.leaf-1 {
  color: rgba(0, 255, 127, 0.12);
  transform: scale(0.8);
  left: var(--start-x, 10%);
}

.leaf-2 {
  color: rgba(64, 224, 208, 0.15);
  transform: scale(1.2);
  left: var(--start-x, 30%);
}

.leaf-3 {
  color: rgba(0, 255, 127, 0.18);
  transform: scale(0.9);
  left: var(--start-x, 50%);
}

.leaf-4 {
  color: rgba(30, 144, 255, 0.12);
  transform: scale(1.1);
  left: var(--start-x, 70%);
}

.leaf-5 {
  color: rgba(64, 224, 208, 0.14);
  transform: scale(0.7);
  left: var(--start-x, 90%);
}

/* Leaf Fall Animation */
@keyframes leafFall {
  0% {
    top: -50px;
    left: var(--start-x, 10%);
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    top: calc(100vh + 50px);
    left: var(--end-x, 90%);
    opacity: 0;
  }
}

/* Leaf Rotation Animation */
@keyframes leafRotate {
  0% {
    transform: rotate(0deg) scale(var(--scale, 1));
  }
  25% {
    transform: rotate(90deg) scale(var(--scale, 1));
  }
  50% {
    transform: rotate(180deg) scale(var(--scale, 1));
  }
  75% {
    transform: rotate(270deg) scale(var(--scale, 1));
  }
  100% {
    transform: rotate(360deg) scale(var(--scale, 1));
  }
}

/* Leaf Sway Animation */
@keyframes leafSway {
  0%, 100% {
    transform: translateX(0px) translateY(0px);
  }
  25% {
    transform: translateX(10px) translateY(-5px);
  }
  50% {
    transform: translateX(-5px) translateY(5px);
  }
  75% {
    transform: translateX(-10px) translateY(-3px);
  }
}

/* Floating Particles */
.floating-particle {
  position: absolute;
  width: 4px;
  height: 4px;
  background: radial-gradient(circle, rgba(0, 255, 127, 0.3), rgba(0, 255, 127, 0.1));
  border-radius: 50%;
  top: 100vh;
  left: var(--x-position, 50%);
  animation: 
    particleFloat var(--duration, 15s) var(--delay, 0s) infinite linear,
    particleGlow 2s ease-in-out infinite alternate;
  will-change: transform;
}

/* Particle Float Animation */
@keyframes particleFloat {
  0% {
    top: 100vh;
    opacity: 0;
    transform: translateX(0px);
  }
  10% {
    opacity: 1;
  }
  50% {
    transform: translateX(20px);
  }
  90% {
    opacity: 1;
  }
  100% {
    top: -20px;
    opacity: 0;
    transform: translateX(-10px);
  }
}

/* Particle Glow Animation */
@keyframes particleGlow {
  0% {
    box-shadow: 0 0 5px rgba(0, 255, 127, 0.3);
  }
  100% {
    box-shadow: 0 0 15px rgba(0, 255, 127, 0.6), 0 0 25px rgba(0, 255, 127, 0.3);
  }
}

/* Enhanced Leaf Variants with Different Shapes */
.leaf:nth-child(3n) {
  animation-direction: reverse;
}

.leaf:nth-child(4n) {
  animation-delay: calc(var(--delay, 0s) + 2s);
}

.leaf:nth-child(5n) svg {
  filter: drop-shadow(0 0 3px rgba(0, 255, 127, 0.4));
}

.leaf:nth-child(6n) {
  animation-duration: calc(var(--duration, 10s) * 1.3);
}

/* Responsive Design */
@media (max-width: 768px) {
  .leaf {
    transform: scale(0.7);
  }
  
  .leaf-2, .leaf-4 {
    transform: scale(0.9);
  }
  
  .floating-particle {
    width: 3px;
    height: 3px;
  }
}

@media (max-width: 480px) {
  .leaf {
    transform: scale(0.5);
  }
  
  .leaf-2, .leaf-4 {
    transform: scale(0.7);
  }
  
  .floating-particle {
    width: 2px;
    height: 2px;
  }
}

/* Performance Optimizations */
@media (prefers-reduced-motion: reduce) {
  .leaf,
  .floating-particle {
    animation-duration: 0.1s;
    animation-iteration-count: 1;
  }
}

/* High contrast mode support */
@media (prefers-contrast: high) {
  .leaf {
    color: rgba(0, 255, 127, 0.3);
  }
  
  .floating-particle {
    background: rgba(0, 255, 127, 0.5);
  }
}

/* Hover effects for interactive elements */
.leaf-background:hover .leaf {
  animation-play-state: paused;
}

/* Additional leaf variants for diversity */
.leaf:nth-child(odd) svg path:first-child {
  d: path("M10 0C14 3 16 7 16 12C16 18 13 22 10 22C7 22 4 18 4 12C4 7 6 3 10 0Z");
}

.leaf:nth-child(even) svg path:first-child {
  d: path("M10 0C16 5 19 9 19 15C19 21 15 25 10 25C5 25 1 21 1 15C1 9 4 5 10 0Z");
}

/* Seasonal color variations */
.leaf-background.autumn .leaf-1 {
  color: rgba(255, 165, 0, 0.15);
}

.leaf-background.autumn .leaf-2 {
  color: rgba(255, 69, 0, 0.12);
}

.leaf-background.autumn .leaf-3 {
  color: rgba(220, 20, 60, 0.14);
}

/* Wind effect */
@keyframes windEffect {
  0%, 100% {
    transform: translateX(0px);
  }
  50% {
    transform: translateX(30px);
  }
}

.leaf-background.windy .leaf {
  animation: 
    leafFall var(--duration, 8s) var(--delay, 0s) infinite linear,
    leafRotate var(--duration, 8s) var(--delay, 0s) infinite linear,
    windEffect 4s ease-in-out infinite;
}