/* ===== Modern Home Page with Dark Green/Blue/Black Theme ===== */
.home-hero {
  height: 100vh;
  width: 100%;
  position: relative;
  overflow: hidden;
}

.home-hero::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('../../assets/hero.jpg');
  background-size: cover;
  background-position: center;
  animation: zoomIn 15s ease-in-out infinite;
  z-index: 1;
}

.home-hero::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(0, 0, 0, 0.8), rgba(26, 26, 46, 0.7));
  z-index: 2;
}

@keyframes zoomIn {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

.home-overlay {
  position: relative;
  z-index: 3;
  color: rgba(255, 255, 255, 0.95);
  text-align: center;
  background: linear-gradient(135deg, rgba(0, 0, 0, 0.6), rgba(15, 76, 117, 0.3));
  /* backdrop-filter: blur(10px); */
  padding: 2rem;
  border-radius: 20px;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid rgba(64, 224, 208, 0.1);
}

.content {
  position: relative;
  text-align: center;
  max-width: 800px;
  padding: 2rem;
}

.content h1 {
  font-size: 3.5rem;
  font-weight: 800;
  margin-bottom: 1.5rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  line-height: 1.2;
}

.content p {
  font-size: 1.3rem;
  margin-bottom: 2.5rem;
  color: rgba(255, 255, 255, 0.9);
  line-height: 1.6;
}

/* Hero Action Buttons */
.hero-actions {
  display: flex;
  gap: 1.5rem;
  justify-content: center;
  flex-wrap: wrap;
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.75rem;
  padding: 1rem 2rem;
  border-radius: 12px;
  border: none;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  text-decoration: none;
  position: relative;
  overflow: hidden;
}

.btn-primary {
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.3);
}

.btn-primary:hover {
  transform: translateY(-3px);
  box-shadow: 0 12px 35px rgba(0, 255, 127, 0.4);
}

.btn-secondary {
  background: rgba(64, 224, 208, 0.1);
  color: #40e0d0;
  border: 2px solid rgba(64, 224, 208, 0.3);
  backdrop-filter: blur(10px);
}

.btn-secondary:hover {
  background: rgba(64, 224, 208, 0.2);
  border-color: #40e0d0;
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(64, 224, 208, 0.2);
}

/* ===== Public Family Archive Section ===== */
.public-archive-section {
  padding: 5rem 0;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 25%, #0f4c75 50%, #3282b8 75%, #0e4b4e 100%);
  position: relative;
}

.public-archive-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 20% 20%, rgba(0, 255, 127, 0.05) 0%, transparent 50%),
    radial-gradient(circle at 80% 80%, rgba(30, 144, 255, 0.05) 0%, transparent 50%);
  pointer-events: none;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  position: relative;
  z-index: 1;
}

.section-header {
  text-align: center;
  margin-bottom: 3rem;
}

.section-icon {
  color: #40e0d0;
  margin-bottom: 1rem;
}

.section-title {
  font-size: 2.5rem;
  font-weight: 800;
  margin-bottom: 1rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.section-subtitle {
  font-size: 1.1rem;
  color: rgba(255, 255, 255, 0.8);
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.6;
}

/* Archive Controls */
.archive-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 3rem;
  gap: 1.5rem;
  flex-wrap: wrap;
}

.search-box {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  flex: 1;
  max-width: 400px;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 12px;
  padding: 0 1rem;
}

.search-input {
  flex: 1;
  background: transparent;
  border: none;
  color: rgba(255, 255, 255, 0.9);
  font-size: 1rem;
  outline: none;
  padding: 0.875rem 0;
}

.search-input::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.view-all-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
  padding: 0.875rem 1.5rem;
  border-radius: 12px;
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s ease;
}

.view-all-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.3);
}

/* Loading State */
.loading-state {
  text-align: center;
  padding: 4rem 2rem;
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 20px;
  margin: 2rem 0;
}

.loading-spinner {
  color: #40e0d0;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}

@keyframes spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.loading-state p {
  color: rgba(255, 255, 255, 0.8);
  font-size: 1.1rem;
  margin: 0;
}

/* Error State */
.error-state {
  text-align: center;
  padding: 4rem 2rem;
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 69, 58, 0.3);
  border-radius: 20px;
  margin: 2rem 0;
}

.error-state p {
  color: rgba(255, 69, 58, 0.9);
  font-size: 1.1rem;
  margin-bottom: 1.5rem;
}

.retry-btn {
  background: linear-gradient(135deg, #ff453a, #ff6b6b);
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.retry-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(255, 69, 58, 0.3);
}

/* Families Grid */
.families-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}

.family-card {
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 20px;
  overflow: hidden;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
}

.family-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #00ff7f, #40e0d0, #1e90ff);
  z-index: 1;
}

.family-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 25px 50px rgba(0, 255, 127, 0.15);
  border-color: rgba(0, 255, 127, 0.4);
}

.family-image {
  position: relative;
  height: 200px;
  overflow: hidden;
}

.family-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: all 0.3s ease;
}

.family-card:hover .family-image img {
  transform: scale(1.1);
}

.family-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(to bottom, transparent, rgba(0, 0, 0, 0.8));
  display: flex;
  align-items: flex-end;
  justify-content: center;
  padding: 1.5rem;
  opacity: 0;
  transition: all 0.3s ease;
}

.family-card:hover .family-overlay {
  opacity: 1;
}

.view-family-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  transform: translateY(20px);
}

.family-card:hover .view-family-btn {
  transform: translateY(0);
}

.view-family-btn:hover {
  background: linear-gradient(135deg, #00e66f, #39d9c7);
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.4);
}

.family-info {
  padding: 1.5rem;
}

.family-name {
  font-size: 1.3rem;
  font-weight: 700;
  color: rgba(255, 255, 255, 0.95);
  margin: 0 0 0.5rem 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.family-location {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.95rem;
  margin: 0 0 1rem 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.family-stats {
  display: flex;
  gap: 1rem;
  margin-bottom: 0.75rem;
}

.stat {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.9rem;
}

.stat svg {
  color: #40e0d0;
}

.family-established {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.85rem;
}

.family-established svg {
  color: #ffd700;
}

/* Empty State */
.empty-families {
  grid-column: 1 / -1;
  text-align: center;
  padding: 4rem 2rem;
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 20px;
}

.empty-icon {
  color: #40e0d0;
  opacity: 0.6;
  margin-bottom: 1.5rem;
}

.empty-families h3 {
  font-size: 1.5rem;
  color: rgba(255, 255, 255, 0.9);
  margin: 0 0 1rem 0;
}

.empty-families p {
  color: rgba(255, 255, 255, 0.7);
  font-size: 1.1rem;
  margin: 0 0 2rem 0;
}

.cta-btn {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
  padding: 0.875rem 2rem;
  border-radius: 12px;
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s ease;
}

.cta-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.3);
}

/* Archive Footer */
.archive-footer {
  text-align: center;
  margin-top: 3rem;
  padding-top: 2rem;
  border-top: 1px solid rgba(64, 224, 208, 0.2);
}

.join-archive-btn {
  display: inline-flex;
  align-items: center;
  gap: 0.75rem;
  background: rgba(64, 224, 208, 0.1);
  color: #40e0d0;
  border: 2px solid rgba(64, 224, 208, 0.3);
  padding: 1rem 2rem;
  border-radius: 12px;
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.join-archive-btn:hover {
  background: rgba(64, 224, 208, 0.2);
  border-color: #40e0d0;
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(64, 224, 208, 0.2);
}

.search-input {
  flex: 1;
  padding: 1rem;
  border: none;
  outline: none;
