  z-index: 1;
}

.stat-card {
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  padding: 2.5rem 2rem;
  border-radius: 20px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(64, 224, 208, 0.2);
  min-width: 250px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
}

.stat-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, #00ff7f, #40e0d0, #1e90ff);
