  }
}

/* ===== Carousel Section ===== */
.carousel-section {
  padding: 5rem 2rem;
  background: linear-gradient(135deg, #0f1419 0%, #1a1a2e 50%, #16213e 100%);
  position: relative;
}

.carousel-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 40% 40%, rgba(0, 255, 127, 0.03) 0%, transparent 50%),
    radial-gradient(circle at 60% 60%, rgba(30, 144, 255, 0.03) 0%, transparent 50%);
  pointer-events: none;
}

.carousel-title {
  text-align: center;
  font-size: 2.5rem;
  font-weight: 800;
  margin-bottom: 3rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  position: relative;
  z-index: 1;
}

/* Swiper slide container */
.carousel-slide {
  position: relative;
  width: 100%;
  max-width: 900px;
  height: 400px;
  margin: 0 auto;
  border-radius: 20px;
  overflow: hidden;
  border: 1px solid rgba(64, 224, 208, 0.2);
  backdrop-filter: blur(20px);
}

/* Responsive image */
.carousel-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  transition: transform 0.3s ease;
}

.carousel-slide:hover .carousel-img {
  transform: scale(1.05);
}

/* Caption overlay */
.carousel-caption {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 1.5rem;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.8), transparent);
  backdrop-filter: blur(10px);
  color: rgba(255, 255, 255, 0.95);
  font-size: 1.1rem;
  text-align: center;
  font-weight: 500;
}

/* Responsive height */
@media (max-width: 768px) {
  .carousel-slide {
    height: 250px;
  }

  .carousel-title {
    font-size: 1.5rem;
  }

  .carousel-caption {
    font-size: 1rem;
    padding: 10px;
  }
}

/* ===== Tree Container Section ===== */
.tree-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5rem 2rem;
  width: 100%;
  background: linear-gradient(135deg, #16213e 0%, #0f4c75 50%, #1a1a2e 100%);
  position: relative;
}

.tree-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 30% 30%, rgba(64, 224, 208, 0.03) 0%, transparent 50%),
    radial-gradient(circle at 70% 70%, rgba(0, 255, 127, 0.03) 0%, transparent 50%);
  pointer-events: none;
}

.tree-title {
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 2rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-weight: 800;
  position: relative;
  z-index: 1;
}

.tree-wrapper {
  width: 100%;
  height: 500px;
  position: relative;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border-radius: 20px;
  border: 1px solid rgba(64, 224, 208, 0.2);
  z-index: 1;
}

/* Upgrade Button */
.plan-button {
  margin-top: 2rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
  border: none;
  padding: 1rem 2rem;
  border-radius: 12px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.3);
  z-index: 1;
  position: relative;
}

.plan-button:hover {
  transform: translateY(-3px);
  box-shadow: 0 12px 35px rgba(0, 255, 127, 0.4);
}

/* responsiveness */
@media (max-width: 768px) {
  .tree-wrapper {
    height: 400px;
  }
  .tree-title {
    font-size: 20px;
  }
  .plan-button {
    width: 90%;
    font-size: 16px;
  }
}

/* ===== Testimonial Section ===== */
.testimonial-section {
  margin: 0 auto;
  padding: 5rem 2rem;
  text-align: center;
  background: linear-gradient(135deg, #0f1419 0%, #1a1a2e 50%, #16213e 100%);
  position: relative;
}

.testimonial-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 25% 25%, rgba(30, 144, 255, 0.03) 0%, transparent 50%),
    radial-gradient(circle at 75% 75%, rgba(64, 224, 208, 0.03) 0%, transparent 50%);
  pointer-events: none;
}

.testimonial-title {
  font-size: 2.5rem;
  font-weight: 800;
  margin-bottom: 3rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  position: relative;
  z-index: 1;
}

.testimonial-card {
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  padding: 2.5rem;
  border-radius: 20px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(64, 224, 208, 0.2);
  width: 50% !important;
  position: relative;
}

.testimonial-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, #00ff7f, #40e0d0, #1e90ff);
  border-radius: 20px 20px 0 0;
}

.testimonial-text {
  font-size: 1.2rem;
  color: rgba(255, 255, 255, 0.9);
  margin-bottom: 1.5rem;
  line-height: 1.7;
}

/* ===== Responsive Design ===== */
@media (max-width: 768px) {
  /* Hero Section */
  .content h1 {
    font-size: 2.5rem;
  }
  
  .content p {
    font-size: 1.1rem;
  }
  
  .hero-actions {
    flex-direction: column;
    align-items: center;
    gap: 1rem;
  }
  
  .btn {
