  }
  
  /* Features Grid */
  .features-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  /* Carousel */
  .carousel-slide {
    height: 250px;
  }

  .carousel-title {
    font-size: 2rem;
  }

  .carousel-caption {
    font-size: 1rem;
    padding: 1rem;
  }
  
  /* Tree Container */
  .tree-wrapper {
    height: 400px;
  }
  
  .tree-title {
    font-size: 2rem;
  }
  
  .plan-button {
