}

/* Responsive */
@media (max-width: 768px) {
  .testimonial-card {
    padding: 25px 20px;
    width: 100%;
  }

  .testimonial-title {
    font-size: 24px;
  }

  .testimonial-text {
    font-size: 16px;
  }
}

/* ===== Story Section ===== */
.story-section {
  padding: 5rem 2rem;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 50%, #0f4c75 100%);
  position: relative;
}

.story-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 25% 25%, rgba(0, 255, 127, 0.05) 0%, transparent 50%),
    radial-gradient(circle at 75% 75%, rgba(30, 144, 255, 0.05) 0%, transparent 50%);
  pointer-events: none;
}

.story-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 4rem;
  max-width: 1200px;
  margin: 0 auto;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border-radius: 24px;
  padding: 4rem 3rem;
  border: 1px solid rgba(64, 224, 208, 0.2);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.3);
  position: relative;
  z-index: 1;
}

.story-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #00ff7f, #40e0d0, #1e90ff);
  border-radius: 24px 24px 0 0;
}

.story-text {
  flex: 1;
  padding-right: 2rem;
}

.story-quote {
  font-size: 1.8rem;
  font-weight: 700;
  font-style: italic;
  margin-bottom: 2rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  line-height: 1.4;
  position: relative;
}

.story-quote::before {
  content: '"';
  position: absolute;
  left: -1.5rem;
  top: -0.5rem;
  font-size: 3rem;
  color: rgba(64, 224, 208, 0.3);
  font-family: serif;
}

.story-quote::after {
  content: '"';
  position: absolute;
  right: -1rem;
  bottom: -1rem;
  font-size: 3rem;
  color: rgba(64, 224, 208, 0.3);
  font-family: serif;
}

.story-paragraph {
  margin-bottom: 1.5rem;
  line-height: 1.7;
  color: rgba(255, 255, 255, 0.8);
  font-size: 1.1rem;
}

.story-cta-btn {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 1rem 2rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
  text-decoration: none;
  border: none;
  border-radius: 12px;
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0 4px 15px rgba(0, 255, 127, 0.3);
  margin-top: 1rem;
}

.story-cta-btn:hover {
  background: linear-gradient(135deg, #00cc64, #1e90ff);
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.4);
  color: white;
}

.story-image {
  flex: 1;
  text-align: center;
  position: relative;
}

.story-image img {
  max-width: 100%;
  height: auto;
  border-radius: 20px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.4);
  border: 2px solid rgba(64, 224, 208, 0.3);
  transition: all 0.3s ease;
}

.story-image img:hover {
  transform: scale(1.05);
  box-shadow: 0 25px 50px rgba(0, 255, 127, 0.15);
  border-color: rgba(0, 255, 127, 0.5);
}

/* Story Section Responsive Design */
@media (max-width: 1024px) {
  .story-container {
    gap: 3rem;
    padding: 3rem 2.5rem;
  }
  
  .story-text {
    padding-right: 1rem;
  }
}

@media (max-width: 768px) {
  .story-section {
    padding: 4rem 1.5rem;
  }
  
  .story-container {
    flex-direction: column;
    gap: 2.5rem;
    padding: 3rem 2rem;
    text-align: center;
  }
  
  .story-text {
    padding-right: 0;
  }
  
  .story-quote {
    font-size: 1.5rem;
  }
  
  .story-quote::before,
  .story-quote::after {
    display: none;
  }
}

@media (max-width: 480px) {
  .story-section {
    padding: 3rem 1rem;
  }
  
  .story-container {
    padding: 2rem 1.5rem;
    gap: 2rem;
  }
  
  .story-quote {
    font-size: 1.3rem;
    margin-bottom: 1.5rem;
  }
  
  .story-paragraph {
    font-size: 1rem;
    margin-bottom: 1.25rem;
  }
  
  .story-cta-btn {
    padding: 0.875rem 1.75rem;
    font-size: 0.95rem;
    width: 100%;
    justify-content: center;
  }
}
