/* Modern Auth Container with Green, Blue, Black Theme */
.login-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 25%, #0f4c75 50%, #3282b8 75%, #0e4b4e 100%);
  padding: 1rem;
  position: relative;
  overflow: hidden;
}

.login-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 25% 25%, rgba(0, 255, 127, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 75% 75%, rgba(64, 224, 208, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 50% 50%, rgba(30, 144, 255, 0.05) 0%, transparent 50%);
  pointer-events: none;
}

/* Modern Card Design */
.login-form,
.auth-form {
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border-radius: 24px;
  padding: 3rem 2.5rem;
  width: 100%;
  max-width: 440px;
  box-shadow: 
    0 25px 50px rgba(0, 0, 0, 0.3),
    0 0 0 1px rgba(64, 224, 208, 0.2),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(64, 224, 208, 0.2);
  position: relative;
  z-index: 1;
  transform: translateY(20px);
  opacity: 0;
  animation: slideUp 0.8s cubic-bezier(0.4, 0, 0.2, 1) forwards;
}

.login-form h2 {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
  text-align: center;
  color: #ffffff;
  background: linear-gradient(135deg, #00ff7f, #40e0d0, #1e90ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.login-form::after {
  content: '';
  position: absolute;
  top: 0;
  left: 50%;
  width: 60px;
  height: 4px;
  background: linear-gradient(90deg, #00ff7f, #40e0d0, #1e90ff);
  border-radius: 2px;
  transform: translateX(-50%);
}

/* Form Styling */
.login-form form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  margin-top: 2rem;
}

/* Input Groups */
.input-group {
  position: relative;
  display: flex;
  align-items: center;
}

.login-form input {
  width: 100%;
  padding: 1rem 1rem 1rem 3.5rem;
  border: 2px solid rgba(64, 224, 208, 0.3);
  border-radius: 16px;
  background: rgba(0, 0, 0, 0.4);
  font-size: 1rem;
  color: #ffffff;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  outline: none;
}

.login-form input:focus {
  border-color: #00ff7f;
  background: rgba(0, 0, 0, 0.6);
  box-shadow: 0 0 0 4px rgba(0, 255, 127, 0.1);
  transform: translateY(-2px);
}

.login-form input::placeholder {
  color: rgba(255, 255, 255, 0.6);
  font-weight: 500;
}

/* Icons */
.icon {
  position: absolute;
  left: 1.2rem;
  color: #40e0d0;
  font-size: 1.1rem;
  z-index: 2;
}

/* Buttons */
.login-form button {
  padding: 1rem 2rem;
  background: linear-gradient(135deg, #00ff7f 0%, #40e0d0 50%, #1e90ff 100%);
  color: #000000;
  border: none;
  border-radius: 16px;
  font-weight: 600;
  font-size: 1.1rem;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
}

.login-form button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 10px 30px rgba(0, 255, 127, 0.4);
}

.login-form button:active {
  transform: translateY(0);
}

.login-form button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  background: rgba(255, 255, 255, 0.2);
  color: rgba(255, 255, 255, 0.6);
}

.login-form button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transition: left 0.6s;
}

.login-form button:hover::before {
  left: 100%;
}

/* Links and Text */
.forgot-password {
  text-align: right;
  margin-bottom: 0.5rem;
}

.forgot-password a {
  color: #40e0d0;
  text-decoration: none;
  font-weight: 500;
  font-size: 0.9rem;
  transition: all 0.3s ease;
}

.forgot-password a:hover {
  color: #00ff7f;
  text-decoration: underline;
}

.rlogin {
  text-align: center;
  margin-top: 1.5rem;
  color: rgba(255, 255, 255, 0.7);
  font-weight: 500;
}

.linkre {
  color: #40e0d0;
  text-decoration: none;
  font-weight: 600;
  margin-left: 0.5rem;
  transition: all 0.3s ease;
}

.linkre:hover {
  color: #00ff7f;
  text-decoration: underline;
}

/* Error Message */
.error-message {
  background: linear-gradient(135deg, rgba(220, 53, 69, 0.2), rgba(139, 69, 19, 0.1));
  color: #ff6b6b;
  padding: 1rem;
  border-radius: 12px;
  border: 1px solid rgba(220, 53, 69, 0.3);
  margin-bottom: 1rem;
  font-weight: 500;
  text-align: center;
  backdrop-filter: blur(10px);
}

/* Success Message */
.success-message {
  background: linear-gradient(135deg, rgba(0, 255, 127, 0.2), rgba(64, 224, 208, 0.1));
  color: #00ff7f;
  padding: 1rem;
  border-radius: 12px;
  border: 1px solid rgba(0, 255, 127, 0.3);
  margin-bottom: 1rem;
  font-weight: 500;
  text-align: center;
  backdrop-filter: blur(10px);
}

/* Responsive Design */
@media (max-width: 768px) {
  .login-container {
    padding: 1rem;
  }
  
  .login-form,
  .auth-form {
    padding: 2rem 1.5rem;
    margin: 1rem;
  }
  
  .login-form h2 {
    font-size: 1.75rem;
  }
}

/* Animations */
@keyframes slideUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes float {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-10px); }
}

.login-form {
  animation: slideUp 0.8s cubic-bezier(0.4, 0, 0.2, 1) forwards;
}

/* Additional dark theme elements */
.parent {
  /* min-height: 100vh; */
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 25%, #0f4c75 50%, #3282b8 75%, #0e4b4e 100%);
  padding: 1rem;
}
