/* ===== Modern Navbar with Green, Blue, Black Theme ===== */
.navbar {
  background: rgba(0, 0, 0, 0.85);
  backdrop-filter: blur(25px);
  border-bottom: 1px solid rgba(64, 224, 208, 0.3);
  padding: 1rem 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: 
    0 8px 32px rgba(0, 0, 0, 0.4),
    0 0 0 1px rgba(0, 255, 127, 0.1),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
  position: sticky;
  top: 0;
  z-index: 1000;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  margin: 0;
  margin-top: 0;
}

.navbar::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, #00ff7f, #40e0d0, #1e90ff, #00ff7f);
  animation: glowing-border 3s linear infinite;
}

@keyframes glowing-border {
  0% { background-position: 0% 50%; }
  100% { background-position: 200% 50%; }
}

/* Logo Section */
.navbar-logo {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  text-decoration: none;
  transition: all 0.3s ease;
}

.navbar-logo:hover {
  transform: translateX(3px);
}

.navbar-logo-icon {
  font-size: 2rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  filter: drop-shadow(0 0 10px rgba(0, 255, 127, 0.5));
  transition: all 0.3s ease;
}

.navbar-logo:hover .navbar-logo-icon {
  transform: rotate(15deg) scale(1.1);
  filter: drop-shadow(0 0 15px rgba(0, 255, 127, 0.8));
}

.navbar-logo-text {
  font-weight: 800;
  font-size: 1.5rem;
  background: linear-gradient(135deg, #ffffff, #40e0d0, #00ff7f);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-shadow: 0 0 20px rgba(64, 224, 208, 0.3);
}

/* Navigation Items */
.navbar-nav {
  display: flex;
  align-items: center;
  gap: 2rem;
}

.navbar-nav-item {
  color: rgba(255, 255, 255, 0.8);
  text-decoration: none;
  font-weight: 500;
  font-size: 1rem;
  padding: 0.5rem 1rem;
  border-radius: 12px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
}

.navbar-nav-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(0, 255, 127, 0.2), transparent);
  transition: left 0.6s;
}

.navbar-nav-item:hover::before {
  left: 100%;
}

.navbar-nav-item:hover {
  color: #00ff7f;
  background: rgba(0, 255, 127, 0.1);
  border: 1px solid rgba(0, 255, 127, 0.3);
  box-shadow: 0 4px 15px rgba(0, 255, 127, 0.2);
  transform: translateY(-2px);
}

.navbar-nav-item.active {
  color: #00ff7f;
  background: rgba(0, 255, 127, 0.15);
  border: 1px solid rgba(0, 255, 127, 0.4);
  box-shadow: 0 0 20px rgba(0, 255, 127, 0.3);
}

/* Actions Section */
.navbar-actions {
  display: flex;
  align-items: center;
  gap: 1rem;
}

/* Language Selector */
.language-select {
  padding: 0.5rem 1rem;
  border: 2px solid rgba(64, 224, 208, 0.3);
  border-radius: 12px;
  background: rgba(0, 0, 0, 0.5);
  color: #ffffff;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.language-select:hover {
  border-color: #40e0d0;
  background: rgba(64, 224, 208, 0.1);
  box-shadow: 0 0 15px rgba(64, 224, 208, 0.3);
}

.language-select option {
  background: #1a1a2e;
  color: #ffffff;
}

/* Profile Button */
.profile-button {
  border: none !important;
  cursor: pointer;
  display: flex;
  align-items: center;
  background: rgba(0, 0, 0, 0.7) !important;
  gap: 0.5rem;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  border: 2px solid rgba(64, 224, 208, 0.2);
}

.profile-button:hover {
  transform: translateY(-2px);
}

.profile-avatar {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 0 !important;
  color: #000000;
  font-weight: 700;
  border: 2px solid rgba(255, 255, 255, 0.2);
}

.profile-chevron {
  color: #40e0d0;
  transition: all 0.3s ease;
}

.profile-button:hover .profile-chevron {
  color: #00ff7f;
  transform: rotate(180deg);
}

/* Profile Dropdown */
.profile-dropdown {
  position: absolute;
  top: calc(100% + 0.5rem);
  right: 0;
  background: rgba(0, 0, 0, 0.9);
  backdrop-filter: blur(25px);
  border: 1px solid rgba(64, 224, 208, 0.3);
  border-radius: 16px;
  width: 240px;
  box-shadow: 
    0 20px 40px rgba(0, 0, 0, 0.5),
    0 0 0 1px rgba(0, 255, 127, 0.1),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
  overflow: hidden;
  z-index: 1001;
}

.profile-dropdown::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, transparent, #00ff7f, transparent);
}

.dropdown-item {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1rem 1.25rem;
  text-decoration: none;
  color: rgba(255, 255, 255, 0.8);
  font-weight: 500;
  transition: all 0.3s ease;
  border-bottom: 1px solid rgba(64, 224, 208, 0.1);
}

.dropdown-item:hover {
  background: rgba(0, 255, 127, 0.1);
  color: #00ff7f;
  padding-left: 1.5rem;
}

.dropdown-item:last-child {
  border-bottom: none;
}

.dropdown-item.logout {
  color: rgba(255, 100, 100, 0.8);
}

.dropdown-item.logout:hover {
  background: rgba(255, 100, 100, 0.1);
  color: #ff6464;
}

.dropdown-separator {
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(64, 224, 208, 0.3), transparent);
  margin: 0.25rem 0;
}

/* Mobile Menu Button */
.mobile-menu-button {
  display: none;
  padding: 0.5rem;
  border: none;
  background: rgba(64, 224, 208, 0.1);
  border-radius: 8px;
  cursor: pointer;
  color: #40e0d0;
  transition: all 0.3s ease;
}

.mobile-menu-button:hover {
  background: rgba(64, 224, 208, 0.2);
  color: #00ff7f;
}

/* Mobile Navigation */
.mobile-nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.95);
  backdrop-filter: blur(25px);
  z-index: 2000;
  display: flex;
  flex-direction: column;
  padding: 2rem;
  transform: translateX(-100%);
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.mobile-nav.open {
  transform: translateX(0);
}

.mobile-nav-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid rgba(64, 224, 208, 0.3);
}

.mobile-nav-close {
  padding: 0.5rem;
  border: none;
  background: rgba(255, 100, 100, 0.1);
  border-radius: 8px;
  color: #ff6464;
  cursor: pointer;
}

.mobile-nav-items {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.mobile-nav-item {
  color: rgba(255, 255, 255, 0.8);
  text-decoration: none;
  font-weight: 500;
  font-size: 1.1rem;
  padding: 1rem;
  border-radius: 12px;
  transition: all 0.3s ease;
}

.mobile-nav-item:hover,
.mobile-nav-item.active {
  background: rgba(0, 255, 127, 0.1);
  color: #00ff7f;
  border: 1px solid rgba(0, 255, 127, 0.3);
}

/* Responsive Design */
@media (max-width: 768px) {
  .navbar {
    padding: 0.75rem 1rem;
  }
  
  .navbar-nav {
    display: none;
  }
  
  .mobile-menu-button {
    display: block;
  }
  
  .navbar-logo-text {
    font-size: 1.25rem;
  }
  
  .profile-dropdown {
    width: 200px;
    right: 0.5rem;
  }
}

@media (max-width: 480px) {
  .navbar {
    padding: 0.5rem 0.75rem;
  }
  
  .navbar-actions {
    gap: 0.5rem;
  }
  
  .navbar-logo-text {
    font-size: 1.1rem;
  }
  
  .profile-dropdown {
    width: 180px;
  }
}

/* Accessibility */
@media (prefers-reduced-motion: reduce) {
  .navbar,
  .navbar-logo,
  .navbar-nav-item,
  .profile-button {
    transition: none;
  }
  
  .navbar::before {
    animation: none;
  }
}