/* ===== Modern Members Page with Dark Green/Blue/Black Theme ===== */
.members-page {
  padding: 2rem;
  max-width: 1400px;
  margin: 0 auto;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 25%, #0f4c75 50%, #3282b8 75%, #0e4b4e 100%);
  min-height: 100vh;
  position: relative;
}

.members-page::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 20% 20%, rgba(0, 255, 127, 0.05) 0%, transparent 50%),
    radial-gradient(circle at 80% 80%, rgba(30, 144, 255, 0.05) 0%, transparent 50%);
  pointer-events: none;
}

/* Page Header */
.page-header {
  margin-bottom: 2rem;
  position: relative;
  z-index: 1;
}

.page-title {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin: 0 0 1.5rem 0;
  font-weight: 800;
  font-size: 2.5rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.text-muted {
  color: rgba(255, 255, 255, 0.6);
  font-weight: 500;
  font-size: 1.1rem;
}

.controls-container {
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  align-items: center;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border-radius: 16px;
  padding: 1.5rem;
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(64, 224, 208, 0.2);
}

.search-container {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  flex: 1;
  min-width: 280px;
  background: rgba(64, 224, 208, 0.05);
  border: 2px solid rgba(64, 224, 208, 0.2);
  border-radius: 12px;
  padding: 0 1rem;
  transition: all 0.3s ease;
}

.search-container:focus-within {
  border-color: #00ff7f;
  background: rgba(0, 255, 127, 0.1);
  box-shadow: 0 0 0 4px rgba(0, 255, 127, 0.1);
}

.search-input {
  flex: 1;
  padding: 1rem;
  border: none;
  outline: none;
  background: transparent;
  font-size: 1rem;
  color: rgba(255, 255, 255, 0.9);
}

.search-input::placeholder {
  color: rgba(255, 255, 255, 0.6);
  font-weight: 500;
}

.filter-container {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  background: rgba(30, 144, 255, 0.1);
  border: 2px solid rgba(30, 144, 255, 0.2);
  border-radius: 12px;
  padding: 0 1rem;
}

.filter-select {
  padding: 1rem;
  border: none;
  outline: none;
  background: transparent;
  font-size: 1rem;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.9);
  cursor: pointer;
}

.filter-select option {
  background: rgba(0, 0, 0, 0.9);
  color: rgba(255, 255, 255, 0.9);
}

.actions-container {
  display: flex;
  gap: 0.75rem;
}

/* Modern Buttons */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  border-radius: 12px;
  border: none;
  font-size: 0.95rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  text-decoration: none;
  position: relative;
  overflow: hidden;
}

.btn:hover {
  transform: translateY(-2px);
}

.btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.15), transparent);
  transition: left 0.6s;
}

.btn:hover::before {
  left: 100%;
}

.btn-primary {
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.3);
}

.btn-primary:hover {
  box-shadow: 0 12px 35px rgba(0, 255, 127, 0.4);
}

.btn-secondary {
  background: rgba(64, 224, 208, 0.1);
  color: #40e0d0;
  border: 2px solid rgba(64, 224, 208, 0.2);
}

.btn-secondary:hover {
  background: rgba(64, 224, 208, 0.2);
  box-shadow: 0 8px 25px rgba(64, 224, 208, 0.2);
}

.btn-danger {
  background: rgba(231, 76, 60, 0.1);
  color: #e74c3c;
  border: 2px solid rgba(231, 76, 60, 0.2);
}

.btn-danger:hover {
  background: rgba(231, 76, 60, 0.15);
  box-shadow: 0 8px 25px rgba(231, 76, 60, 0.2);
}

.btn-icon {
  padding: 0.75rem;
}

.btn-icon span {
  display: none;
}

/* Members Grid */
.members-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: 1.5rem;
  margin-top: 2rem;
  position: relative;
  z-index: 1;
}

.member-card {
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(64, 224, 208, 0.2);
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
}

.member-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #00ff7f, #40e0d0, #1e90ff);
  border-radius: 20px 20px 0 0;
}

.member-card:hover {
  transform: translateY(-5px) scale(1.02);
  box-shadow: 0 25px 50px rgba(0, 255, 127, 0.15);
  border-color: rgba(0, 255, 127, 0.4);
}

.member-info {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.member-avatar {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  overflow: hidden;
  flex-shrink: 0;
  border: 3px solid rgba(64, 224, 208, 0.3);
  transition: all 0.3s ease;
}

.member-card:hover .member-avatar {
  border-color: #00ff7f;
  transform: scale(1.1);
}

.member-avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.avatar-placeholder {
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  color: white;
  font-weight: 700;
  font-size: 1.2rem;
}

.member-details {
  flex: 1;
  min-width: 0;
}

.member-name {
  margin: 0;
  font-size: 1.3rem;
  font-weight: 700;
  color: rgba(255, 255, 255, 0.95);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.member-meta {
  margin: 0.5rem 0 0 0;
  font-size: 1rem;
  color: rgba(255, 255, 255, 0.7);
  font-weight: 500;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.member-actions {
  display: flex;
  gap: 0.75rem;
  justify-content: flex-end;
  padding-top: 1rem;
  border-top: 1px solid rgba(64, 224, 208, 0.2);
}

/* Empty State */
.empty-state {
  grid-column: 1 / -1;
  text-align: center;
  padding: 4rem 1rem;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border-radius: 20px;
  border: 1px solid rgba(64, 224, 208, 0.2);
}

.empty-icon {
  margin-bottom: 1.5rem;
  opacity: 0.6;
  color: #40e0d0;
}

.empty-state h3 {
  margin: 0 0 1rem 0;
  color: rgba(255, 255, 255, 0.9);
  font-size: 1.5rem;
  font-weight: 700;
}

.empty-state p {
  margin: 0 0 2rem 0;
  color: rgba(255, 255, 255, 0.7);
  font-size: 1.1rem;
}

/* Modals */
.modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(4px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 50;
  padding: 1rem;
}

.modal {
  background: rgba(0, 0, 0, 0.9);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 16px;
  width: 100%;
  max-width: 600px;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.5);
}

.modal-large {
  max-width: 900px;
  width: 95%;
}

.modal-scrollable {
  overflow-y: auto;
  max-height: 70vh;
}

.modal-scrollable::-webkit-scrollbar {
  width: 6px;
}

.modal-scrollable::-webkit-scrollbar-track {
  background: rgba(64, 224, 208, 0.1);
  border-radius: 3px;
}

.modal-scrollable::-webkit-scrollbar-thumb {
  background: rgba(64, 224, 208, 0.5);
  border-radius: 3px;
}

.modal-scrollable::-webkit-scrollbar-thumb:hover {
  background: rgba(64, 224, 208, 0.7);
}

.modal-sm {
  max-width: 400px;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 1.5rem 0;
  margin-bottom: 1rem;
}

.modal-header h3 {
  margin: 0;
  font-size: 1.25rem;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.9);
}

.modal-close {
  background: rgba(64, 224, 208, 0.1);
  border: 1px solid rgba(64, 224, 208, 0.2);
  font-size: 1.5rem;
  cursor: pointer;
  color: #40e0d0;
  padding: 0;
  width: 35px;
  height: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  transition: all 0.3s ease;
}

.modal-close:hover {
  background: rgba(64, 224, 208, 0.2);
  transform: scale(1.1);
}

.modal-body {
  padding: 0 1.5rem;
  color: rgba(255, 255, 255, 0.9);
}

.modal-footer {
  display: flex;
  justify-content: flex-end;
  gap: 0.75rem;
  padding: 1.5rem;
  border-top: 1px solid rgba(64, 224, 208, 0.2);
  margin-top: 1rem;
}

/* Enhanced Form Sections */
.form-section {
  margin-bottom: 2rem;
  padding: 1.5rem;
  background: rgba(64, 224, 208, 0.02);
  border: 1px solid rgba(64, 224, 208, 0.1);
  border-radius: 12px;
  position: relative;
}

.form-section:last-child {
  margin-bottom: 0;
}

.section-title {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  margin: 0 0 1.5rem 0;
  font-size: 1.1rem;
  font-weight: 600;
  color: #40e0d0;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.section-title::after {
  content: '';
  flex: 1;
  height: 1px;
  background: linear-gradient(90deg, rgba(64, 224, 208, 0.3), transparent);
}

/* Enhanced Form Groups */
.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.form-group label {
  font-size: 0.9rem;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 0.25rem;
}

/* Checkbox Styling */
.checkbox-label {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  cursor: pointer;
  user-select: none;
  padding: 0.5rem 0;
}

.checkbox-label input[type="checkbox"] {
  appearance: none;
  width: 20px;
  height: 20px;
  border: 2px solid rgba(64, 224, 208, 0.4);
  border-radius: 4px;
  background: rgba(0, 0, 0, 0.5);
  cursor: pointer;
  position: relative;
  transition: all 0.3s ease;
}

.checkbox-label input[type="checkbox"]:checked {
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  border-color: #00ff7f;
}

.checkbox-label input[type="checkbox"]:checked::after {
  content: '\2713';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-size: 14px;
  font-weight: bold;
}

.checkbox-label span {
  font-weight: 500;
  color: rgba(255, 255, 255, 0.8);
}

/* Enhanced Form Input */
.form-input[required] {
  border-left: 4px solid #00ff7f;
}

.form-input[dir="rtl"] {
  text-align: right;
  direction: rtl;
}

.form-input[dir="ltr"] {
  text-align: left;
  direction: ltr;
}

/* Forms */
.form-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
}

.form-input {
  padding: 0.75rem;
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 8px;
  font-size: 0.95rem;
  width: 100%;
  box-sizing: border-box;
  background: rgba(255, 255, 255, 0.05);
  color: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(10px);
}

.form-input:focus {
  outline: none;
  border-color: #00ff7f;
  background: rgba(0, 255, 127, 0.1);
  box-shadow: 0 0 0 3px rgba(0, 255, 127, 0.1);
}

.form-input::placeholder {
  color: rgba(255, 255, 255, 0.6);
}

/* Upload */
.upload-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  padding: 1rem 0;
}

.image-preview {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  overflow: hidden;
  border: 2px solid rgba(64, 224, 208, 0.3);
}

.image-preview img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.preview-placeholder {
  width: 100%;
  height: 100%;
  background: rgba(64, 224, 208, 0.1);
  display: flex;
  align-items: center;
  justify-content: center;
  color: #40e0d0;
}

.file-input-label {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.25rem;
  border: 1px dashed rgba(64, 224, 208, 0.4);
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
  color: rgba(255, 255, 255, 0.8);
  background: rgba(64, 224, 208, 0.05);
}

.file-input-label:hover {
  border-color: #00ff7f;
  color: #00ff7f;
  background: rgba(0, 255, 127, 0.1);
  transform: translateY(-2px);
}

.file-input-label input {
  display: none;
}

.upload-actions {
  display: flex;
  gap: 0.75rem;
}

/* Responsive Design */
@media (max-width: 768px) {
  .members-page {
    padding: 0.5rem;
  }
  
  .controls-container {
    flex-direction: column;
    align-items: stretch;
  }
  
  .search-container,
  .filter-container {
    min-width: auto;
  }
  
  .actions-container {
    justify-content: center;
  }
  
  .members-grid {
    grid-template-columns: 1fr;
  }
  
  .member-actions {
    flex-wrap: wrap;
  }
  
  .btn-icon {
    padding: 0.5rem;
  }
  
  .btn-icon span {
    display: inline;
  }
  
  .modal {
    margin: 0.5rem;
  }
  
  .form-grid {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 480px) {
  .page-title {
    font-size: 1.25rem;
  }
  
  .modal-header {
    padding: 1rem 1rem 0;
  }
  
  .modal-body {
    padding: 0 1rem;
  }
  
  .modal-footer {
    padding: 1rem;
    flex-direction: column;
  }
  
  .modal-footer .btn {
    width: 100%;
    justify-content: center;
  }
}

/* RTL Support for Arabic */
[dir="rtl"] .page-title,
[dir="rtl"] .member-name {
  text-align: right;
}

[dir="rtl"] .search-container,
[dir="rtl"] .filter-container {
  flex-direction: row-reverse;
}

[dir="rtl"] .modal-header {
  flex-direction: row-reverse;
}

[dir="rtl"] .modal-footer {
  flex-direction: row-reverse;
}

/* Modern Animations and Effects */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideInLeft {
  from {
    opacity: 0;
    transform: translateX(-30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes pulse {
  0%, 100% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.05);
    opacity: 0.9;
  }
}

.member-card {
  animation: fadeInUp 0.6s ease-out;
}

.member-card:nth-child(1) { animation-delay: 0.1s; }
.member-card:nth-child(2) { animation-delay: 0.2s; }
.member-card:nth-child(3) { animation-delay: 0.3s; }
.member-card:nth-child(4) { animation-delay: 0.4s; }
.member-card:nth-child(5) { animation-delay: 0.5s; }
.member-card:nth-child(6) { animation-delay: 0.6s; }

.controls-container {
  animation: slideInLeft 0.8s ease-out;
}

/* Icon Colors */
.search-container svg,
.filter-container svg {
  color: #40e0d0;
}

/* Enhanced Glow Effects */
.member-card:hover {
  box-shadow: 
    0 25px 50px rgba(0, 255, 127, 0.15),
    0 0 30px rgba(64, 224, 208, 0.1);
}

.btn-primary:hover {
  box-shadow: 
    0 12px 35px rgba(0, 255, 127, 0.4),
    0 0 20px rgba(0, 255, 127, 0.2);
}

/* Loading Animation for Empty State */
.empty-icon {
  animation: pulse 2s ease-in-out infinite;
}