/* ===== Saudi Arabic Typography Enhancements ===== */

/* Font Face Declarations for Local Saudi Fonts */
@font-face {
  font-family: 'Saudi';
  src: local('Tajawal'), local('Noto Kufi Arabic'), local('IBM Plex Sans Arabic');
  font-display: swap;
}

/* Arabic Font Variables */
:root {
  --saudi-font-primary: 'Tajawal', 'Noto Kufi Arabic', 'IBM Plex Sans Arabic', 'Arial', sans-serif;
  --saudi-font-secondary: 'Noto Kufi Arabic', 'Tajawal', 'IBM Plex Sans Arabic', 'Arial', sans-serif;
  --saudi-font-display: 'IBM Plex Sans Arabic', 'Tajawal', 'Noto Kufi Arabic', 'Arial', sans-serif;
}

/* Arabic Text Base Styling */
.arabic-content {
  font-family: var(--saudi-font-primary);
  direction: rtl;
  text-align: right;
  line-height: 1.8;
  letter-spacing: 0.02em;
}

/* Arabic Headlines and Titles */
.arabic-headline {
  font-family: var(--saudi-font-display);
  font-weight: 800;
  line-height: 1.3;
  letter-spacing: 0.01em;
}

.arabic-title {
  font-family: var(--saudi-font-display);
  font-weight: 700;
  line-height: 1.4;
}

.arabic-subtitle {
  font-family: var(--saudi-font-primary);
  font-weight: 500;
  line-height: 1.6;
  opacity: 0.9;
}

/* Arabic Body Text */
.arabic-body {
  font-family: var(--saudi-font-primary);
  font-weight: 400;
  line-height: 1.8;
  letter-spacing: 0.015em;
}

.arabic-body-light {
  font-family: var(--saudi-font-primary);
  font-weight: 300;
  line-height: 1.8;
}

.arabic-body-medium {
  font-family: var(--saudi-font-primary);
  font-weight: 500;
  line-height: 1.7;
}

/* Arabic Navigation Text */
.arabic-nav {
  font-family: var(--saudi-font-primary);
  font-weight: 500;
  letter-spacing: 0.02em;
}

/* Arabic Button Text */
.arabic-button {
  font-family: var(--saudi-font-primary);
  font-weight: 600;
  letter-spacing: 0.03em;
  text-transform: none;
}

/* Arabic Form Labels and Inputs */
.arabic-label {
  font-family: var(--saudi-font-primary);
  font-weight: 500;
  line-height: 1.5;
}

.arabic-input {
  font-family: var(--saudi-font-primary);
  font-weight: 400;
  direction: rtl;
  text-align: right;
}

.arabic-input::placeholder {
  font-family: var(--saudi-font-primary);
  font-weight: 300;
  opacity: 0.7;
}

/* Font Weight Specific Classes for Arabic */
.saudi-thin {
  font-family: var(--saudi-font-primary);
  font-weight: 200;
}

.saudi-light {
  font-family: var(--saudi-font-primary);
  font-weight: 300;
}

.saudi-regular {
  font-family: var(--saudi-font-primary);
  font-weight: 400;
}

.saudi-medium {
  font-family: var(--saudi-font-primary);
  font-weight: 500;
}

.saudi-semibold {
  font-family: var(--saudi-font-primary);
  font-weight: 600;
}

.saudi-bold {
  font-family: var(--saudi-font-primary);
  font-weight: 700;
}

.saudi-extrabold {
  font-family: var(--saudi-font-primary);
  font-weight: 800;
}

.saudi-black {
  font-family: var(--saudi-font-primary);
  font-weight: 900;
}

/* Enhanced Typography for Different Content Types */
.arabic-card-title {
  font-family: var(--saudi-font-display);
  font-weight: 700;
  line-height: 1.3;
  margin-bottom: 0.5rem;
}

.arabic-card-text {
  font-family: var(--saudi-font-primary);
  font-weight: 400;
  line-height: 1.7;
  opacity: 0.9;
}

.arabic-footer-text {
  font-family: var(--saudi-font-primary);
  font-weight: 400;
  line-height: 1.6;
  font-size: 0.95rem;
}

.arabic-legal-text {
  font-family: var(--saudi-font-secondary);
  font-weight: 400;
  line-height: 1.8;
  text-align: justify;
}

/* Responsive Arabic Typography */
@media (max-width: 768px) {
  .arabic-content {
    line-height: 1.9;
    font-size: 0.95rem;
  }
  
  .arabic-headline {
    line-height: 1.2;
  }
  
  .arabic-title {
    line-height: 1.3;
  }
}

@media (max-width: 480px) {
  .arabic-content {
    line-height: 2.0;
    font-size: 0.9rem;
  }
  
  .arabic-body {
    line-height: 1.9;
  }
}

/* Arabic Text Selection Styling */
.arabic-content ::selection {
  background: rgba(0, 255, 127, 0.2);
  color: inherit;
}

.arabic-content ::-moz-selection {
  background: rgba(0, 255, 127, 0.2);
  color: inherit;
}

/* Enhanced Arabic Readability */
.arabic-readable {
  font-family: var(--saudi-font-primary);
  font-weight: 400;
  line-height: 2.0;
  letter-spacing: 0.02em;
  word-spacing: 0.05em;
}

/* Arabic Quote Styling */
.arabic-quote {
  font-family: var(--saudi-font-display);
  font-weight: 500;
  font-style: normal;
  line-height: 1.8;
  position: relative;
}

.arabic-quote::before {
  content: '';
  font-size: 1.2em;
  color: #40e0d0;
  margin-left: 0.2em;
}

.arabic-quote::after {
  content: '';
  font-size: 1.2em;
  color: #40e0d0;
  margin-right: 0.2em;
}

/* Arabic Numbers Styling */
.arabic-numbers {
  font-feature-settings: 'lnum' 1;
  font-variant-numeric: lining-nums;
}

/* Enhanced Arabic Link Styling */
.arabic-link {
  font-family: var(--saudi-font-primary);
  font-weight: 500;
  text-decoration: none;
  border-bottom: 1px solid transparent;
  transition: all 0.3s ease;
}

.arabic-link:hover {
  border-bottom-color: #40e0d0;
}