/* ===== Tree Viewer Component with Dark Glass Morphism Theme ===== */
.tree-viewer-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 25%, #0f4c75 50%, #3282b8 75%, #0e4b4e 100%);
  position: relative;
}

.tree-viewer-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 20% 20%, rgba(0, 255, 127, 0.05) 0%, transparent 50%),
    radial-gradient(circle at 80% 80%, rgba(30, 144, 255, 0.05) 0%, transparent 50%);
  pointer-events: none;
}

.tree-viewer-content {
  position: relative;
  z-index: 1;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* Loading State */
.tree-viewer-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  color: rgba(255, 255, 255, 0.8);
}

/* Error State */
.tree-viewer-error {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  text-align: center;
  color: rgba(255, 255, 255, 0.8);
  background: rgba(255, 69, 58, 0.1);
  border: 1px solid rgba(255, 69, 58, 0.3);
  border-radius: 16px;
  margin: 2rem;
  padding: 2rem;
}

.tree-viewer-error h2 {
  color: #ff453a;
  margin-bottom: 1rem;
}

.tree-viewer-error p {
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 2rem;
}

.retry-btn {
  background: rgba(255, 69, 58, 0.2);
  color: #ff453a;
  border: 1px solid rgba(255, 69, 58, 0.3);
  padding: 0.75rem 1.5rem;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
  margin: 0.5rem;
  font-size: 0.9rem;
  font-weight: 600;
}

.retry-btn:hover {
  background: rgba(255, 69, 58, 0.3);
  transform: translateY(-2px);
}

.loading-icon {
  color: #40e0d0;
  animation: pulse 2s infinite;
  margin-bottom: 1rem;
}

/* Not Found State */
.tree-not-found {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  text-align: center;
  color: rgba(255, 255, 255, 0.8);
}

.back-to-trees-btn {
  margin-top: 2rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
  border: none;
  padding: 1rem 2rem;
  border-radius: 16px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

/* Header Section */
.tree-viewer-header {
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(20px);
  border-bottom: 1px solid rgba(64, 224, 208, 0.2);
  padding: 1.5rem 2rem;
  position: sticky;
  top: 0;
  z-index: 100;
}

.header-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
}

.back-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(64, 224, 208, 0.2);
  color: rgba(255, 255, 255, 0.9);
  padding: 0.75rem 1.5rem;
  border-radius: 12px;
  font-size: 0.95rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.back-btn:hover {
  background: rgba(64, 224, 208, 0.1);
  border-color: #40e0d0;
  transform: translateX(-3px);
}

.header-actions {
  display: flex;
  gap: 1rem;
}

.action-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.25rem;
  border-radius: 12px;
  font-size: 0.9rem;
  font-weight: 500;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
}

.share-btn {
  background: rgba(30, 144, 255, 0.15);
  color: #1e90ff;
  border: 1px solid rgba(30, 144, 255, 0.3);
}

.share-btn:hover {
  background: rgba(30, 144, 255, 0.25);
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(30, 144, 255, 0.3);
}

.download-btn {
  background: rgba(0, 255, 127, 0.15);
  color: #00ff7f;
  border: 1px solid rgba(0, 255, 127, 0.3);
}

.download-btn:hover {
  background: rgba(0, 255, 127, 0.25);
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.3);
}

.save-btn {
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  color: white;
  border: none;
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.3);
}

.save-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 35px rgba(0, 255, 127, 0.4);
}

/* Tree Header Info */
.tree-header-info {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.tree-title-section h1.tree-title {
  font-size: 2.2rem;
  font-weight: 800;
  margin-bottom: 0.5rem;
  background: linear-gradient(135deg, #00ff7f, #40e0d0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.tree-title-section p.tree-description {
  font-size: 1rem;
  color: rgba(255, 255, 255, 0.7);
  margin: 0;
}

.tree-stats-header {
  display: flex;
  gap: 2rem;
}

.stat-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.95rem;
}

.stat-item svg {
  color: #40e0d0;
}

/* Tab Navigation */
.tab-navigation {
  display: flex;
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(15px);
  border-bottom: 1px solid rgba(64, 224, 208, 0.2);
  overflow-x: auto;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.tab-navigation::-webkit-scrollbar {
  display: none;
}

.tab-btn {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1.25rem 2rem;
  background: transparent;
  border: none;
  color: rgba(255, 255, 255, 0.7);
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  border-bottom: 3px solid transparent;
  white-space: nowrap;
  position: relative;
}

.tab-btn:hover {
  color: rgba(255, 255, 255, 0.9);
  background: rgba(64, 224, 208, 0.05);
}

.tab-btn.active {
  color: #40e0d0;
  border-bottom-color: #40e0d0;
  background: rgba(64, 224, 208, 0.1);
}

.tab-btn.active::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, #00ff7f, #40e0d0);
}

/* Disabled tab button styles */
.tab-btn.disabled {
  color: rgba(255, 255, 255, 0.3);
  cursor: not-allowed;
  opacity: 0.6;
  pointer-events: auto; /* Allow hover effects to show tooltip */
}

.tab-btn.disabled:hover {
  background: transparent;
  transform: none;
  cursor: not-allowed;
}

/* Tab Content */
.tab-content {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.tab-panel {
  flex: 1;
  padding: 2rem;
  min-height: calc(100vh - 200px);
  overflow: visible;
  position: relative;
  z-index: 10;
}

/* Edit Mode Header */
.edit-header {
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(15px);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 16px;
  padding: 1.5rem;
  margin-bottom: 2rem;
}

.edit-header h3 {
  font-size: 1.3rem;
  color: rgba(255, 255, 255, 0.95);
  margin-bottom: 0.5rem;
}

.edit-header p {
  color: rgba(255, 255, 255, 0.7);
  margin: 0;
  font-size: 0.95rem;
}

/* Tree Visualization Container */
.tree-visualization-container {
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(15px);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 20px;
  padding: 2rem;
  min-height: 600px;
  overflow: auto;
  position: relative;
  z-index: 1;
}

/* Member Management Container */
.member-management-container {
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(15px);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 20px;
  padding: 2rem;
  position: relative;
  z-index: 5;
  margin-top: 1rem;
  overflow: visible;
}

/* Settings Container */
.tree-settings-container {
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(15px);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 20px;
  padding: 2rem;
  position: relative;
  z-index: 5;
  margin-top: 1rem;
  overflow: visible;
}

/* Edit Tree Container */
.edit-tree-container {
  position: relative;
  z-index: 1;
  overflow: visible;
}

/* Ensure modals appear above everything */
.create-tree-overlay {
  position: fixed !important;
  top: 0 !important;
  left: 0 !important;
  right: 0 !important;
  bottom: 0 !important;
  z-index: 9999 !important;
  background: rgba(0, 0, 0, 0.8) !important;
  backdrop-filter: blur(10px) !important;
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  padding: 1rem !important;
}

.create-tree-modal {
  position: relative !important;
  z-index: 10000 !important;
  background: rgba(0, 0, 0, 0.9) !important;
  backdrop-filter: blur(20px) !important;
  border: 1px solid rgba(64, 224, 208, 0.3) !important;
  border-radius: 24px !important;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.7) !important;
  width: 100% !important;
  max-width: 600px !important;
  max-height: 90vh !important;
  overflow-y: auto !important;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .tree-viewer-header {
    padding: 1rem 1.5rem;
  }
  
  .tree-header-info {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
  }
  
  .tree-stats-header {
    gap: 1rem;
  }
  
  .tab-panel {
    padding: 1.5rem;
  }
}

@media (max-width: 768px) {
  .header-top {
    flex-direction: column;
    gap: 1rem;
    align-items: stretch;
  }
  
  .header-actions {
    justify-content: center;
    flex-wrap: wrap;
  }
  
  .tree-title-section h1.tree-title {
    font-size: 1.8rem;
  }
  
  .tab-btn {
    padding: 1rem 1.5rem;
    font-size: 0.9rem;
  }
  
  .tab-btn span {
    display: none;
  }
  
  .tab-panel {
    padding: 1rem;
  }
}

@media (max-width: 480px) {
  .tree-viewer-header {
    padding: 1rem;
  }
  
  .tree-title-section h1.tree-title {
    font-size: 1.5rem;
  }
  
  .tree-stats-header {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .action-btn span {
    display: none;
  }
  
  .action-btn {
    width: 45px;
    height: 45px;
    justify-content: center;
    padding: 0;
  }
  
  .tab-btn {
    min-width: 60px;
    justify-content: center;
    padding: 1rem 0.5rem;
  }
}

/* RTL Support */
[dir="rtl"] .back-btn:hover {
  transform: translateX(3px);
}

[dir="rtl"] .tree-header-info {
  text-align: right;
}

[dir="rtl"] .tab-btn.active::before {
  left: auto;
  right: 0;
}

/* ===== Join Request Functionality Styles ===== */

/* Requests Badge */
.requests-badge {
  position: absolute;
  top: -8px;
  right: -8px;
  background: linear-gradient(135deg, #ff6b6b, #ff8e53);
  color: white;
  font-size: 0.7rem;
  font-weight: 700;
  padding: 0.25rem 0.5rem;
  border-radius: 12px;
  border: 2px solid rgba(0, 0, 0, 0.8);
  min-width: 20px;
  text-align: center;
  animation: pulse-badge 2s infinite;
  z-index: 10;
}

@keyframes pulse-badge {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.1); }
}

/* Join Status Indicator */
.join-status {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  background: rgba(255, 193, 7, 0.1);
  color: #ffc107;
  border: 1px solid rgba(255, 193, 7, 0.3);
  border-radius: 8px;
  font-size: 0.9rem;
  font-weight: 500;
}

/* Join Button Styling */
.join-btn {
  background: linear-gradient(135deg, #00ff7f, #40e0d0) !important;
  color: white !important;
  border: none !important;
}

.join-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.4);
}

/* Join Requests Section */
.join-requests-section {
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(15px);
  border: 1px solid rgba(64, 224, 208, 0.2);
  border-radius: 20px;
  padding: 2rem;
  margin: 2rem 0;
}

.join-requests-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid rgba(64, 224, 208, 0.2);
}

.join-requests-header h3 {
  color: rgba(255, 255, 255, 0.95);
  font-size: 1.3rem;
  margin: 0;
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.requests-count {
  background: linear-gradient(135deg, #40e0d0, #00ff7f);
  color: rgba(0, 0, 0, 0.8);
  font-size: 0.8rem;
  font-weight: 700;
  padding: 0.25rem 0.75rem;
  border-radius: 12px;
  margin-left: 0.5rem;
}

/* Request Cards */
.requests-list {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.request-card {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(64, 224, 208, 0.15);
  border-radius: 16px;
  padding: 1.5rem;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.request-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, #00ff7f, #40e0d0);
}

.request-card:hover {
  background: rgba(255, 255, 255, 0.05);
  border-color: rgba(64, 224, 208, 0.3);
  transform: translateY(-2px);
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.3);
}

.request-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1rem;
}

.request-info h4 {
  color: rgba(255, 255, 255, 0.95);
  font-size: 1.1rem;
  font-weight: 600;
  margin: 0 0 0.25rem 0;
}

.request-info p {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.9rem;
  margin: 0.25rem 0;
}

.request-date {
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.8rem;
  background: rgba(64, 224, 208, 0.1);
  padding: 0.25rem 0.75rem;
  border-radius: 8px;
  border: 1px solid rgba(64, 224, 208, 0.2);
}

.request-details {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1rem;
  margin-bottom: 1rem;
  padding: 1rem;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 12px;
  border: 1px solid rgba(64, 224, 208, 0.1);
}

.detail-item {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.detail-label {
  color: #40e0d0;
  font-size: 0.8rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.detail-value {
  color: rgba(255, 255, 255, 0.9);
  font-size: 0.95rem;
}

.request-message {
  margin: 1rem 0;
  padding: 1rem;
  background: rgba(100, 149, 237, 0.05);
  border: 1px solid rgba(100, 149, 237, 0.2);
  border-radius: 12px;
  color: rgba(255, 255, 255, 0.8);
  font-style: italic;
  line-height: 1.5;
}

/* Request Actions */
.request-actions {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
  margin-top: 1.5rem;
  padding-top: 1rem;
  border-top: 1px solid rgba(64, 224, 208, 0.1);
}

.accept-btn,
.reject-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  border-radius: 10px;
  border: none;
  font-size: 0.9rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  min-width: 120px;
  justify-content: center;
}

.accept-btn {
  background: linear-gradient(135deg, #00ff7f, #32cd32);
  color: white;
}

.accept-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 255, 127, 0.4);
}

.reject-btn {
  background: rgba(255, 69, 58, 0.15);
  color: #ff453a;
  border: 1px solid rgba(255, 69, 58, 0.3);
}

.reject-btn:hover:not(:disabled) {
  background: rgba(255, 69, 58, 0.25);
  border-color: rgba(255, 69, 58, 0.5);
  transform: translateY(-2px);
}

.accept-btn:disabled,
.reject-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

/* Empty State */
.no-requests {
  text-align: center;
  padding: 3rem 2rem;
  color: rgba(255, 255, 255, 0.6);
}

.no-requests svg {
  color: rgba(64, 224, 208, 0.5);
  margin-bottom: 1rem;
}

.no-requests h4 {
  color: rgba(255, 255, 255, 0.8);
  font-size: 1.1rem;
  margin-bottom: 0.5rem;
}

.no-requests p {
  font-size: 0.95rem;
  margin: 0;
}

/* Responsive Design for Join Requests */
@media (max-width: 768px) {
  .join-requests-section {
    padding: 1.5rem;
    margin: 1rem 0;
  }
  
  .join-requests-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
  }
  
  .request-card {
    padding: 1rem;
  }
  
  .request-header {
    flex-direction: column;
    gap: 1rem;
  }
  
  .request-details {
    grid-template-columns: 1fr;
  }
  
  .request-actions {
    flex-direction: column;
  }
  
  .accept-btn,
  .reject-btn {
    width: 100%;
  }
}

@media (max-width: 480px) {
  .join-requests-section {
    padding: 1rem;
    border-radius: 16px;
  }
  
  .request-card {
    padding: 1rem;
  }
  
  .requests-badge {
    font-size: 0.65rem;
    padding: 0.2rem 0.4rem;
  }
}